<template>
  <div>
    <!--资金管理 专项资金 -->
    <div class="create_porject">
      <div  class="p_title"><i  class="iconfont icon-tianxie"></i><p >专项资金</p></div>
      <el-form class="p-l" :model="ruleForm" :rules="rules" ref="ruleForm"  label-position="left" label-width="100px" >
        <el-row>
          <el-col :span="10">
            <el-form-item label="预算号:" prop="budgetCode">
              <el-input v-model="ruleForm.budgetCode" class="p_text" ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="3"> &nbsp;</el-col>
          <el-col :span="11">
            <el-form-item label="资金来源:">
              {{capitalsource}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row v-show="isReadOnly">
          <el-col :span="10" >
            <el-form-item label="录入日期:">
              {{ruleForm.lrdate}}
            </el-form-item>
          </el-col>
          <el-col :span="3"> &nbsp;</el-col>
          <el-col :span="11">
            <el-form-item label="录入人:">
              {{ruleForm.lrren}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="资金额度:" prop="initAmount">
              <el-input v-model="ruleForm.initAmount" class="p_text" ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="3"> &nbsp;</el-col>
          <el-col :span="11">
            <el-form-item label="提交日期:">
              2017-11-15
            </el-form-item>
          </el-col>
        </el-row>
        <el-row v-show="isReadOnly">
          <el-col :span="10">
            <el-form-item label="资金余额:">
              ${{ruleForm.balanceAmount}}
            </el-form-item>
          </el-col>
          <el-col :span="3"> &nbsp;</el-col>
          <el-col :span="11">
            <el-form-item label="资金支付额:">
              ${{ruleForm.payAmount}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row v-show="isReadOnly">
          <el-col :span="10">
            <el-form-item label="资金追加额:">
              ${{ruleForm.zjzje}}
            </el-form-item>
          </el-col>
          <el-col :span="3"> &nbsp;</el-col>
          <el-col :span="11">
            <el-form-item label="提交日期:">
              {{ruleForm.tjdate}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="备注:">
          <el-input type="textarea"  :rows="3" v-model="ruleForm.extra" class="p_text"></el-input>
        </el-form-item>
        <el-form-item label="附件" prop="projectName">
          <el-row class="fileremark" v-if="!isReadOnly">
            必须上传的附件
            <span v-for='(item,index) in requiredProjectFile'>
                              【{{item.attachmentName}}】
                </span>
          </el-row>
          <template v-for="(item,index) in ruleForm.updateFileIds">
            <el-row>
              <el-col :span="8">
                <el-input class="p_text" disabled="" value='xxxx' ></el-input>
              </el-col>
              <el-col :span="4">
                <span class="iconfont icon-xiazai2"></span>
                <span class="iconfont icon-jianhao" v-if="!isReadOnly"></span>
              </el-col>
            </el-row>
          </template>
          <el-row v-if="!isReadOnly">
            <el-col :span="8">
              <el-upload class="p_upload"  ref="uploadxxx" :data="uploadData" :on-success="handleSuccess" :on-error="handleError"   :action="uploadPath" :limit="1" :show-file-list="true">
                <a class="p_return sm">上传附件</a>
              </el-upload>
            </el-col>
          </el-row>

        </el-form-item>
        <el-row>
          <el-col :span="24" style="text-align: center;margin-top: 20px;">
            <!--提交按钮-->
            <button type="submit" class="p_submit" style="margin-right: 20px;" @click="submit('ruleForm')"><i class="iconfont icon-submit"></i>提交</button>
            <!--保存按钮-->
            <button type="submit" class="p_save" @click="save('ruleForm')"><i class="iconfont icon-baocun"></i>保存</button>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="create_porjects" v-show="isReadOnly">
      <div  class="p_title"><i  class="iconfont icon-tianxie"></i><p >追加资金记录</p></div>
      <table class="logRecord-table">
        <tr>
          <th width="80">序号</th>
          <th>追加金额</th>
          <th>追加人</th>
          <th>追加理由</th>
          <th>追加时间</th>
          <th width="160">附件</th>
          <th width="120">操作</th>
        </tr>
        <tr v-for="(val, index) in dataList">
          <td width="80">{{index+1}}</td>
          <td>{{val.zjje}}</td>
          <td>{{val.zjr}}</td>
          <td>{{val.zjly}}</td>
          <td>{{val.zjtime}}</td>
          <td width="160">{{val.pathfile}}</td>
          <td width="120">
            <el-button type="primary"  v-show="val.states=='1'"  class="tableop"  icon="el-icon-check">已提交</el-button>
            <el-button type="primary"  v-show="val.states=='2'" size="mini" round>编辑</el-button>
          </td>
        </tr>

      </table>
      <div class="table-footer">
        <div style="  width: 880px; height: 20px; padding-top: 10px; padding-left: 36px; float: left;">
          合计： <span style="font-weight: 800;"> ${{hjsum}}</span>
        </div>
        <div style=" width: 100%;vertical-align: middle; height: 20px; text-align:center;padding-top: 10px;">
          <i class="el-icon-circle-plus-outline" @click="dialogTableVisible = true" style=" font-size: 20px; cursor: pointer;color: #007dfd"></i>
        </div>
      </div>


      <el-dialog title="追加资金" size="mini" :visible.sync="dialogTableVisible" left>
        <el-form  :model="addruleForm" ref="addruleForm" :rules="rulesadd"   label-position="right" label-width="100px" >
          <el-row>
            <el-col :span="11">
              <el-form-item label="追加金额:"  prop="fundAmount">
                <el-input v-model="addruleForm.fundAmount" class="p_text" ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">&nbsp</el-col>
            <el-col :span="11">
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="11">
              <el-form-item label="追加人:">
                张三
              </el-form-item>
            </el-col>
            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="11">
              <el-form-item label="追加日期:">
                2017-09-01
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="追加理由:" prop="appendReason">
            <el-input type="textarea" :rows="3" v-model="addruleForm.appendReason" class="p_text"></el-input>
          </el-form-item>
          <el-form-item label="附件" prop="projectName">
            <el-row class="fileremark" >
              必须上传的附件
              <span v-for='(item,index) in requiredProjectFile'>
                              【{{item.attachmentName}}】
                </span>
            </el-row>
            <template v-for="(item,index) in addruleForm.updateFileIds">
              <el-row>
                <el-col :span="8">
                  <el-input class="p_text" disabled="" value='xxxx' ></el-input>
                </el-col>
                <el-col :span="4">
                  <span class="iconfont icon-xiazai2"></span>
                  <span class="iconfont icon-jianhao"></span>
                </el-col>
              </el-row>
            </template>
            <el-row>
              <el-col :span="8">
                <el-upload class="p_upload"  ref="uploadxxx" :data="uploadData" :on-success="handleSuccess" :on-error="handleError"   :action="uploadPath" :limit="1" :show-file-list="true">
                  <a class="p_return sm">上传附件</a>
                </el-upload>
              </el-col>
            </el-row>

          </el-form-item>
          <el-row>
            <el-col :span="24" style="text-align: center;margin-top: 20px;">
              <!--提交按钮-->
              <button type="submit" size="mini" class="p_submit" style="margin-right: 20px;" @click="addsubmit('ruleForm')"><i class="iconfont icon-submit"></i>提交</button>
              <!--保存按钮-->
              <button type="submit" size="mini" class="p_save" @click="addsave('ruleForm')"><i class="iconfont icon-baocun"></i>保存</button>
            </el-col>
          </el-row>
        </el-form>

      </el-dialog>
    </div>
  </div>
</template>

<script type="text/javascript">
  import baseInfo from '../components/baseinfo'
  import projectStep from '../components/projectStep'
  import {StateCode} from '../../../assets/js/config.js'
  export default {
    name: 'project-zijin',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        hjsum:'4000',
        dialogTableVisible: false,
        ruleForm: {
          addAmount:'',//资金追加额
          balanceAmount:'',//资金余额
          budgetCode:'',//预算号
          extra:'',//备注 ,
          fundAmount:'',//资金额度
          fundSource:'',//资金来源
          initAmount:'',//资金初始额度
          payAmount:'',//资金支付额度
          projectId:'',//项目标识
          updateFileIds:[],
        },
        addruleForm:
        {
          appendReason:'',//追加理由
          appender:'',//追加人
          budgetCode:'',//预算号
          extra:'',//备注
          fundAmount:'',//资金额度
          fundId:'',//资金标识
          fundSource:'',//资源来源
          projectId:'',// 项目标识
          updateFileIds:'',//关联附件的ID集合
        },
        dataList: [
          {
            zjje:'123',
            zjr:'123',
            zjly:'123',
            zjtime:'123',
            pathfile:'123',
            states:'1'
          },
          {
            zjje:'123',
            zjr:'123',
            zjly:'123',
            zjtime:'123',
            pathfile:'123',
            states:'2'
          },
        ],
        rules: {
          initAmount: [
            { required: true, message: '请输入资金额度', trigger: 'blur' },
          ],
          budgetCode:[
            { required: true, message: '请输入预算号', trigger: 'blur' },
          ]
        },
        rulesadd: {
          fundAmount: [
          { required: true, message: '请输入追加金额', trigger: 'blur' },
          ],
          appendReason: [
            { required: true, message: '请输入追加理由', trigger: 'blur' },
          ],
        },
        capitalsource:'专项',//资金来源,
        // 是否只读模式
        isReadOnly:false,
        // 项目必传的文档名称
        requiredProjectFile:[],
        // 上传文件集合
        updateFileList:[],
        uploadPath:this.$xhr.defaults.baseURL+'/CommonFileUploadController/upload',
        uploadData:{attachmentCate:1,stageNo:StateCode.CREATE,stageId:StateCode.CREATE},
      }
    },
    mounted() {
      //console.log(this.$route.params)

    },
    methods: {
      addsubmit(formName)
      {},
      addsave(formName)
      {},
      submit(formName)
      {
        //提交
        //this.isReadOnly=true;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$xhr.post("/LzPmFundController/add", {
              budgetCode: this.ruleForm.budgetCode,//预算号
              fundSource: '1',//自己来源
              initAmount: this.ruleForm.initAmount,//资金额度
              extra:this.ruleForm.extra,
              projectId:'3f2d48ca3e3e408bb13e970409ddc374',
              updateFileIds:this.ruleForm.updateFileIds,
            }).then((res) => {
              console.log(res);
            })
          }
        })
      },
      save(formName)
      {
        //保存
        //this.isReadOnly=true;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$xhr.post("/LzPmFundController/add", {
              budgetCode: this.ruleForm.budgetCode,//预算号
              fundSource: '1',//自己来源
              initAmount: this.ruleForm.initAmount,//资金额度
              extra:this.ruleForm.extra,
              projectId:'3f2d48ca3e3e408bb13e970409ddc374',
              updateFileIds:this.ruleForm.updateFileIds,
            }).then((res) => {
              console.log(res);
            })
          }
        })
      },
      addsubment()
      {
        this.dataList.push({zjje: this.adddatatable.zjje,zjr:"李四",zjtime:'2017-01-10',bz:this.adddatatable.zjly,pathfile:'1.png'});
        this.dialogTableVisible = false;
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      // 文档上传
      handleSuccess(res, file, list) {
        if(res.code == "1"){
          this.updateFileList.push(res.lzPmAttachmentPojo);
          this.ruleForm.updateFileIds=[];
          this.addruleForm.updateFileIds=[];
          for(var item of this.updateFileList) {
            if (dialogTableVisible) {
              this.addruleForm.updateFileIds.push(item.id);
            }
            else {
              this.ruleForm.updateFileIds.push(item.id);
            }
          }
          this.$message({type: 'success',message: '上传成功!'});
        }
        console.log(this.ruleForm.updateFileIds);
        this.$refs.uploadxxx.clearFiles();
      },
      handleError() {
        console.log("上传文件失败");
      },
    },
    components: {
      baseInfo,
      projectStep
    }
  }
</script>

<style lang="scss">
  .create_porject{
    margin-top:10px;
    padding: 20px;
    background: #fff;
    font-size: 14px;
  }
  .create_porjects{
    padding: 20px;
    background: #fff;
    font-size: 14px;
  }
  .p_title{
    margin-top: 20px;;
    margin-bottom: 20px;
  }
  .el-select {
    width: 100%;
  }
  .el-date-editor.el-input, .el-date-editor.el-input__inner {
    width: 100%;
  }
  .logRecord-table {
    width: 100%;
    th {
      height: 42px;
      background-color: #F3F6FE;
      border: 1px solid #E7ECFF;
      text-align: center;
      font-weight: bold;
      .p_select {
        width: 150px;
      }
    }
    td {
      height: 42px;
      text-align: center;
      border: 1px solid #E7ECFF;
      &.pactName {
        color: #345de7;
      }
    }
  }
  .tableop{
    font-size: 10px;
    background-color: #ffffff;
    color: #82DAB1;
    border: 0px;
  }
  .table-footer
  {
    height: 42px;
    width: 100%;
    border: 1px solid #E7ECFF;
  }
  .addmodeldiv{
    background-color:#ffffff;
    width:800px;
    height:300px;
    position: fixed;
    left:300px;
    top:320px;
    font-weight: 800;
    border: 1px solid #5b81ff;
    transition: bottom 1s;

  }
  .right-container-button
  {
    margin-left: 600px;
    width: 200px;
    height: 100%;
    margin-top: 0px;
  }
  .el-dialog__body
  {
    padding-top: 10px;
  }
  .create_porject{
    .icon-xiazai2, .icon-jianhao {
      margin-left: 10px;
      display: inline-block;
      vertical-align: middle;
      cursor: pointer;
      color: #345DE7;
      font-size: 24px;
      &:hover {
        color: #409EFF;
      }
    }
  }

</style>



