<template>
  <div>
    <baseInfo :projectId='1'></baseInfo>
    
    <div class="warpper">
    	<Div class="tab">
    		<button class="active iconfont icon-yingjian">数据铁笼软件采购</button>
    		<button class="iconfont icon-screen">数据铁笼硬件采购</button>    		
    	</Div>
    	<realStep :prams='prams'></realStep>
    </div>
  </div>
</template>

<script type="text/javascript">
import baseInfo from '../components/baseinfo'
import realStep from '../components/realStep'

  export default {
    name: 'information',
    data() {
      return {
        active:5,
       	prams:{
       		tabId:'0',//选签ID
       		stepId:'8',//步骤ID       		
       	}
      }
    },
    mounted() {
       
    },
    methods: {
 
    },
    components: {
      baseInfo,
      realStep
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
  .my-tab-wrap{
  	margin:30px;
  	li{
  		width:88px;
  		height: 30px;
  		border: 1px solid #9899ad;
  		border-radius: 30px;
  		line-height: 30px;
  		text-align: center;
  		display: inline-block;
  		margin-right: 10px;
  		cursor:pointer;
	  	&.active{
	  		border-color: #635fc0;
	  		color: #635FC0;
	  	}
  	}
  }
  
  .warpper{
  
 		
  	.tab{
  		margin-top:20px;
  		button{
  			width: 176px;
			height: 46px;
			background:#eaeefd;	 
			font-size: 16px;
			font-weight: normal;
			font-stretch: normal;
			line-height: 45px;
			letter-spacing: 0px;
			color: #456cf0;
			border: none;
			&:before{
					margin-right: 5px;
			}
		    display: inline-block;
		    margin-right: -4px;
		    border-bottom: 4px solid #eaeefd;
			&.active{
				background: #fff;
				border-bottom: 4px solid #345de7;
			}
  		}
  	}
  }
  
  
</style>



