<template>
  <div>
    <div class="create_porject">
      <div  class="p_title"><i  class="iconfont icon-tianxie"></i><p >部门资金</p></div>
      <el-form class="p-l" :model="ruleForm" ref="ruleForm"  label-position="left" label-width="120px" >
        <el-row>
          <el-col :span="11">
            <el-form-item label="资金额度:">
              <el-input v-model="ruleForm.zjed" disabled="true" class="p_text" ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2"> &nbsp;</el-col>
          <el-col :span="11">
            <el-form-item label="预算号:">
              <el-input v-model="ruleForm.ysh"  disabled="true" class="p_text" ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="资金支付额:">
              <el-input v-model="ruleForm.zjzfe" disabled="true" class="p_text" ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2"> &nbsp;</el-col>
          <el-col :span="11">
            <el-form-item label="资金余额:">
              <el-input v-model="ruleForm.zjye" disabled="true" class="p_text" ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="create_porject">
      <div  class="p_title"><i  class="iconfont icon-tianxie"></i><p >添加资金</p></div>
      <table class="logRecord-table">
        <tr>
          <th width="80">序号</th>
          <th>添加资金</th>
          <th>预算号</th>
          <th>添加人</th>
          <th>备注</th>
          <th>确定时间</th>
          <th width="160">附件</th>
          <th width="120">操作</th>
        </tr>
        <tr v-for="(val, index) in dataList">
          <td width="80">{{index+1}}</td>
          <td>{{val.zjed}}</td>
          <td>{{val.ysh}}</td>
          <td>{{val.addren}}</td>
          <td>{{val.bz}}</td>
          <td>{{val.okdate}}</td>
          <td width="160">{{val.pathfile}}</td>
          <td width="120">
            <el-button type="primary"  class="tableop" icon="el-icon-check">已确认</el-button>
          </td>
        </tr>

      </table>
      <div class="table-footer">
        <div style="  width: 860px; height: 20px; padding-top: 10px; padding-left: 36px; float: left;">
          合计： <span style="font-weight: 800;"> ${{hjsum}}</span>
        </div>
        <div style=" width: 100%;vertical-align: middle; height: 20px; text-align:center;padding-top: 10px;">
          <i class="el-icon-circle-plus-outline" @click="dialogTableVisible = true" style=" font-size: 20px; cursor: pointer;color: #007dfd"></i>
        </div>
      </div>

      <el-dialog size="mini"  :visible.sync="dialogTableVisible"  top="40vh">
        <el-form  :model="adddatatable" ref="adddatatable"  label-position="right" label-width="90px" >
          <el-row>
            <el-col :span="11">
              <el-form-item label="添加金额:" prop="zjed">
                <el-input v-model="adddatatable.zjed" class="p_text" ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="11">
              <el-form-item label="预算号:" prop="ysh">
                <el-input v-model="adddatatable.ysh" class="p_text" ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="11">
              <el-form-item label="添加人:">
                张三
              </el-form-item>
            </el-col>
            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="11">
              <el-form-item label="添加日期:">
                2017-09-01
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="备注: " prop="bz">
            <el-input type="textarea" :rows="3" v-model="adddatatable.bz" class="p_text"></el-input>
          </el-form-item>

          <el-form-item label="附件:">
            <el-row>
              <el-col :span="12">
                <div>
                  <div class="file-group">
                    <el-input class="p_text" disabled="" value='合同文件.docx' placeholder="合同文件"></el-input>
                    <el-upload class="p_upload"  ref="uploadxx" :headers="myheaders" action="http://192.168.20.145:8080/aobo/file/upload"  :limit="1"  :show-file-list="true">
                      <el-button size="small" >点击上传</el-button>
                    </el-upload>
                  </div>
                </div>
              </el-col>
              <el-col :span="12">
                <span class="iconfont icon-xiazai2"></span>
                <span class="iconfont icon-jianhao"></span>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24" :offset="6">
                <el-form-item >
                  <el-button type="primary" @click="addsubment" size="small">提交</el-button>
                  <el-button @click="resetForm('adddatatable')" size="small">重置</el-button>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form-item>
        </el-form>
      </el-dialog>

    </div>
  </div>
</template>

<script type="text/javascript">
  import baseInfo from '../components/baseinfo'
  import projectStep from '../components/projectStep'
  export default {
    name: 'project-zijin',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        hjsum: '0',
        dialogTableVisible: false,
        ruleForm: {
          zjed: '1000',
          ysh: '1000',
          zjzfe:'1000',
          zjye:'1000'

        },
        dataList: [
          {
            zjed: "100000",
            ysh: "200000",
            addren: "张欣",
            okdate: "2017-09-10",
            bz: "提交",
            pathfile: "无"
          },
        ],
        adddatatable: {
          zjed: '',
          ysh: '',
          addren:'',
          okdate: "",
          bz:'',
          pathfile:''
        },
        rules: {
          zjed: [
            { required: true, message: '请输入添加金额', trigger: 'blur' },
            { type: 'number', message: '金额必须为数字值'}
          ],
          ysh: [
            { required: true, message: '请输入预算号', trigger: 'blur' },
            { type: 'number', message: '预算号必须为数字值'}
          ],
          bz: [
            { required: true, message: '请输入备注', trigger: 'blur' }
          ]
        },
      }
    },
    mounted() {
    },
    methods: {
      //自己列表添加按钮事件
      adddata(){
        this.dialogTableVisible = true
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      //添加资金页面，保存方法
      addsubment()
      {
        this.dataList.push({zjed: this.adddatatable.zjed,ysh:this.adddatatable.ysh,addren:"张三",okdate:'2017-01-10',bz:this.adddatatable.bz,pathfile:'1.png'});
        this.dialogTableVisible = false;
      }
    },
    components: {
      baseInfo,
      projectStep
    }
  }
</script>
<style lang="scss" scoped>
  .create_porject{
    margin-top:10px;
    padding: 20px;
    background: #fff;
    font-size: 14px;
  }
  .el-select {
    width: 100%;
  }
  .el-date-editor.el-input, .el-date-editor.el-input__inner {
    width: 100%;
  }
  .logRecord-table {
    margin-top: 20px;
    width: 100%;
    th {
      height: 42px;
      background-color: #F3F6FE;
      border: 1px solid #E7ECFF;
      text-align: center;
      font-weight: bold;
      .p_select {
        width: 150px;
      }
    }
    td {
      height: 42px;
      text-align: center;
      border: 1px solid #E7ECFF;
      &.pactName {
        color: #345de7;
      }
    }
  }
  .tableop{
    font-size: 10px;
    background-color: #ffffff;
    color: #82DAB1;
    border: 0px;
  }
  .table-footer
  {
    height: 42px;
    width: 100%;
    border: 1px solid #E7ECFF;
  }
  .addmodeldiv{
    background-color:#ffffff;
    width:800px;
    height:300px;
    position: fixed;
    left:300px;
    top:320px;
    font-weight: 800;
    border: 1px solid #5b81ff;
    transition: bottom 1s;

  }
  .right-container-button
  {
    margin-left: 600px;
    width: 200px;
    height: 100%;
    margin-top: 0px;
  }
  .create_porject {
    .icon-xiazai2, .icon-jianhao {
      margin-left: 10px;
      display: inline-block;
      vertical-align: middle;
      cursor: pointer;
      color: #345DE7;
      font-size: 24px;
      &:hover {
        color: #409EFF;
      }
    }
  }
</style>



