<template>
  <div class="document-wrap">
    <div class="basetitle">
      <span class="base-title-icon"></span>
      <span class="base-title">文档目录</span>
    </div>
    <div class="information-wrap">
      <div class="p_title">
        <i class="iconfont icon-wendang1"></i>
        <p>基本信息</p>
      </div>
      <el-form class="p-l" label-position="left" label-width="110px">
        <el-row>
          <el-col :span="10">
            <el-form-item label="项目名称：">
              <span>柳州铁笼</span>
            </el-form-item>
          </el-col>
          <el-col :span="4"> &nbsp;</el-col>
          <el-col :span="10">
            <el-form-item label="立项负责人：">
              <span>闫磊</span>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="立项日期：">
              <span>2017-10-23</span>
            </el-form-item>
          </el-col>
          <el-col :span="4"> &nbsp;</el-col>
          <el-col :span="10">
            <el-form-item label="移交日期：">
              <span>2017-10-23</span>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="document-table">
      <button class="p_return sm p_add" type="button" @click="dialogVisible = true"><i
        class="icon iconfont icon-jiahao"></i>添加附件
      </button>
      <el-dialog
        :visible.sync="dialogVisible"
        width="800px"
        :before-close="handleClose">
        <el-form class="p-l" :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="90px">
          <el-row>
            <el-col :span="10">
              <el-form-item label="项目名称：">
                <el-select v-model="ruleForm.region1" placeholder="数据铁笼" class="p_select">
                  <el-option label="数据铁笼1" value="1"></el-option>
                  <el-option label="数据铁笼2" value="2"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="4"> &nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label="录入人：">
                <el-input v-model="ruleForm.pactName" class="p_text"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col class="pactClassItem" :span="10">
              <el-form-item label="阶段名称：">
                <el-select v-model="ruleForm.region2" placeholder="设计阶段" class="p_select">
                  <el-option label="准备阶段" value="1"></el-option>
                  <el-option label="开发阶段" value="2"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="4"> &nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label="录入时间：">
                <el-input v-model="ruleForm.pactName" class="p_text"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="10">
              <el-form-item label="文件类型：">
                <el-select v-model="ruleForm.region1" placeholder="附件" class="p_select">
                  <el-option label="自用" value="1"></el-option>
                  <el-option label="未用" value="2"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="4"> &nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label="上传附件：" style="text-align: left">
                <el-upload class="upload-demo small" ref="uploadxxx" :headers="myheaders"
                           action="http://192.168.20.145:8080/aobo/file/upload" :on-success="handleSuccess"
                           :on-error="handleError" :limit="1" :disabled="fileList.length>=3" :show-file-list="true">
                  <el-button class="batch-import" size="small" type="primary"><i class="iconfont icon-jiahao"></i>上传附件
                  </el-button>
                </el-upload>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24" style="text-align: center">
              <button type="submit" class="p_submit"><i class="iconfont icon-submit"></i>提交</button>
            </el-col>
          </el-row>
        </el-form>
      </el-dialog>
      <table>
        <thead>
        <tr>
          <th>序号</th>
          <th>项目名称</th>
          <th>项目阶段</th>
          <th>文件类型</th>
          <th>文档名称</th>
          <th>录入人</th>
          <th>录入日期</th>
        </tr>
        </thead>
        <tr v-for="(item, index) in tableData">
          <td :rowspan="item.orderspan" :class="{hidden: item.orderdis}">{{item.order}}</td>
          <td :rowspan="item.projectNamespan" :class="{hidden: item.projectNamedis}">{{item.projectName}}</td>
          <td :rowspan="item.projectPhasespan" :class="{hidden: item.projectPhasedis}">{{item.projectPhase}}</td>
          <td>{{item.documentType}}</td>
          <td class="documentName">{{item.documentName}}</td>
          <td>{{item.enteringPerson}}</td>
          <td>{{item.enteringDate}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: '',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        fileList: ['111.txt'],
        dialogVisible: false,
        dialogVisibleb: false,
        myheaders: {
          'Authorization': 'bearer ce6e1d40-495d-4c7d-a870-dd9d384b5058',
        },
        ruleForm: {},
        rules: {},
        tableData: []
      }
    },
    mounted() {
      let data = [
        {
          order: '1',
          projectName: '柳州铁笼',
          projectPhase: '立项阶段',
          documentType: '附件',
          documentName: '立项申请书',
          enteringPerson: '闫磊',
          enteringDate: '2017-10-23'
        },
        {
          order: '2',
          projectName: '柳州铁笼',
          projectPhase: '资金管理',
          documentType: '附件',
          documentName: '立项申请书',
          enteringPerson: '闫磊',
          enteringDate: '2017-10-23'
        },
        {
          order: '3',
          projectName: '铁笼项目-子项目',
          projectPhase: '资金管理',
          documentType: '附件',
          documentName: '立项申请书',
          enteringPerson: '闫磊',
          enteringDate: '2017-10-23'
        },
        {
          order: '4',
          projectName: '铁笼项目-子项目',
          projectPhase: '资金管理',
          documentType: '附件',
          documentName: '立项申请书',
          enteringPerson: '闫磊',
          enteringDate: '2017-10-23'
        },
        {
          order: '5',
          projectName: '铁笼项目-子项目',
          projectPhase: '招标阶段',
          documentType: '附件',
          documentName: '立项申请书',
          enteringPerson: '闫磊',
          enteringDate: '2017-10-23'
        },
        {
          order: '6',
          projectName: '铁笼项目硬件监理项目（子项目）',
          projectPhase: '招标阶段',
          documentType: '附件',
          documentName: '立项申请书',
          enteringPerson: '闫磊',
          enteringDate: '2017-10-23'
        }
        ];
        this.tableData = this.combineCell(data)
    },
    methods: {
      //表格自动合并
      combineCell(tableData) {
        for (var field in tableData[0]) {
          var k = 0;
          while (k < tableData.length) {
            tableData[k][field + 'span'] = 1;
            tableData[k][field + 'dis'] = false;
            for (var i = k + 1; i <= tableData.length - 1; i++) {
              if (tableData[k][field] == tableData[i][field] && tableData[k][field] != '') {
                tableData[k][field + 'span']++;
                tableData[k][field + 'dis'] = false;
                tableData[i][field + 'span'] = 1;
                tableData[i][field + 'dis'] = true;
              } else {
                break;
              }
            }
            k = i;
          }
        }
        console.log(tableData)
        return tableData;
      },
      //关闭添加
      handleClose(done) {
        done()
      },
      handleSuccess(res, file, list) {
        this.$refs.uploadxxx.clearFiles();
      },
      handleError() {
        console.log(444);
      }
    },
    components: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";

  .document-wrap {
    width: 100%;
    .information-wrap {
      padding: 20px 20px 0 20px;
      box-sizing: border-box;
      background: #fff;
      margin-bottom: 20px;
    }
    .document-table {
      background: #fff;
      padding: 20px;
      text-align: right;
      .el-button--mini, .el-button--small {
        font-size: 14px !important;
      }
      table {
        width: 100%;
        margin-top: 20px;
        th {
          height: 42px;
          text-align: center;
          background: #F3F6FE;
          border: 1px solid #E7ECFF;
        }
        tr {
          height: 42px;
        }
        td {
          height: 42px;
          text-align: center;
          border: 1px solid #E7ECFF;
          &.hidden {
            display: none;
          }
          &.documentName {
            color: #0033FF;
          }
        }
      }
    }
  }
</style>



