<template>
  <div class="finished-wrap">
    <div class="p_title">
      <i class="iconfont icon-finish"></i><p>年度已完成项目数量统计</p>
    </div>
    <div class="bottomLeft">
      <bottomLeft :data="bottomleftData"></bottomLeft>
    </div>
    <div class="bottomRight">
      <table>
        <thead>
        <tr>
          <th>年份</th>
          <th>已完成项目</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item, index) in bottomRightData">
          <td>{{item.year}}</td>
          <td>{{item.finished}}</td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script type="text/javascript">
  import bottomLeft from '../echarts/bottomLeft.vue'

  export default {
    name: '',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        bottomleftData: {
          data1: [],
          data2: [],
        },
        bottomRightData: [
          {year: '2017年', finished: '12'},
          {year: '2017年', finished: '12'},
          {year: '2017年', finished: '12'},
          {year: '2017年', finished: '12'},
          {year: '2017年', finished: '12'},
          {year: '2017年', finished: '12'},
          {year: '2017年', finished: '12'},
          {year: '2017年', finished: '12'},
        ]
      }
    },
    mounted() {
      this.getBottomLeftData();
    },
    methods: {
      getBottomLeftData() {
        this.bottomleftData.data1 = ['2014年', "2015年", "2016年", "2017年"];
        this.bottomleftData.data2 = [90, 80, 70, 60];
      },
    },
    components: {
      bottomLeft
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";
  .finished-wrap {
    padding: 20px;
    font-size: 0;
    .p_title {
      font-size: 14px;
      margin-bottom: 20px;
    }
    .bottomLeft {
      display: inline-block;
      width: 65%;
      height: 400px;
      vertical-align: top;
    }
    .bottomRight {
      display: inline-block;
      width: 35%;
      padding: 0 20px;
      font-size: 14px;
      box-sizing: border-box;
      table {
        width: 100%;
        thead {
          border: 1px solid #e7ecff;
        }
        th {
          height: 42px;
          background-color: #eaeefd;
          text-align: center;
          font-weight: bold;
        }
        td {
          height: 42px;
          text-align: center;
          border: 1px solid #e7ecff;
        }
      }
    }
  }
</style>



