              line-height: 10px;
              background: #FDFDFD;
              border: 1px solid #E2E2E2 !important;
              border-radius: 0 25px 25px 0 !important;
            }
            /*弹出树图*/
            .el-dialog__wrapper {
              & > .el-dialog {
                height: 400px;
                .el-dialog__body {
                  height: 235px;
                  .p_title {
                    width: 100%;
                    margin-top: -24px;
                    text-indent: 4px;
                  }
                  .p_top {
                    font-weight: bold;
                    line-height: 47px;
                    height: 47px;
                    color: #666;
                  }
                  .tree-box {
                    overflow: auto;
                    height: 209px;
                  }
                }
                .el-dialog__footer {
                  text-align: center !important;
                  .dialog-footer {
                    width: 100%;
                    text-align: center;
                  }
                }
              }
            }
          }
          .icon-jiahao1{
            margin-left: 10px;
            display:inline-block;
            vertical-align: middle;
            cursor: pointer;
            color:#345DE7;
            font-size: 24px;
            &:hover{
              color: #409EFF;
            }
          }
          .el-select {
            width: 100%;
          }
          .el-date-editor.el-input, .el-date-editor.el-input__inner {
            width: 100%;
          }
        }
        .btn-box {
          margin-top: 30px;
          text-align: center;
          button {
            margin: 0 15px;
          }
        }
      }
    }
  }

</style>



