<template>
  <div ref="centerLeft" class="bar"></div>
</template>

<script type="text/javascript">
  import { echartsMixin } from 'assets/js/mixin'
  export default {
    name: '',
    mixin: [echartsMixin],
    props: {
      title: {
        type: String,
        default: ''
      },
      data: {
        type: Object,
        default: () => {}
      },
    },
    data() {
      return {

      }
    },
    mounted() {
      this.initOption();
      this.initChart();
    },
    methods: {
      initOption() {
        // initOption 作用： 一次性捕获 echats 图表的挂载点，声明好 echats 图表的 基本配置
        this.chart = this.$echarts.init(this.$refs.centerLeft);
        this.option = {
          tooltip : {
            trigger: 'axis',
            axisPointer : {            // 坐标轴指示器，坐标轴触发有效
              type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            },
            padding:10
          },
          legend:{
            right: '40',
            top: '5',
            textStyle: {
              fontSize: '14',
              color: '#333'
            },
            itemGap: 20,
            itemHeight: 8,
            itemWidth: 8,
            data:['正常执行的项目','逾期项目']
          },
          grid: {
            right: '5%',
            left: '3%',
            bottom: '10%',
            top: '15%',
            containLabel: true
          },
          xAxis : [
            {
              type : 'category',
              data: [],
              nameTextStyle:{
                color:'#333',
                fontSize:14
              },
              nameGap:30,
              axisTick: {
                alignWithLabel: true,
                show:false
              },
              axisLabel:{
                textStyle:{
                  color:'#333',
                  fontSize:14
                }
              },
              axisLine: {
                lineStyle: {
                  color: '#dae1ef',
                  width:2
                }
              },
              splitLine:{
                lineStyle:{
                  color:'#ededed'
                }
              }
            }
          ],
          yAxis : [
            {
              type : 'value',
              name: '项目数量（件）',
              nameTextStyle:{
                color:'#333',
                fontSize:14
              },
              axisTick: {
                alignWithLabel: true,
                show:false
              },
              axisLabel:{
                textStyle:{
                  color:'#333',
                  fontSize:14
                }
              },
              axisLine: {
                lineStyle: {
                  color: '#dae1ef',
                  width:2
                }
              },
              splitLine:{
                show:false,
              },
              splitArea: {
                show: true,
                areaStyle: {
                  color: ['#fff', '#F7F7F7']
                }
              }
            }
          ],
          color:['#557BF7','#FFAF2A'],
          series : [
            {
              name:'正常执行的项目',
              type:'bar',
              stack:'总量',
              barWidth: '17',
              itemStyle: {
                normal: {
                  borderColor: '#4C91F6',
                  borderWidth: 1,
                }
              },
              data: [],
            },
            {
              name:'逾期项目',
              type:'bar',
              stack:'总量',
              barWidth: '17',
              itemStyle: {
                normal: {
                  borderColor: '#4C91F6',
                  borderWidth: 1,
                }
              },
              data:[]
            }
          ]
        };
      },
      initChart() {
        this.option.xAxis[0].data = this.data.data1;
        this.option.series[0].data = this.data.data2;
        this.option.series[1].data = this.data.data3;
        this.chart.setOption(this.option, true);
      },

      // echats 图表自适应
      _resizeHanlder() {
        this.chart.resize()
      },
      // 销毁echats图表方法
      _destroyHandler() {
        this.chart.dispose()
      }
    },
    components: {},
    watch: {
      data: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";
  .bar {
    width: 100%;
    height: 400px;
  }
</style>



