<template>
  <div class="project">
    <table>
      <thead>
      <tr>
        <th>序号</th>
        <th>项目名称</th>
        <th>立项时间</th>
        <th>
          <el-select class="p_select capitalType" v-model="tableData.fundSource" @change="placesearch" placeholder="全部资金类别">
            <el-option label="全部资金类别" value="0"></el-option>
            <el-option label="专项资金" value="1"></el-option>
            <el-option label="部门资金" value="2"></el-option>
          </el-select>
        </th>
        <th>资金额度</th>
        <th>子项个数</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item, index) in tableData">
        <td>{{index+1}}</td>
        <td class="pactName">{{item.projectName}}</td>
        <td>{{item.setupDate.substr(0,10)}}</td>
        <td>{{item.fundSource}}</td>
        <td>{{item.fundAmount}}</td>
        <td>{{item.subProjectCount}}</td>
      </tr>
      </tbody>
    </table>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="curPageIndex"
      layout="total, prev, pager, next, jumper"
      :total="totalCount" :page-size="pageSize">
    </el-pagination>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: '',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        ruleForm: {
          region: ''
        },
        curPageIndex:1,
        pageSize:5,
        totalCount:1,
        tableData: [],
        year:'',
        month:'',
      }
    },
    watch:{
      $route(){
        this.pageload();
      },
      curPageIndex:{
        handler:function(val, oldVal){
          this.tableData.fundSource='0';
          this.pageload()
        },
        deep:true
      },
    },
    mounted() {
      this.pageload();
    },
    methods: {
      pageload()
      {
        this.year=this.$route.query.year;
        this.month=this.$route.query.month;
        this.$xhr.get(`/api/lzPmStatis/getProjectList`,{
          params:{
            year:this.year,
            month:this.month,
            page:this.curPageIndex,
            limit:10,
            fundSource:'',
          }
        })
          .then((res) => {
          console.log(res);
            this.totalCount = res.data.totalCount
            this.pageSize = res.data.pageSize
            this.tableData=res.data.pageList;
          })
          .catch((req)=>{
            console.log(req)
          })
      },
      placesearch()
      {
        if(this.tableData.fundSource=="0")
        {
          this.tableData.fundSource='';
        }
        this.year=this.$route.query.year;
        this.month=this.$route.query.month;
        this.$xhr.get(`/api/lzPmStatis/getProjectList`,{
          params:{
            year:this.year,
            month:this.month,
            page:this.curPageIndex,
            limit:10,
            fundSource:this.tableData.fundSource,
          }
        })
          .then((res) => {
            this.totalCount = res.data.totalCount
            this.pageSize = res.data.pageSize
            this.tableData=res.data.pageList;
          })
          .catch((req)=>{
            console.log(req)
          })
      },
      //分页
      handleCurrentChange(val) {
        this.curPageIndex = val
      },
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";
  @import "../../../assets/css/diy-elementui";
  .project {
    background: #fff;
    padding: 20px;
    margin-top: 12px;
    table {
      width: 100%;
      thead {
        border: 1px solid #E7ECFF;
      }
      th {
        height: 42px;
        background-color: #F3F6FE;
        text-align: center;
        font-weight: bold;
      }
      td {
        height: 42px;
        text-align: center;
        border: 1px solid #E7ECFF;
        &.pactName {
          color: #345de7;
        }
      }
    }
  }
</style>



