<template>
  <div>
  	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" action="" method="post">
  		<div  class="newRole">
    		<div class="p_title">
      	 	<i class="iconfont icon-add"></i><p>基本信息</p>
      	</div>
      	<div class="form-add">
      		<el-col :span="10">
      			<el-form-item label="用户编号：" class="indent">
      				<span>{{ruleForm.userId}}</span>
						</el-form-item>
					  <el-form-item label="部门:" prop="govId">
	  				<el-select  v-model="ruleForm.govId"   class="p_select big">
			          <el-option v-for="(val,index) in sectionData"  :key="val.id" :label="val.name" :value="val.id"></el-option>
			       </el-select>
					  </el-form-item>
			      <el-form-item label="职务:" prop="job">
						    <el-input class="p_text" placeholder="请输入职务"  v-model="ruleForm.job"></el-input>
					  </el-form-item>
					  <el-form-item label="创建人："  class="indent">
      				<span>{{name}}</span>
						</el-form-item>
						<el-form-item label="密码:" prop="password">
						    <el-input type="password" class="p_text" placeholder="请输入密码"  v-model="ruleForm.password"></el-input>
						</el-form-item>
      		</el-col>
      		<el-col :span="4">
      			
      		</el-col>
      		<el-col :span="10" class="">
      			<el-form-item label="姓名:" prop="realName">
						    <el-input class="p_text" placeholder="请输入姓名"  v-model="ruleForm.realName"></el-input>
						</el-form-item>
      			<el-form-item label="账号:" prop="name">
						    <el-input class="p_text" placeholder="请输入账号"  v-model="ruleForm.name"></el-input>
						</el-form-item>
						<el-form-item label="角色:" class="role" prop="role">
						  	<el-select  v-model="ruleForm.role" placeholder="请选择角色" multiple @change="change" class="p_select big">
				           <el-option v-for="(val,index) in roleData" :key="val.id" :label="val.name" :value="val.id"></el-option>
				        </el-select>
						</el-form-item>
						<el-form-item label="创建日期："  class="indent">
      				<span>{{time}}</span>
						</el-form-item>
						<el-form-item label="ukey:" prop="ukey">
						    <el-input class="p_text" placeholder="请输入ukey"  v-model="ruleForm.ukey"></el-input>
						</el-form-item>
      		</el-col>
	      	<div class="remark-box">
	      		 <label for="remark">备注：</label>
	      		 <textarea placeholder="请输入备注信息" id="remark"  v-model="remark"></textarea>
	      	</div>
      	</div>
      	<div class="form-submit">
      		<el-form-item>
				   <button type="button" @click="submitForm('ruleForm')"  class="p_submit" ><i class="iconfont icon-baocun"></i>提交</button>
				    <router-link tag="button" :to="{path:'/system/userManage'}" class="p_save qx" ><i class="iconfont icon-quxiao"></i>取消</router-link>
				  </el-form-item>

      	</div>

    	
     </div>

  	</el-form>
  </div>
</template>

<script type="text/javascript">
export default {
  name: 'component_name',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
    	time:null,
    	num:null,
    	name:null,
    	remark:'',
       sectionData:null,
       roleData:null,
        arr:null,
       ruleForm:{
       	userId:'',
       	govId:'',
       	role:"",
       	realName:"",
    		name:"",
    		password:"",
    		job:"",
    		ukey:"",
    	},
    	rules:{
      	realName: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
        
        ],
      	name: [
          { required: true, message: "请输入账号", trigger: 'blur' },
  
        ],
      	password: [
          { required: true, message: "请输入密码", trigger: 'blur' },
        ],
      	job: [
          { required: true, message: "请输入职务", trigger: 'blur' },
      
        ],
      	govId: [
          { required: true, message: "请选择部门", trigger: 'change' },
        ],
      	role: [
//        {   required: true, message: "请选择角色", trigger: 'blur' },
        ],
      	ukey: [
          { required: true, message: "请输入ukey", trigger: 'blur' },
//        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
      },
    }
  },
watch:{
	ruleForm:{
		handler:function(val,oldval){
			console.log(val)
		},
		deep:true
	}
},
  
  mounted(){
  	this.getData()
  	this.getSection()
  	this.getRole()
  	
  },
  methods:{
  	change(i){
  	 
  	  console.log(i)
  	},
  	//提交
  	submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
          	console.log(this.ruleForm)
          	 
          	this.$xhr.post("/api/sysUser/add",{
          		  extra: this.remark,
							  govId: this.ruleForm.govId,
							  id:this.ruleForm.userId.toString(),
							  job: this.ruleForm.job,
							  name: this.ruleForm.name,
							  password: this.ruleForm.password,
							  realName: this.ruleForm.realName,
							  ukey: this.ruleForm.ukey,
							  userId: this.ruleForm.userId.toString(),
							  roleIdList: this.ruleForm.role
							  
          	})
	    	     .then((res)=>{
	    	     	  console.log(res)
	   				})
	    	     .catch((err)=>{
	    	     	  console.log(111)
	    	     })
            
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    getData(){
   				 this.$xhr.get("/api/sysUser/initializationSysUser")
    	     .then((res)=>{
    	     	  this.ruleForm.userId = res.userId
    	     	  this.time = res.createDate
    	     	  this.name = res.creator
//            console.log(res)
   				})
    },
    //获取部门列表
    	getSection(){
					this.$xhr.get("/api/sysGov/getSysGovList",{
					 })
          .then((res) => {
  					this.sectionData=res.data.pageList
//				   	console.log(res)
          })
         .catch((req)=>{
            this.tableData=[];
         })

  	},
  	//获取角色列表
  	getRole(){
					 this.$xhr.get("/api/sysRole/getSysRoleList",{
					 })
          .then((res) => {
					this.roleData=res.data.pageList
					  console.log(res)
          })
         .catch((req)=>{
//          console.log(err)
         })
  	},
  }
}
</script>

<style lang="scss" >
   .newRole{
   	 	position: relative;
   	 	padding: 45px 0;
   	 	.p_title{
   	 		i{
   	 			font-weight: bold;
   	 			font-size: 16px;
   	 		}
   	 	}
   	 	.form-add{
   	 		box-sizing: border-box;
        padding: 0 20px;
        padding-top: 36px;
        display: flex;
   	 		flex-wrap: wrap;
   	 		justify-content: space-between;
   	 		.el-form-item__label{
   	 			text-align: left;
   	 		}
   	 		.indent{
   	 			text-indent: 11px;
   	 		}
   	 		.role /deep/ label{
   	 			text-indent: 11px;
   	 		}
        .el-form-item__content>span{
   	 			padding-left: 18px;
   	 		}
   	 		.el-col{
   	 		 .el-form-item__error{ 
   	 		 	   text-indent: 15px !important;
   	 		 }
   	 	}
   	 		.p_text{
   	 			width: 330px;
   	 		}
   	 		.p_select{
   	 			width: 330px;
   	 			& /deep/ input{
   	 			height: 31px !important;
   	 		 }
   	 		}
   	 		 .remark-box{
   	 		 	padding-top: 8px;
   	 			label{
   	 				display: inline-block;
   	 				vertical-align: top;
   	 				width: 100px;
   	 				text-indent: 11px;
   	 			}

   	 			&>textarea{
   	 				width: 980px;
   	 				height:165px;
   	 				background: #f3f3f3 ;
            border-radius: 5px;
            border:none;
            text-indent: 8px;
            box-sizing: border-box;
            padding: 10px;
   	 			}
   	 		}
   	 	}
   	 	.form-submit{
   	 		/*position: absolute;
   	 		left: 0;
   	 		bottom: -80px;*/
   	 		padding-top: 40px;
   	 		width: 100%;
   	 		text-align: center;

   	 		.p_submit{
   	 			margin-right: 25px;
   	 		}
   	 	}
   	 }
</style>



