<template>
  <div class="project">
    <table>
      <thead>
      <tr>
        <th>序号</th>
        <th>项目编号</th>
        <th>项目名称</th>
        <th>计划阶段</th>
        <th>计划完成时间</th>
        <th>当前阶段</th>
        <th>阶段负责人</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item, index) in tableData">
        <td>{{index + 1}}</td>
        <td>{{item.projectCode}}</td>
        <td class="pactName">{{item.projectName}}</td>
        <td>{{item.planStage}}</td>
        <td>{{item.finishDate.substr(0,10)}}</td>
        <td>{{item.currentStage}}</td>
        <td>{{item.userName}}</td>
      </tr>
      </tbody>
    </table>
    <!--分页-->
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="curPageIndex"
      layout="total, prev, pager, next, jumper"
      :total="totalCount" :page-size="pageSize">
    </el-pagination>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: '',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        curPageIndex:1,
        pageSize:5,
        totalCount:1,
        tableData: [],
        year:'',
        month:'',
      }
    },
    watch:{
      $route(){
        this.pageload();
      },
      curPageIndex:{
        handler:function(val, oldVal){
          this.pageload()
        },
        deep:true
      },
    },
    mounted() {
      this.pageload()
    },
    methods: {
      pageload()
      {
        this.year=this.$route.query.year;
        this.month=this.$route.query.month;
        this.$xhr.get(`/api/lzPmStatis/queryPlanOverList`,{
          params:{
            year:this.year,
            month:this.month,
            page:this.curPageIndex,
            limit:10,
          }
        })
          .then((res) => {
            console.log(res);
            this.totalCount = res.data.totalCount
            this.pageSize = res.data.pageSize
            this.totalCount = res.data.totalCount
            this.tableData=res.data.pageList;
          })
          .catch((req)=>{
            console.log(req)
          })
      },
      //分页
      handleCurrentChange(val) {
        this.curPageIndex = val
      },
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";
  @import "../../../assets/css/diy-elementui";

  .project {
    background: #fff;
    padding: 20px;
    margin-top: 12px;
    table {
      width: 100%;
      thead {
        border: 1px solid #E7ECFF;
      }
      th {
        height: 42px;
        background-color: #F3F6FE;
        text-align: center;
        font-weight: bold;
        .p_select {
          width: 150px;
        }
      }
      td {
        height: 42px;
        text-align: center;
        border: 1px solid #E7ECFF;
        &.pactName {
          color: #345de7;
        }
      }
    }
  }
</style>



