<template>
  <div>
    <div class="header">
      <span>符合条件的记录12条</span>
      <a href="#" class="btn-showSearch" @click.stop.prevent='showMoreSearchWrap();'>
        <span class="iconfont icon-sousuo"></span>
        高级搜索
      </a>
    </div>
    <!--<transition enter-active-class="animated fadeInDown" leave-active-class="animated fadeOutUp">-->
    <div class="more" :class='{show:showMoreSearch}'>
      <el-row>
        <el-col :span="3">
          <span class="txtlab">项目编号/项目名称:</span>
        </el-col>
        <el-col :span="5">
          <el-input  class="p_text"></el-input>
        </el-col>
        <el-col :span="2">
          <span class="txtlab">负责人:</span>
        </el-col>
        <el-col :span="5">
          <el-input  class="p_text"></el-input>
        </el-col>
        <el-col :span="2">
          <span class="txtlab">立项时间:</span>
        </el-col>
        <el-col :span="7">
          <el-date-picker
            type="date"
            class="p_date p_time"
            placeholder="选择日期">
          </el-date-picker>
          -
          <el-date-picker
            type="date"
            class="p_date p_time"
            placeholder="选择日期">
          </el-date-picker>
        </el-col>
      </el-row>
      <el-row class="mtl">
        <el-col :span="24" style="text-algin:right">
          <button class="p_return sm source" type="button"><i class="icon iconfont icon-sousuo"></i>查询</button>
          <button class="p_return sm mll"><i class="iconfont icon-zhongzhi"></i>重置</button>
        </el-col>
      </el-row>
    </div>
    <!--</transition>-->
    <div class="mtl">
      <div class="treeTable">
        <div class="head">
          <span class="w48">&nbsp;</span>
          <span class="w311">项目名称</span>
          <span class="w200">立项类别</span>
          <span class="w200">立项日期</span>
          <span class="w200">负责人</span>
          <span class="w160">子项目个数</span>
        </div>
        <div class="tableBody">
          <div class="item" v-for="(item,index) in maintable">
            <div class="item-info" :class="{currentProject:curSelectProject==item.projectname}" @click="curSelectProject=item.projectname">
              <div class="fl w48">
                   <i v-if="item.child.length>0" class="el-icon el-icon-arrow-right" :class="{expanded:item.expanded}" @click="showMoreDetial(item)"></i>
              </div>
              <div class="fl w311 name">{{item.projectname}}</div>
              <div class="fl w200">{{item.projectcategory}}</div>
              <div class="fl w200">2017-10-12</div>
              <div class="fl w200">李明</div>
              <div class="fl w160">5</div>
            </div>
            <div class="clild-wrap"
                 :class="{show:item.expanded}" :style="showHide(item)" >
              <div class="child-item" v-for="(childitem,index) in item.child" >
                <div class="fl w48">
                  &nbsp;
                </div>
                <div class="fl w311 name">{{childitem.projectname}}</div>
                <div class="fl w200"><sapn style="font-weight: 800">中标单位</sapn>  {{childitem.projectcategory}}</div>
                <div class="fl w200"><sapn style="font-weight: 800">起始日期</sapn>   2017-10-12</div>
                <div class="fl w200"><sapn style="font-weight: 800">终止日期</sapn>   2017-10-12</div>
                <div class="fl w160"><sapn style="font-weight: 800">项目评分</sapn>   5</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <el-pagination
        @current-change="handlePageChange"
        :current-page.sync="curPageIndex"
        layout="total, prev, pager, next, jumper"
        :total="400">
      </el-pagination>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: 'project-all-list',
    props: {

    },
    data() {
      return {
        showMoreSearch:false,
        transitionName:'slideUp',
        curSelectProject:'铁笼项目4',
        curPageIndex:1,
        maintable: [
          {
            projectname: '铁笼项目1',
            projectcategory: '专项',
            projectdate: '2017-10-12',
            currentstage: '招标管理',
            capitaloperation: '追加',
            Progressoperation: '',
            TotalPoints: '3',
            expanded:false,
            child:[
              {
                projectname: '铁笼项目硬件采购',
                projectcategory: '专项',
                projectdate: '2017-10-12',
                currentstage: '招标管理',
                capitaloperation: '追加',
                Progressoperation: '',
                TotalPoints: '4'
              },
              {
                projectname: '铁笼项目硬件采购2',
                projectcategory: '专项',
                projectdate: '2017-10-12',
                currentstage: '招标管理',
                capitaloperation: '追加',
                Progressoperation: '',
                TotalPoints: '4'
              },
            ]
          },
          {
            projectname: '铁笼项目4',
            projectcategory: '专项',
            projectdate: '2017-10-12',
            currentstage: '招标管理',
            capitaloperation: '追加',
            Progressoperation: '',
            TotalPoints: '3',
            expanded:false,
            child:[
              {
                projectname: '铁笼项目5',
                projectcategory: '专项',
                projectdate: '2017-10-12',
                currentstage: '招标管理',
                capitaloperation: '追加',
                Progressoperation: '',
                TotalPoints: '4'
              },
              {
                projectname: '铁笼项目6',
                projectcategory: '专项',
                projectdate: '2017-10-12',
                currentstage: '招标管理',
                capitaloperation: '追加',
                Progressoperation: '',
                TotalPoints: '4'
              },
            ]
          },
          {
            projectname: '铁笼项目7',
            projectcategory: '专项',
            projectdate: '2017-10-12',
            currentstage: '招标管理',
            capitaloperation: '追加',
            Progressoperation: '',
            TotalPoints: '4',
            expanded:false,
            child:[]
          },
          {
            projectname: '铁笼项目8',
            projectcategory: '专项',
            projectdate: '2017-10-12',
            currentstage: '招标管理',
            capitaloperation: '追加',
            Progressoperation: '',
            TotalPoints: '4',
            expanded:false,
            child:[]
          },
        ],
      }
    },
    mounted() {

    },
    methods: {
      showMoreSearchWrap(){
        this.showMoreSearch=!this.showMoreSearch;
      },
      showHide(item){
        if(item.expanded==false || item.child.length == 0){
          return {height:"0px"}
        }
        return {height:item.child.length*52+"px"}

      },
      showMoreDetial(item){
        item.expanded=!item.expanded
      },
      getRowClass(row) {
        if(row.row.child && row.row.child.length>0)
        {
          return ''
        }
        else {
          return 'hide-expand'
        }
      },
      clickCurProject(row, event, column){
        console.log(column);
      },
      handlePageChange(page) {
        console.log(`当前页:`,this.curPageIndex);
      },
    },
    components: {
    }
  }
</script>
<style lang="scss" scoped>

  .header{
    height: 24px;
    line-height: 24px;
  }
  .more{
    width: 100%;
    height: 0px;
    overflow: hidden;
    margin-top:0px;
    padding-top:0px;
    line-height: 28px;
    text-align: right;
    transition:all 1s;
    .txtlab{
      margin-right:10px;
    }
    .el-date-editor.el-input, .el-date-editor.el-input__inner {
      width: 160px;
    }
    &.show{
      margin-top:10px;
      padding-top:15px;
      height: 93px;
      border-top: 1px solid #e7ecff;
      box-sizing: border-box;
    }
  }
  .btn-showSearch{
    float: right;
    font-size: 14px;
    color:#345de7;
  }
  .treeTable{
    .w311{width:311px;}
    .w48{width:48px}
    .w160{width:160px}
    .w120{width:120px}
    .w200{width:200px}


    .head{
      border-top: 1px solid #e7ecff;
      border-left: 1px solid #e7ecff;
      border-right: 1px solid #e7ecff;
      background: #f3f6fe;
      overflow: hidden;
      span{
        float: left;
        padding: 12px 0;
        border-right: 1px solid #e7ecff;
        padding-left: 10px;
        box-sizing: border-box;
        font-size: 14px;
        color: #333333;
        font-weight: 600;
      }
    }
    .tableBody{
      border-top: 1px solid #e7ecff;
      border-left: 1px solid #e7ecff;
      border-right: 1px solid #e7ecff;
      .item{
        border-bottom: 1px solid #e7ecff;
        .item-info{
          height: 52px;
          overflow: hidden;
          .el-icon{
            cursor: pointer;
            transition: transform .2s ease-in-out,-webkit-transform .2s ease-in-out;
            &.expanded{
              transform: rotate(90deg);
            }
          }
          .name{
            font-size: 14px;
            font-weight: 600;
            color: #345de7;
          }
          &.currentProject{
            background: #ecf5ff;
          }
        }
        .fl{
          height: 100%;
          padding: 12px 0;
          padding-left: 10px;
          box-sizing: border-box;
          border-right: 1px solid #e7ecff;
          &:last-child{
            border-right: none;
          }
          &.w48{
            padding-left: 0;
            text-align: center;
          }
        }
        .clild-wrap{
          height: 0px;
          transition:all 1s;
          overflow: hidden;
          .child-item{
            background: rgba(52, 93, 231, 0.05);
            height: 52px;
            overflow: hidden;
            border-top: 1px solid #e7ecff;
            .name{
              font-size: 14px;
              color: #345de7;
            }
          }

        }
      }
    }
  }
</style>



