    mounted() {
    },
    methods: {
    	
    		
    	
         	/*s删除附件*/
	    	deletefj(num,id){
	    		//ajax    		
	    		this.fileList.splice(num,1)
	    	},
	      handleSuccess(res, file, list) {   
	      	this.fileList.push({id:'22',filename:res.name,url:'http://www.qq.com'}); 
	     	  this.$refs.annex.clearFiles(); 
	   		},
	   		weekListSuccess(res, file, list) {   
	      	this.weekList.push({id:'22',filename:res.name,url:'http://www.qq.com'}); 
	     	  this.$refs.weekList.clearFiles();
	 
	   		},
	   		summaryListSuccess(res, file, list) {   
	      	this.summaryList.push({id:'22',filename:res.name,url:'http://www.qq.com'}); 
	     	  this.$refs.summaryList.clearFiles();
	 
	   		},
	    
	    handleError() {
	      console.log(444);
	    },
	    
	    /*confrim*/
	   
	   	submitForm(formName,type) {
     		//centerDialogVisible = false
        this.$refs[formName].validate((valid) => {
  
          if (valid) {
			     			  this.$confirm('确定提交吗？', '提示', {
			     			  		lockScroll:false,
						          confirmButtonText: '确定',
						          cancelButtonText: '取消',
						          type: 'warning'
						        }).then(() => {
						          this.$message({
						            type: 'success',
						            message: '成功!'
						          });
						        }).catch(() => {
						          this.$message({
						            type: 'info',
						            message: '已取消'
						          });          
						        });
          } else {
            console.log('error submit!!');
            return false;
          } 
        });
      },
	       
	 
	    
    },
    components: {
  		projectStep
    },
    watch:{
     
    },
  }
