<template>
  <div>
    <baseInfo :projectId='1'></baseInfo>
    
    <div class="warpper">
    	<Div class="tab">
    		<button v-for="(v,i) in proList" @click="changePro(i,v.id)" :class="{active:i==activeNum}" class="iconfont icon-liebiao">{{v.projectName}}</button>	
    	</Div>

    	<div class="warpmain">
    		<projectStep class="mtm" :stepType="2" :curStep='curStep' style='padding-top:15px;'></projectStep>
     		<div class="wapcont" v-if='proInfo'>
     				<el-row>
						  <el-col :span="3">
						  	        <span class="lable">项目名称: </span>
						 </el-col>
							 <el-col :span="9">
						  	        <span class="txt">{{proInfo.projectName}}</span> 

							</el-col>
							 <el-col :span="3">
						  	        <span class="lable">项目编号: </span>
							 </el-col>
							 <el-col :span="9">
						  	        <span class="txt">{{proInfo.projectCode}}</span>     
							</el-col>	
						</el-row>
						
							<el-row>
						  <el-col :span="3">
						  	        <span class="lable">录入人: </span>
							</el-col>
							 <el-col :span="9">
						  	        <span class="txt">{{proInfo.creator}}</span>     
							</el-col>
							 <el-col :span="3">
						  	        <span class="lable">录入日期: </span>
							</el-col>
							 <el-col :span="9">
						  	        <span class="txt">{{proInfo.createDate}}</span>     
							</el-col>	
						</el-row>
						
						<el-row>
						  <el-col :span="3">
						  	        <span class="lable">项目负责人: </span>
							</el-col>
							 <el-col :span="9">
						  	        <span class="txt">{{proInfo.manager}}</span>     
							</el-col>
							 <el-col :span="3">
						  	        <span class="lable">项目启动日期: </span>
							</el-col>
							 <el-col :span="9">
						  	        <span class="txt">{{proInfo.startDate}}</span>     
							</el-col>	
						</el-row>
						
						<el-row>
						  <el-col :span="3">
						  	        <span class="lable">项目完成日期: </span>
							</el-col>
							 <el-col :span="9">
						  	        <span class="txt">{{proInfo.finishDate}}</span>     
							</el-col>
							 <el-col :span="3">
						  	        <span class="lable">提交日期: </span>
							</el-col>
							 <el-col :span="9">
						  	        <span class="txt">{{proInfo.finishDate}}</span>     
							</el-col>	
						</el-row>
						
						<el-row>
						  <el-col :span="3">
						  	        <span class="lable">备注: </span>
							</el-col>
							 <el-col :span="21">
							 		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm"> 
						  	      	 <el-form-item prop="remark">
						  	        	<el-input type="textarea" :disabled='!isshow' v-model="ruleForm.remark" :rows="4"  class="p_text"></el-input>
						  	        </el-form-item>
						  	 </el-form>
							</el-col>
							
						</el-row>
			 	
							<div class="files	">
						  		<span class="lable">附件：</span>
						  		
						  		<div class="file-list-box">
						  			<div class="tips-box">	
							  	
							  			<el-upload class="upload-demo small" :data='uploadParms' ref="annex3" :disabled='!isshow' action="http://192.168.200.55:8090/CommonFileUploadController/upload" :on-success="handleSuccess" :on-error="handleError"   :show-file-list="true">
	        									<el-button   size="small" type="primary">上传附件</el-button>
							  			</el-upload>	
						  			</div>	
						   	<div class="file-list-boxs">
						  				<div class="filelist" v-for='(val,i) in fileList'>
								 				<div class="file-group">
					                 	<el-input class="p_text" disabled="" :value='val.attachmentName' placeholder="合同文件"></el-input>
					             	</div>
	                  		<a :href="$xhr.defaults.baseURL+val.attachmentUrl" target="_blank" class="iconfont icon-xiazai2"></a>
             			 			<span class="iconfont icon-jianhao" @click="deletefj(i,val.id)" v-if='isshow'></span> 
								 			</div>	
						  			</div>	 
						 			</div>
							
						</div>
						
     		</div>
    	</div>	
    		
    	<el-row v-if='isshow'>
    		<el-col :span="24" style="text-align: center;margin-top: 20px;">
    				<!--提交按钮-->	
      	 <button type="submit" class="p_submit" style="margin-right: 20px;" @click="submitForm('ruleForm','1')"><i class="iconfont icon-submit"></i>提交</button>
      	<!--保存按钮-->   
      	 <button   @click="submitForm('ruleForm','0')" class="p_save"><i class="iconfont icon-baocun"></i>保存</button>
    		</el-col>
    	</el-row>
    </div>
  </div>
</template>

<script type="text/javascript">
import baseInfo from '../components/baseinfo'
import projectStep from '../components/projectStep'
import {StateCode} from '../../../assets/js/config.js'

  export default {
    name: 'information',
    data() {
      return {
        curStep:'5',
        activeNum:0,
        proList:null,
        isshow:true,
        prams:{
        	projectId:'1',
        	parentProjectId:'1'
        },
        uploadParms:{projectId:1,subProjectId:'',stageId:StateCode.information},
        ruleForm:{
        	remark :'',
        	fileIds:[] 
        },
    		rules: {
    			remark: [
            { required: true, message: '请填写备注', trigger: 'blur' }
          ]
       },
         
     		fileList: null,    
        proInfo:null,
       
      }
    },
    mounted() {
      this.getProListName();
    },
    methods: {
    	/*获取项目列表*/
    	getProListName(){
    			 this.$xhr.get(`/LzPmSubprojectController/querySubProjectInfoById`,{
					 	  params:{
					 	  	...this.dataParms				 	  	
					 	  }
					 })
          .then((res) => {
          	 this.proList=res.lzPmSubproject;    
          	 	this.getProInfo(res.lzPmSubproject[0].id)
          })
         .catch((req)=>{
            //this.tableData=[];
         })
    	},
    	/*点击获取项目详情*/
    	changePro(num,pid){
    		this.activeNum=num,
    		this.getProInfo(pid)
    	},    
    	/*获取项目详情*/
    	getProInfo(pid){
    		 this.$xhr.get(`/LzPmSubprojectController/info`,{
					 	  params:{
					 	  	id:pid		 	  	
					 	  }
					 })
          .then((res) => {
          	 this.proInfo=res.lzPmSubproject;   
          	 this.ruleForm.remark=res.lzPmSubproject.remark
          	 this.fileList=res.lzPmSubproject.lzPmAttachment;
          	 this.curStep=res.lzPmSubproject.stageId.toString()
          	 this.uploadParms.subProjectId=pid
          	 res.lzPmSubproject.status==1 ? this.isshow=false : this.isshow=true
          	 	
          	  
          })
         .catch((req)=>{
         })
    	},
	
	 
			 	deletefj(num,id){
			 	    this.$confirm('你确定要删除吗？', '提示', {
		          confirmButtonText: '确定',
		          lockScroll:false,
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		        	//ajax    		
		        	this.$xhr.get(`/LzPmAttachmentController/delete`,{
					 	  params:{
					 	  	attachmentId:id		 	  	
						 	  }
						 })
	          .then((res) => {
	          	 this.fileList.splice(num,1)
	          	 this.$message({
		            type: 'success',
		            message: '删除成功!'
		           
		          });
	         	})
		    	}).catch(() => {          
		        });
    		
    	},
      handleSuccess(res, file, list) {   
      	this.fileList.push({id:res.lzPmAttachmentPojo.id
,attachmentName:res.lzPmAttachmentPojo.attachmentName,attachmentUrl:res.lzPmAttachmentPojo.attachmentPath}); 
     		 this.$refs.annex3.clearFiles(); 
   		},
   		 handleError() {
 
    	}, 
    	submitForm(formName,status) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
          	let text='';
           	status==1?text='提交' :text='保存';
           	this.$confirm('你确定要'+text+'吗？', '提示', {
		          confirmButtonText: '确定',
		          lockScroll:false,
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		        	//ajax  
		         for(let i=0;i<this.fileList.length;i++){
		        	 	this.ruleForm.fileIds.push(this.fileList[i].id)
		         }
		        		
		        	console.log(this.ruleForm.fileIds)
		        	
		        	this.$xhr.post(`/LzPmSubprojectController/addSubProjectInfo`,{
					 	  		id:this.uploadParms.subProjectId,
					 	  		stageId:StateCode.information,
					 	  		remark:this.ruleForm.remark,
					 	  		status:status,
					 	  		updateFileIds:this.ruleForm.fileIds
		        	})
	          .then((res) => {
	          	 this.$message({
		            type: 'success',
		            message: text+'成功!',
		             onClose:function(){
		            	window.location.reload()
		            }
		          });
	         	})
		    	}).catch(() => {          
		        });
           
          	
          	
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }, 
    	confirmLog(text,btn) {
		        this.$confirm(text, '提示', {
		          confirmButtonText: '确定',
		          lockScroll:false,
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: btn+'成功!'
		          });
		        }).catch(() => {          
		        });
		 		}
      
      
      
      
    },
    	watch:{
  	dataParms:{
  		handler:function(val, oldVal){
     		 this.getListData()
  		},
  		deep:true
  	},
  
  },
  computed:{  	
  	  dataParms() {
        return this.prams
      },
  },
    components: {
      baseInfo,
      projectStep
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
 .el-col-9{
 		min-width: 363px;
 		span{
 			width:100%;
 			display: inline-block;
 		}
 }
 
  .warpper{
  
 		.warpmain{
 			 	background:#fff;
 			 	padding:40px; 			  
 			 	.right{
 			 		float: right;
 			 	}
 			 	.el-row{
 			 		margin-bottom: 20px;
 			 	}
 			 	.wapcont{
 			 		margin-top: 20px;
 			 	}
 		}
  	.tab{
  		margin-top:20px;
  		display: flex;
  		align-items: center;
  		button{
  			position: relative;
	  		width: 176px;
				height: 46px;
				background:#eaeefd;	 
				font-size: 16px;
				font-weight: normal;
				font-stretch: normal;
				line-height: 46px;
				letter-spacing: 0px;
				color: #456cf0;
     		text-overflow: ellipsis;
     		overflow: hidden;
				border: none;
				padding-left: 40px;
				border-right: 1px solid #fff;
				text-align: left;	
				padding-left: 40px;
   		  padding-right: 20px;
    		white-space: nowrap;
				&:last-child{
					border: none;
				}
				&:before{
						position: absolute;
						left:20px;
						top: 0px;
						font-size: 16px;
						margin-right: 20px;
				}
		  
		    border-bottom: 4px solid #eaeefd;
			&.active{
				background: #fff;
				border-bottom: 4px solid #345de7;
			}
  		}
  	}
  }
  
    .files{
  	overflow: hidden;
  	span.lable{
 			width: 122px;
 		}
  	&>span,&>.file-list-box{
  		display: inline-block;
  		float: left;
  	}
  	&>.file-list-box{
  			width: 86%;
		    display: flex;
		    flex-wrap: wrap;
		    justify-content: space-between;
  	}
  } 
  .filelist{
  	width: 330px;
 	
  	display: inline-block;
  	margin-bottom: 20px;
  	.file-group{
		 	 width: 240px;
		 }
 		.icon-xiazai2,.icon-jianhao{ 	 
 			width: auto;
      margin-left: 10px;
      display:inline-block;
      vertical-align: middle;
      cursor: pointer;
      color:#345DE7;
      font-size: 24px;
      &:hover{
        color: #409EFF;
      }
    }
  }
   .tips-box{
 	width: 100%;
 	margin-bottom: 10px;
 	button{
  		color: #345DE7;
   		min-width: 82px;
	    height: 28px;
	    line-height: 10px;
	    background: #FDFDFD;
	    border: 1px solid #E2E2E2 !important;
	    border-radius: 25px !important;
  	}
  	.tips{
  		margin-bottom: 10px;
  		span{
  			color: #ff7800;
  		}
  	}
 } 
 .file-list-boxs{
		.filelist{
			margin-bottom: 10px;
			width: 100%;
		} 	
 }
  
</style>



