<template>
  <div class="assetsClassManage-wrap">
  	<div class="assetsClassManage-content clearfix">
  		 <div class="fl left-content">
  		 	<div class="p_title">
      	 	<i class="iconfont icon-wendang1"></i><p>基本信息</p>
      	 </div>
  		 	<div class="left-tree">
  		 		<el-tree 
  		 			accordion
  		 			:data="treeData" 
  		 			:props="defaultProps"
						 highlight-current
						 default-expand-all
  		 			 @node-click="handleNodeClick">
  		 		</el-tree>
  		 		
  		 	</div>
  		 </div>
  		 <div class="fr right-content">
  		 	  <button class="p_return sm p_add" type="button" @click="showAddDialog()"><i class="icon iconfont icon-jiahao"></i>添加</button>					
					<table>
						<tr>
							<th>资产类别编号</th>
							<th>资产类别名称</th>
							<th>操作</th>
						</tr>
						<tr v-for="(val,index) in listData">
							<td>{{val.name}}</td>
							<td>{{val.name}}</td>
							<td>
								<button class="p_return sm edit"  type="button" @click="showEditDialog(val)">
									<span>编辑</span>
								</button>
								<button class="p_return sm remove" v-if="val.children.length==0" type="button" @click="remove(val)">删除</button>
							</td>
						</tr>
					</table>

  		 	  <el-dialog
					  :visible.sync="dialogVisible"
					  width="380px"
					  :before-close="handleClose">
						<el-form :model="ruleForm" :rules="rules" ref="ruleForm"  action="" method="post">
							<div class="p_title">
									<i class="iconfont icon-add"></i><p>{{optionType}}</p>
							</div>
							<div class="tree-box">
								<el-col >
											<el-form-item label="资产编号:" prop="assetTypeCode">
												<el-input class="p_text" placeholder="请输入资产编号" v-model="ruleForm.assetTypeCode"></el-input>
											</el-form-item>
											<el-form-item label="资产名称:"  prop="assetTypeName">
												<el-input class="p_text" placeholder="请输入资产名称" v-model="ruleForm.assetTypeName"></el-input>
											</el-form-item>
											<el-form-item label="所属上级分类:" >
											<el-input class="p_text" :disabled="true" v-model="curNode.name"></el-input>
										</el-form-item>
								</el-col>
							</div>
						</el-form>
						<div slot="footer" class="dialog-footer">
							<button type="submit" class="p_submit" @click="add()"><i class="iconfont icon-submit"></i>提交</button>
							<button  class="p_return mll" @click="dialogVisible = false">取消</button>
						</div>
					</el-dialog>

  		 </div>
  	</div>
  </div>
</template>

<script type="text/javascript">
import axios from 'axios'

export default {
  name: 'component_name',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
			optionType:'添加资产类别',
			curNode:{},
    	dialogVisible: false,
    	dialogVisibleb: false, 
    	value:"完成",
    	editNum:"",
    	editName:"",
    	editUpClass:"",
    	item: "",
    	listData:[],
    	id:null,
    	showDel:false,
    	child:null,
			parentName:'',
    	ruleForm:{
    		assetTypeCode: '',
    		assetTypeName: '',
				parentTypeId:'',
    	},
    	rules:{
    		assetTypeCode:[
    		   { required: true, message: '请输入资产类别编号', trigger: 'blur' },
    		],
    		assetTypeName:[
    		   { required: true, message: '请输入资产类别名称', trigger: 'blur' },
    		],
    	},
			defaultProps: {
				children: 'children',
				label: 'name'
			},
			treeData:[],
    }
  },
  mounted(){
		this.initData();
  },
  methods:{
		initData(){
			this.$xhr.get('/api/lzPmAssetType/getLzPmAssetTypeTree').then((res)=>{
					function getChild(pid,pname){
						var listChild=[];
						for(var node of res.LzPmAssetTypeTrees){
							if(node.pId==pid){
								var childNode=getChild(node.id,node.name); 
								listChild.push({
									id:node.id,
									name:node.name,
									code:node.name,
									pId:node.pId,
									pname:pname,
									children:childNode
								})
							}
						}
						return listChild;
					}
					this.treeData = getChild('0','');
					this.listData = this.treeData;
			});
		},
		showAddDialog(){
			this.optionType='添加资产类别';
			this.dialogVisible = true;
			this.ruleForm.assetTypeCode= '';
			this.ruleForm.assetTypeName= '';
			delete this.ruleForm.id;
		},
		showEditDialog(obj){
			this.optionType='修改资产类别';
			this.dialogVisible = true;
			this.ruleForm.assetTypeCode = obj.code;
			this.ruleForm.assetTypeName = obj.name;
			this.ruleForm.parentTypeId = obj.pId;			
			this.parentName=obj.pname;
			this.ruleForm.id = obj.id;
		},
  	//添加
  	add(){
  		this.dialogVisible = false;
			let that = this;
			if(this.optionType=='添加资产类别'){
				this.$refs.ruleForm.validate(function(ok){
					if(!ok) return; 
					that.$xhr.post('/api/lzPmAssetType/add',that.ruleForm).then((res)=>{
							that.$message({type: 'success',message: '操作成功!'});
							that.ruleForm.assetTypeCode= '';
							that.ruleForm.assetTypeName= '';
							that.initData();
					});
				});
			}
			else{ //编辑资产类别
				this.$refs.ruleForm.validate(function(ok){
					if(!ok) return; 
					that.$xhr.put('/api/lzPmAssetType/update',that.ruleForm).then((res)=>{
							that.$message({type: 'success',message: '操作成功!'});
							that.ruleForm.assetTypeCode= '';
							that.ruleForm.assetTypeName= '';
							that.initData();
					});
				});
			}
  	},
  	//tree点击
  	handleNodeClick(data) {
				this.curNode=data;
				this.parentName=data.name;
				this.ruleForm.parentTypeId = data.id;
        if (data.hasOwnProperty("children")){
        	this.showDel = false
        	this.listData = data.children
        	this.editUpClass = data.name
        	let str = data.id.toString()
	        	 if(str.length==2){
	        		this.showDel = true
	        	 }
	        	 else{
	        	 	this.child = true
	           }
        	}
     },
     //关闭添加
    handleClose(done) {
        done()
    },
    //编辑
    edit(i){
    	this.dialogVisibleb = true
    	this.item = i;
    	this.editNum = this.listData[i].num
    	this.editName = this.listData[i].label  
    	
    },
    //编辑提交
    submit(ruleForm){
    	this.$refs[ruleForm].validate((valid) => {
          if (valid) {
          	this.listData[this.item].num = this.editNum
          	this.listData[this.item].label = this.editName
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    	this.dialogVisibleb = false
    },
    //删除
    remove(obj){
			this.$confirm('确定要删除?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.$xhr.delete('/api/lzPmAssetType/delete?ids='+obj.id).then((res)=>{
						this.$message({type: 'success',message: '操作成功!'});
						this.initData();
				});
			});
    },
    //关闭编辑
    handleCloseb(done) {
      done()
    },

  }
}
</script>

<style lang="scss">
  .assetsClassManage-wrap{
  	.assetsClassManage-content{
  		
  		padding: 45px 20px;
  		.left-content{
  			width: 400px;
  			/*height: 150px;*/
  			.left-tree{
  				margin-top: 14px;
  				width: 348px;
  				border: 1px solid #F3F3F3;
  				height: 245px;
  				overflow: auto;
  			}
  		}
  		.right-content{
  			width: 690px;
  			/*height: 285px;*/
  			.p_add{
  				border: 1px solid #345DE7;
  				color: #345DE7;
  				background: #EAEEFD;
  				i{
  					margin-right: 7px;
  					margin-left: -4px;
  				}
  				&:hover{
  					background: #fff;
  				}
  			}
  			/*弹出树图*/
  			.el-dialog__wrapper{
  				&>.el-dialog{
  					height:400px ;
  					.el-dialog__body{
  						/*height: 235px;*/
  						.p_title{
  							width: 100%;
  							margin-top: -24px;
  							text-indent: 4px;
  							i{
  								margin: 0;
  							}
  						}

  						.tree-box{
  							box-sizing: border-box;
  							padding-left: 25px;
  							padding-top: 15px;
  							height: 250px;
  							.p_text{
  								width: 265px;
  							}
  							.el-form-item__content{
  								line-height: 30px;
  							}
  						}
  					}
  					.el-dialog__footer{
  						text-align: center;
  					}
  				}
  			}
  			&>table{
  				margin-top: 10px;
  				width: 690px;
  				border:1px solid #E7ECFF;
  				text-align: center;
  				tr{
  					height: 42px;
  					th{
  						text-align: center;
  						background: #F3F6FE;
  						font-weight: bold;
  					}
  					td{
  						border:1px solid #E7ECFF;
  						width: 230px;
  						.edit{
  							background: #1276DC;
  							color:#fff;
  							border:none;
  							&:hover{
					        background: rgba(52,93,231,0.8);
					     }
  						}
  						.el-dialog__wrapper{
  							  text-align: left;
  							  .el-dialog{
  							  	box-shadow: 0 0 0 transparent;
  							  }
  						}
  						
  						.remove{
  							background: #fff;
  							color: #FA4A21;
  							border: 1px solid #FEDBD3;
  						}
  					}
  				}
  			}
  		}
  	}
  }
</style>



