              </el-form-item>
            </el-col>
            <el-col :span="3"> &nbsp;</el-col>
            <el-col :span="11">
              <el-form-item label="合同名称:">
                {{ruleForm.htname}}
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="10">
              <el-form-item label="中标金额:">
                {{ruleForm.zbje}}
              </el-form-item>
            </el-col>
            <el-col :span="3"> &nbsp;</el-col>
            <el-col :span="11">
              <el-form-item label="中标供应商:">
                {{ruleForm.zbgys}}
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="10">
              <el-form-item label="合同签订日期:">
                {{ruleForm.htqddate}}
              </el-form-item>
            </el-col>
            <el-col :span="3"> &nbsp;</el-col>
            <el-col :span="11">
              <el-form-item label="提交日期:">
                {{ruleForm.tjdate}}
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="10">
              <el-form-item label="合同状态:">
                <el-select    v-model="ruleForm.htstate" placeholder="正常" class="p_select">
                  <el-option label="正常" value="1"></el-option>
                  <el-option label="不正常" value="2"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="3"> &nbsp;</el-col>
            <el-col :span="11">
              <el-form-item label="录入日期:">
                {{ruleForm.lrdate}}
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="10">
              <el-form-item label="录入人:">
                {{ruleForm.lrren}}
              </el-form-item>
            </el-col>
            <el-col :span="3"> &nbsp;</el-col>
            <el-col :span="11">
            </el-col>
          </el-row>
          <el-form-item label="备注:">
            <el-input type="textarea"  :rows="3" v-model="ruleForm.bz" class="p_text"></el-input>
          </el-form-item>
          <el-form-item label="附件">
            <el-row>
              <el-col :span="8">
                <div>
                  <div class="file-group">
                    <el-input class="p_text" disabled="" value='合同文件.docx' placeholder="合同文件"></el-input>
                    <el-upload class="p_upload"  ref="uploadxx" :headers="myheaders" action="http://192.168.20.145:8080/aobo/file/upload"  :limit="1"  :show-file-list="true">
                      <el-button size="small" >点击上传</el-button>
                    </el-upload>
                  </div>
                </div>
              </el-col>
              <el-col :span="4">
                <span class="iconfont icon-xiazai2"></span>
                <span class="iconfont icon-jianhao"></span>                  
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <div>
                  <div class="file-group">
                    <el-input class="p_text" disabled="" value='合同文件.docx' placeholder="合同文件"></el-input>
                    <el-upload class="p_upload" disabled  ref="uploadxx" :headers="myheaders" action="http://192.168.20.145:8080/aobo/file/upload"  :limit="1"  :show-file-list="true">
                      <el-button size="small" disabled >点击上传</el-button>
                    </el-upload>
                  </div>
                </div>
              </el-col>
              <el-col :span="4">
                <span class="iconfont icon-xiazai2"></span>
                <span class="iconfont icon-jianhao"></span>                  
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="8">
                <el-input class="p_text" disabled="" value='合同文件.docx' placeholder="合同文件"></el-input>
              </el-col>
              <el-col :span="4">
                <span class="iconfont icon-xiazai2"></span>
                <span class="iconfont icon-jianhao"></span>                  
              </el-col>                
            </el-row>          
            <el-row class="mtm">
              <el-col :span="4">
                  <el-upload class="p_upload"  ref="uploadxx" :headers="myheaders" action="http://192.168.20.145:8080/aobo/file/upload"  :limit="1"  :show-file-list="true">                        
                    <button class="p_return sm">上传其他文件</button>    
                  </el-upload>
              </el-col>
            </el-row>                                
                                          
          </el-form-item>
          <div style="text-align:center;">
            <button type="submit" class="p_submit"><i class="iconfont icon-submit"></i>提交</button>
            <!--保存按钮-->
            <button class="p_save mll" ><i class="iconfont icon-baocun"></i>保存</button>
          </div>          
        </el-form>
      </div>      
