         <table class="logRecord-table">
           <tr>
             <th  width="15%">变更日期</th>
             <td width="15%">{{item.bgdate}}</td>
             <th width="15%">录入人</th>
             <td width="15%">{{item.lrren}}</td>
             <th width="20%">附件</th>
             <td width="20%">{{item.fj}}</td>
           </tr>
           <tr style="height: 80px;">
             <th  width="15%">更新内容</th>
             <td width="85%" colspan="7" style=" vertical-align: top; text-align: left">
               {{item.gxnr}}
             </td>
           </tr>
         </table>
      </div>
      <div style="text-align: center;padding-top: 5px;">
         <button class="p_save mll" @click="addbjju" ><i class="iconfont icon-jiahao"></i>增加合同变革记录</button>
        </div>

      <el-dialog  size="mini" :visible.sync="dialogTableVisible" left>
        <el-form  :model="adddatatable" ref="adddatatable"   label-position="right" label-width="90px" >
          <el-form-item label="更改内容:" prop="zjly">
            <el-input type="textarea" :rows="3" v-model="adddatatable.gxnr" class="p_text"></el-input>
          </el-form-item>
