    </div>
  </div>
</template>
<script type="text/javascript">
  import baseInfo from '../components/baseinfo'
  import projectStep from '../components/projectStep'
  export default {
    name: 'information',
    data() {
      return {
        dialogTableVisible: false,
        adddatatable: {
          bgdate:'',
          lrren:'',
          fj:'',
          gxnr:''
        },
        ruleForm: {
          hth:'20010309481',
          htname: '数据铁笼硬件采购',
          zbje:'¥50,000',
          zbgys:'2017-10-11',
          htqddate:'2017-10-11',
          tjdate:'2017-10-11',
          htstate:'正常',
          lrdate:'2017-10-11',
          lrren:'张三',
          bz:''
        },
        htbgjitable:[{
          bgdate:'2017-11-14',
          lrren:'张三',
          fj:'正式合同.docx',
          gxnr:'sdfsdf'
        }]
      }
    },
    methods: {
      addbjju()
      {
        this.dialogTableVisible=true;
      },
      addsubment()
      {
        this.htbgjitable.push({bgdate:'2017-11-14',lrren:'张三',fj:"柳州铁笼项目合同.docx",gxnr:this.adddatatable.gxnr});
        this.dialogTableVisible = false;
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
    },
    components: {
      baseInfo,
      projectStep
    }
  }
</script>
<style lang="scss" scoped>
  .tab{
    margin-top:20px;
  button{
    width: 176px;
    height: 46px;
    background:#eaeefd;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 45px;
    letter-spacing: 0px;
    color: #456cf0;
    border: none;
  &:before{
     margin-right: 5px;
   }
  display: inline-block;
  margin-right: -4px;
  border-bottom: 4px solid #eaeefd;
  &.active{
     background: #fff;
     border-bottom: 4px solid #345de7;
   }
  }
  }
  .create_porject{
    margin-top:10px;
    padding: 20px;
    background: #fff;
    font-size: 14px;
  }
  .logRecord-table{
    margin-top: 10px;
    width: 100%;
    border: 1px solid #f3f6fe;
    tr{
      box-sizing: border-box;
      height: 36px;
      text-align: center;
      th{
        border-top: 2px solid #f3f6fe;
        background: #f3f6fe;
        text-align: center;
        font-weight: 800;
      }
      td{
        border-top: 1px solid #f3f6fe;
      }
    }
  }
  .addmodeldiv{
    background-color:#ffffff;
    width:800px;
    height:300px;
    position: fixed;
    left:300px;
    top:320px;
    font-weight: 800;
    border: 1px solid #5b81ff;
    transition: bottom 1s;

  }
  .create_porject {
    .icon-xiazai2, .icon-jianhao {
      margin-left: 10px;
      display: inline-block;
      vertical-align: middle;
      cursor: pointer;
      color: #345DE7;
      font-size: 24px;
      &:hover {
        color: #409EFF;
      }
    }
  }
</style>



