<template>
  <div>
    <div v-if="projectId!=0">
      <div class="basetitle">
          <span class="base-title-icon"></span>
          <span class="base-title">项目信息</span>
          <button class="p_return sm fr"><i class="iconfont icon-zhongzhi"></i>返回</button>
      </div>
      <baseInfo class="mbm" :projectId='projectId'></baseInfo>
    </div>
    <div v-else>
      <div class="basetitle">
          <span class="base-title-icon"></span>
          <span class="base-title">新建项目</span>
          <div class="fr">
            <button class="p_return sm"><i class="iconfont icon-zhongzhi"></i>返回</button>
            <!--<button class="p_return sm mll"><i class="iconfont icon-shanchu"></i>删除</button>-->
            <button class="p_return remove sm mll" type="button" >删除</button>
          </div>
      </div>
    </div>
    <projectStep  :stepType="1" :curStep='curStep' style='padding-top:35px;'></projectStep>
    <div class="create_porject">
      <div  class="p_title"><i  class="iconfont icon-tianxie"></i><p >填写基本信息</p></div>
      <el-form class="p-l" ref="ProjectForm" :model="ruleForm" :rules="rules" label-position="left" label-width="90px" v-on:submit.prevent>
        <el-row>
          <el-col :span="10">
            <el-form-item label="项目名称" prop="setupName">
              <el-input v-model="ruleForm.setupName" class="p_text" :disabled="isReadOnly" ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4"> &nbsp;</el-col>
          <el-col :span="10">
            <el-form-item label="立项日期" prop="setupTime">
              <el-date-picker type="date" :disabled="isReadOnly"  placeholder="选择日期" v-model="ruleForm.setupTime" value-format="yyyy-MM-dd"  class="p_date"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="录入日期">
              <span>{{ruleForm.createDate}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="4"> &nbsp;</el-col>
          <el-col :span="10">
            <el-form-item label="录入人">
              <span>{{ruleForm.creator}}</span>
            </el-form-item>
          </el-col>
        </el-row>        
        <el-row>
          <el-col :span="10">
            <el-form-item label="负责人" prop="manager" >
              <el-input v-model="ruleForm.managerName" class="p_text" :disabled="true"></el-input>
              <a  class="btn-selectUesr" @click="showDialog()">选择人员</a>
            </el-form-item>
          </el-col>
          <el-col :span="4"> &nbsp;</el-col>
          <el-col :span="10">
            <el-form-item label="申请部门">
              <el-select v-model="ruleForm.applyDept" placeholder="申请部门" class="p_select" :disabled="isReadOnly">
                <el-option :label="item.name" :value="item.id" v-for="(item,index) in orgList" :key="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>        
        <el-row>
          <el-col :span="10">
            <el-form-item label="立项类别">
              <el-select v-model="ruleForm.setupCategory" placeholder="立项类别" class="p_select" :disabled="isReadOnly">
                <el-option label="专项资金" :value="1"></el-option>
                <el-option label="部门资金" :value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"> &nbsp;</el-col>
          <!--<el-col :span="10">
            <el-form-item label="立项状态">
              <el-select placeholder="立项状态" class="p_select">
                <el-option label="通过" value="1"></el-option>
                <el-option label="不通过" value="2"></el-option>
                <el-option label="待批复" value="3"></el-option>
              </el-select>
            </el-form-item>
          </el-col>-->
        </el-row>
        <el-form-item label="申请理由 " prop="reason">
          <el-input v-model="ruleForm.reason" class="p_text" :disabled="isReadOnly"></el-input>
        </el-form-item>        
        <el-form-item label="备注">
          <el-input type="textarea" :rows="4" v-model="ruleForm.remark" class="p_text" :disabled="isReadOnly"></el-input>
        </el-form-item>
        <!--<el-row>
          <el-col :span="10">-->
            <el-form-item label="附件" prop="projectName">
              <el-row class="fileremark" v-if="!isReadOnly">
                必须上传的附件 
                <span v-for='(item,index) in requiredProjectFile'>
                              【{{item.attachmentName}}】
                </span>
              </el-row>
              <template v-for="(item,index) in ruleForm.updateFileIds">   
                <el-row>
                  <el-col :span="8">
                    <el-input class="p_text" disabled="" value='xxxx' ></el-input>
                  </el-col>
                  <el-col :span="4">
                    <span class="iconfont icon-xiazai2"></span>
                    <span class="iconfont icon-jianhao" v-if="!isReadOnly"></span>                  
                  </el-col>
                </el-row>
              </template> 
              <el-row v-if="!isReadOnly">
                <el-col :span="8">
                    <el-upload class="p_upload"  ref="uploadxxx" :data="uploadData" :on-success="handleSuccess" :on-error="handleError"   :action="uploadPath" :limit="1" :show-file-list="true">                        
                      <a class="p_return sm">上传附件</a>    
                    </el-upload>
                </el-col>
              </el-row>

            </el-form-item>
          <!--</el-col>
        </el-row>        -->
        <div class="txt-c" v-if="!isReadOnly">
            <button class="p_submit " @click.stop.prevent="SubmitProject(2,1)"><i  class="iconfont icon-submit"></i>提交</button>
            <button class="p_save mll" @click.stop.prevent="SubmitProject(1,0)"><i class="iconfont icon-baocun"></i>保存</button>
            <button class="p_save mll" @click.stop.prevent="SubmitProject(3,0)">不通过</button>
        </div>
      </el-form>
      <div>
        <div class="p_title"><i  class="iconfont icon-jianlixiangmu"></i><p >项目组织架构</p></div>
        <table class="orgusertb mtl">
          <tr>
            <th style="width:72px;">序号</th>
            <th style="width:100px;">角色</th>
            <th style="width:120px;">负责阶段</th>
            <th>人员</th>
            <th style="width:100px;">修改人</th>
            <th style="width:160px;">修改时间</th>
            <th style="width:120px;">操作</th>
          </tr>
          <tr>
            <td>1</td>
            <td>
              <!--<el-select class="p_select">
                <el-option label="组长" value="组长"></el-option>
                <el-option label="副组长" value="副组长"></el-option>
                <el-option label="组员" value="组员"></el-option>
              </el-select>-->
            </td>
            <td>
              <!--<el-select class="p_select">
                <el-option label="资金管理" value="资金管理"></el-option>
                <el-option label="招标管理" value="招标管理"></el-option>
              </el-select>-->
            </td>
            <td>张三</td>
            <td>张三</td>
            <td>2017-10-23 23:00:00</td>
            <td>
              <a  @click="" >选择人员</a>
              <span class="iconfont icon-jianhao"></span>                  
            </td>
          </tr>
        </table>
      </div>
    </div>
    <el-dialog
      title="选择人员"
      :visible.sync="dialogVisible">
      <selectUser ref="comSelectUser" :userlist="orgUserList" @SaveSelectUser="selectManageUser" @CloseSelectUser="dialogVisible=false" :single="true" ></selectUser>   
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
import baseInfo from '../components/baseinfo'
import projectStep from '../components/projectStep'
import selectUser from '../../system/components/selectUser'

import {StateCode} from '../../../assets/js/config.js'
  export default {
    name: 'project-lixiang',
    props: {
    },
    data() {
      return {
        // 当前项目所处阶段
        curStep:'1',
        // 当前项目Id
        projectId:0,
        // 部门集合
        orgList:[],
        // 项目必传的文档名称
        requiredProjectFile:[],
        // 上传文件集合
        updateFileList:[],
        uploadPath:this.$xhr.defaults.baseURL+'/CommonFileUploadController/upload',
        uploadData:{attachmentCate:1,stageNo:StateCode.CREATE,stageId:StateCode.CREATE},
        // 是否只读模式
        isReadOnly:false,
        ruleForm: {
          setupName: '',
          setupTime: '',
          manager: '99999',
          managerName:'99999',
          applyDept:'',
          setupCategory: 1,
          reason:'',
          remark:'',
          setupStatus:1, 
          createDate:null,
          creator:null,
          status:0,
          updateFileIds:[],
          id:null,
          projectId:null,
        },
        rules: {
          setupName: [
            { required: true,message: '请输入项目名称', trigger: 'blur' }
          ],
          setupTime: [
            { required: true, message: '请选择立项日期', trigger: 'change' }
          ], 
          manager: [
             { required: true,message: '请选择负责人', trigger: 'blur' }
          ],
          reason: [
            { required: true,message: '请输入申请理由', trigger: 'blur' }
          ],
        },
        dialogVisible:false,
        orgUserList:[
          
        ],
        userTarget:'manager'
      }
    },
    mounted() {
      
      if(this.$route.query.pid){
        this.projectId = this.$route.query.pid;
      }
      this.init();
    },
    methods: {
        init(){
          // 部门
          this.$xhr.get('/api/sysGov/getSysGovList?limit=100').then((res)=>{
              if(res.data.pageList&&res.data.pageList.length>0){
                  this.orgList = res.data.pageList;
              }
          });
          // 必须上传的附件
          this.$xhr.get('/api/sysPmStage/getAttachmentListByID?id='+StateCode.CREATE).then((res)=>{
            if(res.data){
              this.requiredProjectFile = res.data;
            }
          });
          // 项目信息
          if(this.projectId!=0){
            this.$xhr.get("/LzPmProjectController/info?projectId="+this.projectId).then((res)=>{     
              console.log(res);     
              if(res.lzPmProject != null){
                  if(res.lzPmProject.lzPmSetupPojo.status==1){
                    this.isReadOnly=true;
                  }
                  let data = res.lzPmProject.lzPmSetupPojo;
                  this.ruleForm.setupName = data.setupName;
                  this.ruleForm.setupTime = data.createDate;
                  this.ruleForm.createDate =  data.createDate;
                  this.ruleForm.creator = data.creator;
                  this.ruleForm.manager = data.manager;
                  this.ruleForm.managerName = data.managerName;
                  this.ruleForm.applyDept = data.applyDept;
                  this.ruleForm.applyDeptName = data.applyDeptName;
                  this.ruleForm.setupCategory = data.setupCategory;
                  this.ruleForm.reason = data.reason;    
                  this.ruleForm.remark = data.remark;
                  this.ruleForm.projectId = data.projectId;
                  this.ruleForm.id = data.id;
                  this.ruleForm.updateFileIds=[];
                  this.updateFileList=[];
                  for(var item of data.lzPmAttachmentPojoList){
                    this.ruleForm.updateFileIds.push(item.id);
                    this.updateFileList.push(item);
                  }
                  this.$set(this.ruleForm);
              }
            });
          }
        },
        // 提交
        SubmitProject(setupStatus,status){
          let that = this;
          this.$refs.ProjectForm.validate(function(ok){
            if(!ok) return;            
            that.ruleForm.setupStatus=setupStatus;
            that.ruleForm.status=status;
            if(status === 1){            
              that.$confirm('信息提交后将不能修改,确定要提交项目信息?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                that.saveProjectInfo(that);
              });
            }
            else{
              that.saveProjectInfo(that);
            }
          })
        },
        saveProjectInfo(that){
          let api= "/LzPmSetupController/add";
          if(this.ruleForm.id){
            api= "/LzPmSetupController/update";
          }
          that.$xhr.post(api,that.ruleForm).then((res)=>{
            if(res.code == "1"){
              that.$message({type: 'success',message: '操作成功!'});
              setTimeout(function(){
                // 不通过
                if(that.ruleForm.setupStatus=="3"){
                  that.$router.push('/project');
                }
                // 提交
                if(that.ruleForm.status=="1"){
                  that.$router.push('/project/setupManage?pid='+res.lzPmSetup.projectId);
                }
                // 保存
                if(that.ruleForm.status=="0"){
                  that.$router.push('/project/setupManage?pid='+res.lzPmSetup.projectId);
                }
              },2000)              
            }
          });
        },
        // 文档上传
        handleSuccess(res, file, list) {
          this.updateFileList.push(res.lzPmAttachmentPojo);
          this.ruleForm.updateFileIds=[];
          for(var item of this.updateFileList){
            this.ruleForm.updateFileIds.push(item.id);
          }
          this.$message({type: 'success',message: '上传成功!'});      
          this.$refs.uploadxxx.clearFiles();
        },       
        handleError() {
          console.log("上传文件失败");
        },
        showDialog(){
          this.userTarget='manager';
          this.orgUserList=[{name:'张三',userid:'adsf'},{name:'李四',userid:'asdfj'},{name:'ss',userid:'asdfj'}];
          this.dialogVisible=true;
        },
        selectManageUser(){
          this.orgUserList = this.$refs.comSelectUser.getSelectUserList();
          // 选择的是部门
          if(this.userTarget=='manager'){
            this.ruleForm.manager = this.orgUserList[0].userid;
            this.ruleForm.managerName = this.orgUserList[0].name;
            this.dialogVisible=false;
            return;
          }
          console.log(list);
        },
        selectManageUser22(obj){
          console.log(2222,obj);
        }
    },
    components: {
      baseInfo,
      projectStep,
      selectUser
    },
    watch:{
      $route(){
        this.init();
        this.projectId = this.$route.query.pid;
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
  .txt-c{
    text-align: center;
  }
  .create_porject{
    margin-top:10px;
    padding: 20px;
    background: #fff;
    font-size: 14px;
  }
  .el-select {
    width: 100%;
  }
  .el-date-editor.el-input, .el-date-editor.el-input__inner {
      width: 100%;
  }  
  .create_porject{
    .icon-xiazai2,.icon-jianhao{
      margin-left: 10px;
      display:inline-block;
      vertical-align: middle;
      cursor: pointer;
      color:#345DE7;
      font-size: 24px;
      &:hover{
        color: #409EFF;
      }
    }
  }
  .fileremark span{
    color: #ff7800;
    margin:0 5px;
  }
  .orgusertb{
    width:100%;
    border: 1px solid #E8EAF6;
    tr{
      height: 40px;
      text-align: center;
      .checkbox{
        background: #fff;
      }
      th{
        background: #F3F6FE;
        text-align: center;
        font-weight: bold;
        border: 1px solid #E8EAF6;
      }
      td{
        border: 1px solid #E8EAF6;
        a{
          color: #409EFF;
        }
      }

    }
  }
</style>



