<template>
  <div>
    <baseInfo :projectId='1'></baseInfo>
    
    <div class="warpper">
    	<Div class="tab">
    		<button class="iconfont icon-yingjian" :class="{active:tabName=='0'}" @click="handleClick('0')">数据铁笼软件采购</button>
    		<button class="iconfont icon-screen" :class="{active:tabName=='1'}" @click="handleClick('1')">数据铁笼硬件采购</button>    		
    	</Div>
  		<proProgress :projectId='1'></proProgress>
    
    		<div class="warpmain">
     		<div class="wapcont">
     			<div class="title-btn">
     					<div class="p_title">
				        <i class="iconfont icon-wendang1"></i><p>资金支付记录</p>		        	
				      </div>
				   		<button class="p_save" @click="centerDialogVisible = true,ruleForm={}">支付登记</button>    		 
     			</div>
     			
     				<el-dialog :visible.sync="centerDialogVisible"
     				center
     				@close="dialogClose"
     				style=""
     				>
     				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm"> 
     					<div class="p_title">
				        <i class="iconfont icon-write"></i><p>资金支付记录</p>		        	
				      </div>
     					<div class="dialig-body">
		     				<el-row>
								  <el-col :span="12">
								  	        <span class="lable">项目名称: </span>  数据铁笼
									</el-col>
									<el-col :span="12">
								  	        <span class="lable">项目编号: </span>  
								  	         <span class="widths">
								  	        	2011838647836
								  	        </span>
									</el-col>
								 			
								</el-row>
						
							<el-row>
								 <el-col :span="12">
			  	       		 <span class="lable">付款类型: </span>
			  	       		 <el-form-item prop="payType">
		  	       		 		<el-select v-model="ruleForm.payType" placeholder="请选择活动付款类型" class="p_select">
						            <el-option label="阶段付款计划" value="0"></el-option>
						            <el-option label="其他付款" value="1"></el-option>
						          </el-select>
						         </el-form-item>
						         
						          
						          
								 </el-col>							 
								<el-col :span="12">
						  	        <span class="lable">申请人: </span>
						  	        <el-form-item  prop="user">
											  	<el-input  class="p_text" v-model="ruleForm.user" value="ruleForm.user"></el-input>
											  </el-form-item>
											
			            <!--<el-date-picker class="p_date" v-model="ruleForm.time2" type="date" placeholder="选择日期" >
	                </el-date-picker> -->   
								</el-col>	
							</el-row>
							<el-row>
								 <el-col :span="12">
			  	       		 <span class="lable">申请金额: </span>
		  	       		   <el-form-item  prop="money">
												<el-input  class="p_text" v-model="ruleForm.money" value="ruleForm.money"></el-input>
										 </el-form-item>
								 </el-col>							 
								<el-col :span="12">
						  	   		<span class="lable">录入人: </span>
							  		  <span class="widths">
								  	        	刘邓是
								  	        </span>
								</el-col>	
							</el-row>
								<el-row>
								 <el-col :span="12">
			  	       		 <span class="lable">录入日期: </span>
		  	       			 	2017-11-23
								 </el-col>							 
								<el-col :span="12">
						  	   		<span class="lable">付款计划金额: </span>
							  			<span class="widths"> ¥3,000</span>
								</el-col>	
							</el-row>
							
							<el-row>
								 <el-col :span="12">
			  	       		 	 <span class="lable">阶段付款: </span>
			  	       			 <el-form-item prop="payStep">
			  	       		 		<el-select v-model="ruleForm.payStep" placeholder="请选择付款阶段" class="p_select">
							         		<el-option label="准备阶段" value="0"></el-option>
							            <el-option label="准备阶段1" value="1"></el-option>
							            <el-option label="准备阶段2" value="2"></el-option>
							            <el-option label="准备阶段3" value="3"></el-option>
							            <el-option label="准备阶段4" value="4"></el-option>
							            <el-option label="准备阶段5" value="5"></el-option>
							          </el-select>
							         </el-form-item>
			  	       		 	 
								 </el-col>							 
								<el-col :span="12">
						  	   		<span class="lable">支付时间: </span>
							  			<el-form-item prop="date1">
							  				<el-date-picker class="p_date" v-model="ruleForm.date1" type="date" placeholder="选择日期" >
               			 		</el-date-picker> 
               				</el-form-item>
								</el-col>	
							</el-row>
			 
						<el-row>
						  <el-col :span="24">
						  	        <span class="lable">支付流水号: </span>						
						  	       	<el-form-item prop="payNumber">
						  	       		<el-input  v-model="ruleForm.payNumber"  class="p_text"></el-input> 
						  	       	</el-form-item>
							</el-col>
							
						</el-row>
						<el-row>
						  <el-col :span="24">
						  	        <span class="lable">附件</span>
							  
						  	     <el-upload class="p_upload-demo" ref="uploadxx"  	
                         action="http://192.168.20.145:8080/aobo/file/upload" :limit="1" :show-file-list="true">
			                <el-input id="accessory" placeholder=""  class="text upload"></el-input>
			                <el-button size="small" type="primary">上传</el-button>
			              </el-upload>
										 
							</el-col>
							
						</el-row>
				 			<el-row>
							  <el-col :span="24">
						  	        <span class="lable">备注: </span>						
						  	        <el-form-item prop="desc">
						  	        	<el-input type="textarea" v-model="ruleForm.desc" :rows="4"  class="p_text"></el-input>
						  	        </el-form-item>
								</el-col>
								
							</el-row>
					  	</div>
					  	</el-form>
					  	<span slot="footer" class="dialog-footer">
						     <button type="submit" class="p_submit" @click="submitForm('ruleForm')" style="margin-right: 20px;"><i class="iconfont icon-submit"></i>提交</button>
					      	<!--保存按钮-->   
					     	 <button class="p_save"  @click="centerDialogVisible = false"><i class="iconfont icon-baocun"></i>保存</button>
					  	</span>
					 
					</el-dialog>
		       <table>        		
        			<tr>
        				<th>序号</th>
        				<th>录入日期</th>
        				<th>录入人</th>
        				<th>付款阶段</th>
        				<th>付款类型</th>
        				<th>付款金额</th>
        				<th>支付时间</th>
        				<th>操作</th>
        			</tr>
        	
        			<tr v-for="(item,index) in list.aleryPay">
        				<td>{{index+1}}</td>
        				<td>{{item.insertTime}}</td>
        				<td>{{item.user}}</td>
        				<td>{{item.name}}</td>
        				<td>{{item.payType}}</td>
        				<td>{{item.payMoney}}</td>
        				<td>{{item.payTime}}</td>
        				<td v-if="item.status==1"><button @click="edit(item.id)">编辑</button></td>
        				<td v-else><span>√已提交</span></td>
        			</tr>
        			 
        			
        		</table>
		   
     		</div>
    	</div>	
   
    </div>
  </div>
</template>

<script type="text/javascript">
import baseInfo from '../components/baseinfo'
import proProgress from '../components/progress'
import step from 'components/step'
  export default {
    name: 'information',
    data() {
      return {
        active:5,
        tabName:0,
       	fileList: [],
	      centerDialogVisible:false,
	      curStep:'11',
     		ruleForm:{
					user:'',
					payType:'0',
					payStep:'',
					money:'',
       		date1:'',
       		payNumber:'',
       		desc:''
       	},
      	rules: {
          user: [
            { required: true, message: '请输入申请人', trigger: 'blur' },
           /* { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }*/
          ],
        	payType: [
            { required: true, message: '请选择付款类型', trigger: 'change' }
          ], 
          money: [
            { required: true, message: '请输入申请金额', trigger: 'blur' },
           /* { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }*/
          ],
        	payStep: [
            { required: true, message: '请选择付款阶段', trigger: 'change' }
          ],
         	date1: [
            { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
          ], 
          payNumber: [
            { required: true, message: '请输入支付流水号', trigger: 'blur' },
           /* { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }*/
          ],
          /* date2: [
            { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
          ],
          type: [
            { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
          ],
          resource: [
            { required: true, message: '请选择活动资源', trigger: 'change' }
          ],*/
          desc: [
            { required: true, message: '请填写备注', trigger: 'blur' }
          ]
       },
				list:{
					
					aleryPay:[
						{id:'10086',insertTime:'2017-10-23',user:'张三',name:'付款阶段',payType:'阶段付款计划',payMoney:'8000',payTime:'2017-11-03 14:41:34',status:'1'},
						{id:'10010',insertTime:'2017-10-24',user:'李四',name:'付款阶段',payType:'阶段付款计划',payMoney:'8000',payTime:'2017-11-03 14:41:34',status:'0'},
						{id:'10000',insertTime:'2017-10-23',user:'张三',name:'付款阶段',payType:'阶段付款计划',payMoney:'8000',payTime:'2017-11-03 14:41:34',status:'1'}
					
					]
					 
					 
				}
      }
    },
    mounted() {
      console.log(this.$route.params)
    },
    methods: {
    	edit(id){
    		console.log(id)
    		this.centerDialogVisible = true,
    		this.ruleForm={
					user:'测试人',
					payType:'1',
					payStep:'2',
					money:'9999',
       		date1:'2017-10-11',
       		payNumber:'456789',
       		desc:'来编辑我啊啊啊啊啊啊啊啊啊'
       }
    	},
       	dialogClose(){
      		this.resetForm('ruleForm')
      	},
     	submitForm(formName) {
     		//centerDialogVisible = false
        this.$refs[formName].validate((valid) => {
          if (valid) {
           console.log(this.ruleForm)
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }, 
    	handleClick(tab) {
	        this.tabName = tab;
	    
     	 },  
			handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      }
    },
    components: {
      baseInfo,
      proProgress
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
  .my-tab-wrap{
  	margin:30px;
  	li{
  		width:88px;
  		height: 30px;
  		border: 1px solid #9899ad;
  		border-radius: 30px;
  		line-height: 30px;
  		text-align: center;
  		display: inline-block;
  		margin-right: 10px;
  		cursor:pointer;
	  	&.active{
	  		border-color: #635fc0;
	  		color: #635FC0;
	  	}
  	}
  }
  
  .warpper{

  	.tab{
  		margin-top:20px;
  		button{
  			width: 176px;
			height: 46px;
			background:#eaeefd;	 
			font-size: 16px;
			font-weight: normal;
			font-stretch: normal;
			line-height: 45px;
			letter-spacing: 0px;
			color: #456cf0;
			border: none;
			&:before{
					margin-right: 5px;
			}
		    display: inline-block;
		    margin-right: -4px;
		    border-bottom: 4px solid #eaeefd;
			&.active{
				background: #fff;
				border-bottom: 4px solid #345de7;
			}
  		}
  	}
  }

         		.warpmain{
 			 	margin-top: 20px;
 			 	background:#fff;
 			 	padding:40px; 	
 			 	padding-top: 0;	
 			 	overflow: hidden;	  
 			  .p_title{
 			  	margin-top: 20px;;
 			  	margin-bottom: 20px;
			  }
			  .title-btn{
			  	display: flex;
			  	justify-content: space-between;
			  	align-items: center;
			  	 button{
			  	 	padding: 0px 20px;
			  	 	height: 23px;
			  	 	font-size: 14px;
			  	 }
			  }
 			 	.wapcont{
 			 		margin-top: 20px;
 			 		
 				table{
	      	border-top: 1px solid #ccc;
	      	border-right:1px solid #ccc;
	      	width: 100%;
      	th{
      		color: #000;
      		font-weight: bold;
      		background: #f3f6fe;
      		height: 40px;
      	}
      	td,th{
      		border-left: 1px solid #ccc;
      		border-bottom:1px solid #ccc;
      		text-align: center;
      	}
      	td{
      		height: 40px;
      		padding-left: 10px;
      		padding-right: 10px;
      		&.red{
      			color: #ff2300;
      		}
      	 
      	 button{
	      	 	width: 80px;
						height: 28px;
						background-color: #345de7;
						border-radius: 14px;
						opacity: 0.75;
						color: #FFFFFF;
						text-align: center;
						line-height: 30px;
						outline: none;
						border: none;
      	 }
      	 span{
	      	 	color: #15bd68;
	      	 	font-size: 14px;
      	 }
      	 
      	 
      	}
      }
 			 	}
 		}
 		
 		
 		
 		  .dialig-body{
 		 
  		padding-left:20px;
  		padding-right: 20px;
  		.el-date-editor.el-input, .el-date-editor.el-input__inner,.p_select{
 			 		width: 240px;
 			 	}
 			 	.el-col-12{
 			 		 display: flex;
			   	 align-items: center;
			   
	 				&:nth-child(2n){
	 					justify-content: flex-end;
	 					span.widths{
	 					 
	 						display: inline-block;
	 						width: 240px;
	 					}
	 				}
 			 	}
 			 	.el-col-24{
 			 		display: flex;
 			 		justify-content: flex-start;
 			 		align-items: center;
 			 	}
 			 	.el-row{
 			 		margin-bottom: 20px;
	 		    display: flex;
			    justify-content: center;
			    flex-wrap: wrap;
			    align-items: center;
 			 	}
 			  
 			 	.el-input{
 			 			width: 240px;
 			 		}			 		
 			 		span{
			   	 	width: 100px;
			   	 	display: inline-block;
			   	 	text-align: left;
			   	 }
  }
</style>



