<template>
  <div>
    <div class="baseinfo">
      <div class="p_title">
        <i class="iconfont icon-wendang1"></i>
        <p>基本信息</p>
      </div>
      <div class="base-content">
        <el-row>
          <el-col :span="8">
            <span class="lable">项目名称: </span>
            <span class="txt">{{projectData.projectName}}</span>
          </el-col>
          <el-col :span="11">
            <span class="lable">立项申请单位: </span>
            <span class="txt">{{projectData.applyDeptName}}</span>
          </el-col>
          <el-col :span="5">
            <span class="lable">立项日期: </span>
            <span class="txt">{{projectData.setupDate| dataFromat}}</span>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <span class="lable">立项负责人: </span>
            <span class="txt">{{projectData.managerName}}</span>
          </el-col>
          <el-col :span="11">
            <span class="lable">立项类别: </span>
            <span class="txt">{{projectData.setupCategory==1?"专项资金":"部门资金"}}</span>
          </el-col>
          <el-col :span="5">
            <span class="lable">当前阶段: </span>
            <span class="txt">{{projectData.stageName}}</span>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
export default {
  name: 'project-baseinfo',
  props: {
    projectId: {
      default: ''
    }
  },
  data() {
    return {
      projectData:{
        projectName:'',
        applyDeptName:'',
        setupDate:'',
        managerName:'',
        setupCategory:'',
        stageName:'',
      }
    }
  },
  mounted() {
    if(this.projectId !=0){
      this.init();
    }    
  },
  filters:{
    dataFromat:function(val){
      return  new Date(val).Format("yyyy-MM-dd");
    }
  },
  methods: {
    init() {
      if (this.projectId && this.projectId != 0) {
        this.$xhr.get("/LzPmProjectController/info?projectId="+this.projectId).then((res)=>{
          if(res.lzPmProject){
              this.projectData.projectName=res.lzPmProject.projectName;
              this.projectData.applyDeptName=res.lzPmProject.lzPmSetupPojo.applyDeptName;
              this.projectData.setupDate=res.lzPmProject.lzPmSetupPojo.setupTime;
              this.projectData.managerName=res.lzPmProject.lzPmSetupPojo.managerName;
              this.projectData.setupCategory=res.lzPmProject.lzPmSetupPojo.setupCategory;
              this.projectData.stageName=res.lzPmProject.stageName;
          }
        });
      }
    }
  },
  components: {

  },
  watch: {
    projectId: function() {
      if(this.projectId !=0){
        this.init();
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "~assets/css/variable";
@import "~assets/css/mixin";
.baseinfo {
  height: 150px;
  padding: 20px;
  background: #fff;
  box-sizing: border-box;
  font-size: 14px;
  .base-content {
    padding: 0 20px;
  }
  .el-row {
    line-height: 30px;
  }
  .p_title {
    margin-bottom: 20px;
  }
  .lable {
    display: inline-block;
    width: 100px;
    color: #777777;
  }
  .txt {
    display: inline-block;
    color: #303030;
  }
}
</style>



