<template>
  <div>
    <div class="header">
        <div class="line1"></div>
        <div class="line2"></div>
       <div class="content">
         <img class="header-img" src='static/img/home001.jpg'>
         <projectStep class="projectStep" :stepType="projectStepType" :curStep="curStep"></projectStep>
       </div>
    </div>
    <div class="content-wrap">
      <div class="tab-wrap">
        <router-link to='/project/setupManage'>
          <button class="p_save fr"><i class="iconfont icon-jiahao"></i>立项</button>
        </router-link>
        <ul class="tab">
          <li @click="curPage='projectList'" :class="{active:this.curPage=='projectList'}"><span class='iconfont icon-icon02'></span> 进行中</li>
          <li @click="curPage='projectListEnd'" :class="{active:this.curPage=='projectListEnd'}"><span class='iconfont icon-completed'></span> 已完成</li>
        </ul>
      </div>
      <div class="component">
        <component :is="curPage" keep-alive></component>  
      </div>
    </div>
  </div>

</template>

<script type="text/javascript">
  import projectStep from './components/projectStep.vue'
  import projectList from './pages/projectList.vue'
  import projectListEnd from './pages/projectListEnd.vue'
  
  export default {
    name: 'assetManage-wrap',
    data() {
      return {
        curPage:'projectList',
        projectStepType:3,
        curStep:'3',
        searchicon: 'el-icon-arrow-left',
      }
    },
    mounted() {
    },
    methods: {
      changeTab(page){
          this.curPage= page;
      },
    },
    components: {
      projectStep,
      projectList,
      projectListEnd,
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";
  
  .header{
    margin-top: 30px;
    .line1{
      height: 7px;
      margin: 0 24px;
      background: #e2e9fb;
    }
    .line2{
      height: 7px;
      margin: 0 14px;
      background: #d1daf2;
    }
    .content{
      height: 163px;
      background: #fff;
      img{
        float: left;
        width: 300px;
        height: 163px;
        border: none;
      }
      .projectStep{
        float: left;
        width: 900px;
        box-sizing: border-box;
      }
    }
  }
  .content-wrap{
    margin-top: 30px;
    .tab-wrap{
      .p_save{
        padding: 15px;
        margin-top: 10px;
        .iconfont{font-size: 14px;}
      }
    }
    .tab{
      overflow: hidden;
      li{
        float: left;
        padding: 0 35px;
        background:rgba(52,93,231,0.1);
        color: #476eeb;
        height:46px;
        line-height: 46px;
        font-weight: 600;
        font-size: 16px;
        cursor: pointer;
        box-sizing: border-box;
        border-bottom: 4px solid transparent;
        &.active{
          background: #fff;
          border-bottom: 4px solid #345de7;
        }
      }
    }
  }
  .component{
    background: #fff;
    padding: 20px;
  }

  .tableop
  {
    background-color: #ffffff;
    color: #476eeb;
    border: 0px;
  }
  .tableop1
  {
    background-color: #f3f6fe;
    color: #476eeb;
    border: 1px solid #f3f6fe;
  }
  .tableoplxbut
  {
    padding-top: 8px;
    line-height: 0px;
    height:30px;
    border-radius: 50px;
  }
  .create_porject{
    margin-top:10px;
    padding: 20px;
    background: #fff;
    font-size: 14px;
  }
  .serchtile
  {
    height: 40px;
    color: #476eeb;
    border-bottom:1px solid #e7ecff;
    text-align: right;
    cursor: pointer;
    font-weight: 800;
  }
  .serchbutton:hover
  {
    background-color: #e7ecff;
  }
  .serch-serchbut
  {
    padding-top: 8px;
    line-height: 0px;
    height:30px;
    border-radius: 50px;
    background-color: #ff6400;
    border: 0px;
  }
  .serch-serchreturn
  {
    padding-top: 8px;
    line-height: 0px;
    height:30px;
    border-radius: 50px;
    border: 0px;
  }
//隐藏表格中的展开按钮
  .hide-expand .el-table__expand-column .el-icon {
    visibility: hidden;
  }
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
  .el-form p-l el-form--label-left {
    padding: 0px;
  }
  .expand-enter-active, .expand-leave-active {
    transition: opacity .5s
  }
  .expand-enter, .expand-leave-to {
    opacity:0;
  }
</style>



