<template>
  <div class="statement-wrap">
    <div class="header-wrap">
      <div class="basetitle">
        <span class="base-title-icon"></span>
        <span class="base-title">项目信息</span>
        <router-link v-if="this.$route.name != 'statistics'" class="p_return sm fr" tag="button" :to="{path:'/statistics'}"><i class="iconfont icon-zhongzhi"></i>返回</router-link>
      </div>
      <div class="header-container">
        <div class="header-left">
          <div class="p_title">
            <p class="title-left">累计项目</p>
          </div>
          <el-select class="p_select header-month" v-model="month1" placeholder="全部" @change="loadleftporject">
            <el-option v-for="(value,key,index) in sectionData2" :value="value"></el-option>
          </el-select>
          <el-select class="p_select header-years"  v-model="years1" @change="loadleftporject">
            <el-option v-for="(value,key,index) in sectionData1" :value="value"></el-option>
          </el-select>
          <div class="cricle-box" @click="gotourl">
            <span class="cricle cricle-left">{{monthaddlist.project}}</span>
            <p class="cricle-text">项目</p>
          </div>
        </div>
        <div class="header-right">
          <div class="p_title">
            <p class="title-left">本月新增</p>
          </div>
          <el-select class="p_select header-month" v-model="month2" placeholder="全部" @change="loadrightporject">
            <el-option v-for="(value,key,index) in sectionData2" :value="value"></el-option>
          </el-select>
          <el-select class="p_select header-years" v-model="years2" @change="loadrightporject">
            <el-option v-for="(value,key,index) in sectionData1" :value="value"></el-option>
          </el-select>
          <ul class="add-list">
            <li class="add-pact">
              <div class="num-box"><span class="add-pact-num">{{monthaddlist.Newproject}}</span>个</div>
              <div class="text-box" @click="pagetab('2')">新增项目</div>
            </li>
            <li class="add-item">
              <div class="num-box"><span class="add-item-num">{{monthaddlist.Conductcontract}}</span>份</div>
              <div class="text-box" @click="pagetab('3')">进行中合同</div>
            </li>
            <li class="add-pact">
              <div class="num-box"><span class="add-sum-num">{{monthaddlist.paymentoverdue}}</span>份</div>
              <div class="text-box" @click="pagetab('4')">支付逾期</div>
            </li>
            <li class="add-item">
              <div class="num-box"><span class="progress-num">{{monthaddlist.progressoverdue}}</span>份</div>
              <div class="text-box" @click="pagetab('5')">进度逾期</div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script type="text/javascript">
  import step from 'components/step.vue'
  import * as Api from '../../api/api.js'
  export default {
    data() {
      return {
        years1:'2017',
        years2:'2017',
        month1:'全部',
        month2:'全部',
        sectionData1: [
        ],
        sectionData2:[
        ],
        ruleForm: {
          region: '',
        },
        monthaddlist:{
          project:'0',
          Newproject:'0',
          Conductcontract:'0',
          paymentoverdue:'0',
          progressoverdue:'0',
        }
      }
    },
    mounted() {
      this.getYearAndmonth();
      this.setmonth();
      this.loadleftporject();
      this.loadrightporject();
    },
    wacth:{
      years1:{
        hander:function (val,oldval) {
          this.setmonth();
        },
        deep:true
      }
    },
    methods: {
      pageload()
      {

      },
      //加载年下拉框
      getYearAndmonth() {
        this.$xhr.get(`/api/lzPmStatis/getYears`)
          .then((res) => {
            this.sectionData1=res.data;
          })
          .catch((req)=>{
            console.log(req)

          })
      },
      //得到月下拉框
      setmonth()
      {
        this.$xhr.get(`/api/lzPmStatis/getMonthsOfYear`,{
          params:{
            year:this.years1,//合同编号
          }
        })
          .then((res) => {
            this.sectionData2=res.data;
            this.sectionData2.push("全部");
          })
          .catch((req)=>{
            console.log(req)
          })
      },
      //初始化左边项目个数
      loadleftporject()
      {
        if(this.month1=="全部")
        {
          this.month1="";
        }
        else {
          var reg = /[\u4E00-\u9FA5]/g;
          this.month1 = this.month1.replace(reg, '');
        }
        this.$xhr.get(`/api/lzPmStatis/getProjectStatis`,{
          params:{
            year:this.years1,
            month:this.month1,
          }
        })
          .then((res) => {
            this.monthaddlist.project=res.data;
          })
          .catch((req)=>{
            console.log(req)
          })
      },
      //初始化右边的项目
      loadrightporject() {
        if (this.month2 == "全部") {
          this.month2 = "";
        }
        else {

          var reg = /[\u4E00-\u9FA5]/g;
          this.month2 = this.month2.replace(reg, '');
        }
        this.$xhr.get(`/api/lzPmStatis/getContractStatis`,{
          params:{
            year:this.years2,
            month:this.month2,
          }
        })
          .then((res) => {
            this.monthaddlist.Newproject=res.data.endContractTotal;
            this.monthaddlist.Conductcontract=res.data.payOverTotal;
            this.monthaddlist.paymentoverdue=res.data.planOvertotal;
            this.monthaddlist.progressoverdue=res.data.runContractTotal;
          })
          .catch((req)=>{
            console.log(req)
          })
      },
      gotourl(){
        this.$router.push({path:'/statistics/project',query:{year:this.years1,month:this.month1}});
      },
      pagetab(tab)
      {
        if(tab=="1")
        {
          this.$router.push({path:'/statistics/project',query:{year:this.years1,month:this.month1}});
        }
        else if(tab=="2")
        {
          this.$router.push({path:'/statistics/addProject',query:{year:this.years2,month:this.month2}});
        }
        else if(tab=="3")
        {
          this.$router.push({path:'/statistics/underwayPact',query:{year:this.years2,month:this.month2}});
        }
        else if(tab=="4")
        {
          this.$router.push({path:'/statistics/payOverdue',query:{year:this.years2,month:this.month2}});
        }
        else if(tab=="5")
        {
          this.$router.push({path:'/statistics/progressOverdue',query:{year:this.years2,month:this.month2}});
        }
      }
    },
    components: {
    }
  }
</script>

<style lang="scss" scoped>
  @import "./../../assets/css/variable";
  @import "./../../assets/css/mixin";
  @import "./../../assets/css/diy-elementui";
  .statement-wrap {
    width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    .header-wrap {
      .header-title {
        padding: 0 0 20px 0;
        font-size: 16px;
        color: #3653b3;
      }
      .header-container {
        font-size: 0;
        box-sizing: border-box;
        .header-left {
          display: inline-block;
          position: relative;
          width: 280px;
          height: 253px;
          padding: 30px 20px;
          margin-right: 12px;
          background: #fff;
          box-sizing: border-box;
          vertical-align: top;
          font-size: 14px;
          .p_title {
            width: 100%;
            padding-left: 10px;
            margin-bottom: 40px;
          }
          .header-month {
            position: absolute;
            top: 30px;
            right: -30px;
            /*width: 20%;*/
          }
          .header-years {
            position: absolute;
            top: 30px;
            right: 50px;
            /*width: 20%;*/
          }
          .cricle-box {
            width: 100px;
            height: 100px;
            text-align: center;
            margin: 0 auto;
            .cricle {
              display: inline-block;
              width: 100px;
              height: 100px;
              line-height: 100px;
              font-size: 32px;
              color: #fff;
              margin-bottom: 10px;
              &.cricle-left {
                background: url("../../assets/img/cricle-02.jpg") no-repeat;
              }
              &:hover {
                cursor: pointer;
              }
            }
          }
        }
        .header-right {
          display: inline-block;
          position: relative;
          width: 908px;
          height: 253px;
          padding: 30px 20px;
          background: #fff;
          box-sizing: border-box;
          vertical-align: top;
          font-size: 14px;
          .p_title {
            width: 100%;
            padding-left: 10px;
            margin-bottom: 25px;
          }
          .p-select {
            /*min-width: 117px !important;*/
          }
          .header-month {
            position: absolute;
            top: 30px;
            right: -20px;
            /*width: 100px;*/
          }
          .header-years {
            position: absolute;
            top: 30px;
            right: 80px;
            /*width: 100px;*/
          }
          .add-list {
            display: flex;
            justify-content: space-between;
            padding: 0 20px;
            li {
              width: 160px;
              height: 150px;
              background-color: #f5f8fd;
              border-radius: 6px;
              padding: 0 15px;
              box-sizing: border-box;
              text-align: center;
              .num-box {
                height: 50%;
                line-height: 75px;
                border-bottom: 1px solid #e1e7fa;
                span {
                  font-size: 36px;
                  display: inline-block;
                  -webkit-background-clip: text;
                  -webkit-text-fill-color: transparent;
                  font-weight: 600;
                  &.add-item-num {
                    color: #2a5afa;
                    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#2abdfa), to(#5b9dff));
                  }
                  &.add-pact-num {
                    color: #ff9100;
                    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#ffca48), to(#ff9100));
                  }
                  &.add-sum-num {
                    color: green;
                    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(rgba(48,189,26,0.99)), to(rgba(0,160,0,0.99)));
                  }
                  &.progress-num {
                    color: #2a5afa;
                    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#ffca48), to(#ff9100));
                  }
                }
              }
              .text-box {
                line-height: 75px;
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }
</style>



