<template>
  <div class="userAsset-wrap">
    <div class="p_title">
      <i class="iconfont icon-zijin2"></i>
      <p>资金使用情况统计</p>
    </div>
    <div class="topLeft">
      <topLeft :data="topleftData" v-if="Object.keys(topleftData).length"></topLeft>
    </div>
    <div class="topRight">
      <div class="select">
        <el-select v-model="year" placeholder="2017" class="p_select small">
          <el-option v-for="(val,index) in yearData" :key="val.id" :value="val.label"></el-option>
        </el-select>
        <el-select v-model="month" placeholder="全年" class="p_select small">
          <el-option v-for="(val,index4) in monthData" :key="val.id" :value="val.label"></el-option>
        </el-select>
      </div>
      <topRight :data="toprightData" v-if="Object.keys(topleftData).length"></topRight>
    </div>
  </div>
</template>
<script type="text/javascript">
  import topLeft from '../echarts/topLeft.vue'
  import topRight from '../echarts/topRight.vue'

  export default {
    name: '',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        year: '2017',
        month: '全年',
        yearData: [
          { label: "2017", value: "2017", id: 2017 },
          { label: "2016", value: "2016", id: 2016 },
          { label: "2015", value: "2015", id: 2015 }
        ],
        monthData: [
          { label: "全部", value: "0", id: 0 },
          { label: "1月", value: "1", id: 1 },
          { label: "2月", value: "2", id: 2 },
          { label: "3月", value: "3", id: 3 },
          { label: "4月", value: "4", id: 5 },
          { label: "5月", value: "5", id: 5 },
          { label: "6月", value: "6", id: 6 }
        ],
        topleftData: {
          data1: [],
          data2: [],
          data3: [],
          data4: []
        },
        toprightData: {
          data: []
        }
      }
    },
    mounted() {
      this.getTopLeftData();
      this.getToprightData();
    },
    methods: {
      getTopLeftData() {
          this.topleftData.data1 = ['一月', "二月", "三月", "四月", "五月", "六月", "七月", "八月"];
          this.topleftData.data2 = [200, 300, 100, 330, 350, 450, 660, 700];
          this.topleftData.data3 = [500, 100, 400, 300, 600, 800, 1000, 1200];
          this.topleftData.data4 = [600, 800, 1200, 1500, 1200, 1450, 1600, 1300];
      },
      getToprightData() {
        this.toprightData.data = [{ value: 300, name: '专项资金额度' }, { value: 50, name: '部门资金额度' }];
      }
    },
    components: {
      topLeft,
      topRight
    }
  }

</script>
<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";
  .userAsset-wrap {
    padding: 20px;
    font-size: 0;
    .p_title {
      font-size: 14px;
      margin-bottom: 20px;
    }
    .topLeft {
      display: inline-block;
      width: 65%;
      vertical-align: top;
      box-sizing: border-box;
    }
    .topRight {
      display: inline-block;
      width: 35%;
      /*height: 400px;*/
      box-sizing: border-box;
      padding: 0 20px;
      .select {
        text-align: right;
        .el-select {
          margin: 0 5px;
        }
      }
    }
  }

</style>



