<template>
  <div class="body-wrap">
    <div class="basetitle">
      <span class="base-title-icon"></span>
      <span class="base-title">报表</span>
    </div>
    <div class="user-asset">
      <userAsset></userAsset>
    </div>
    <div class="inExecution">
      <inExecution></inExecution>
    </div>
    <div class="finished">
      <finished></finished>
    </div>
  </div>
</template>

<script type="text/javascript">
  import userAsset from './userAsset.vue'
  import inExecution from './inExecution.vue'
  import finished from './finished.vue'
  export default {
    name: '',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {}
    },
    mounted() {
    },
    methods: {},
    components: {
      userAsset,
      inExecution,
      finished
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";
  @import "../../../assets/css/diy-elementui";
  .body-wrap {
    .user-asset, .inExecution, .finished {
      /*height: 500px;*/
      background: #fff;
      margin-bottom: 30px;
    }
  }
</style>



