<template>
  <div class="asset-main">
    <div class="asset-wrap">
      <div class="basetitle">
        <span class="base-title-icon"></span>
        <span class="base-title">资产管理</span>
        <router-link class="register sm fr" tag="button" :to="{path:'asset/add'}"><i class="iconfont icon-jiahao"></i>&nbsp;资产登记
        </router-link>
      </div>
      <div class="select-wrap">
        <form action="">
          <ul class="select-box">
            <li v-if="!this.$route.params.id">
              <label>合同编号：</label>
              <el-input class="p_text" id="contractCode" v-model="queryparameter.contractCode" placeholder="请输入编号"></el-input>
            </li>
            <li>
              <label>资产名称：</label>
              <el-input class="p_text" id="assetName" v-model="queryparameter.assetName" placeholder="请输入项目名称"></el-input>
            </li>
            <li>
              <label>使用状态：</label>
              <el-select v-model="queryparameter.useStatusData" placeholder="全部" class="p_select big">
                <el-option v-for="(val1,index1) in useStatusData" :key="val1.id" :value="val1.label"></el-option>
              </el-select>
            </li>
            <li class="entering-data">
              <label>录入日期：</label>
              <el-date-picker
                v-model="queryparameter.startTime"
                type="date"
                class="p_date"
                placeholder="选择日期">
              </el-date-picker>
              -
              <el-date-picker
                v-model="queryparameter.endTime"
                type="date"
                class="p_date"
                placeholder="选择日期">
              </el-date-picker>
            </li>
            <li class="status" v-if="!this.$route.params.id">
              <label>状态：</label>
              <el-select v-model="queryparameter.status" placeholder="全部" class="p_select big">
                <el-option v-for="(val2, index) in statusData" :key="val2.id" :value="val2.label"></el-option>
              </el-select>
            </li>
          </ul>
          <div class="btn-box">
            <button class="p_submit" @click="source()"><i class="iconfont icon-sousuo"></i>查询</button>
            <button type="reset" class="p_return"><i class="iconfont icon-shuaxin"></i>重置</button>
          </div>
        </form>
      </div>
      <div class="pact-list">
        <div class="btn-batch">
          <button class="batch-delete" @click="batchRemove()"><i class="iconfont icon-shanchu"></i>批量删除</button>
          <el-upload class="upload-demo small" ref="uploadxxx" :headers="myheaders" action="http://192.168.20.145:8080/aobo/file/upload" :on-success="handleSuccess" :on-error="handleError" :limit="1" :disabled="fileList.length>=3" :show-file-list="true">
            <el-button class="batch-import" size="small" type="primary"><i class="iconfont icon-daoru"></i>批量导入</el-button>
          </el-upload>

        </div>
        <table class="pact-table" border="1">
          <thead>
          <tr>
            <th><input @click="selectAll()" :checked="check" type="checkbox" name="" value=""/></th>
            <th>序号</th>
            <th>合同编号</th>
            <th>资产名</th>
            <th>资产分类</th>
            <th>使用状态</th>
            <th>录入日期</th>
            <th>状态</th>
            <th>操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(val, index) in listData" :key="index">
            <td><input @click="select(index)" type="checkbox" :checked="val.check" name="" value=""/></td>
            <td>{{index+1}}</td>
            <td>{{val.contractCode}}</td>
            <router-link class="propertyName" :to="{path:'asset/assetDetail'}" tag="td">{{val.assetName}}
            </router-link>
            <td>{{val.assetTypeId}}</td>
            <td>{{val.useStatus}}</td>
            <td>{{val.getDate}}</td>
            <td style="color:#15BD68" :class="{state0: val.status == '已保存'}"><i class="icon iconfont icon-duigou"></i>{{val.status}}</td>
            <td>
              <button @click="remove(val.id)" class="p_return remove sm " type="button" >删除</button>
              <button @click="update(val.id)" class="p_return update sm " type="button" >编辑</button>
              <!--<button v-if="val.status == '已保存'" class="p-edit" type="button" >编辑</button>-->
            </td>
          </tr>
          </tbody>
        </table>
        <!--分页-->
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="curPageIndex"
          layout="total, prev, pager, next, jumper"
          :total="totalCount" :page-size="pageSize">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">

  export default {
    name: 'asset-main',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        useStatus: '全部',
        useStatusData: [
          {label:"全部"},
          {label:"在用",value:"2",id:2},
          {label:"未用",value:"3",id:3},
        ],
        date0: '',
        date1: '',
        status: '全部',
        statusData: [
          {label:"全部"},
          {label:"保存",value:"0",id:"0"},
          {label:"提交",value:"1",id:"1"},
        ],
        fileList: ['111.txt'],
        myheaders: {
          'Authorization': 'bearer ce6e1d40-495d-4c7d-a870-dd9d384b5058',
        },
        propertyName: '',
        delivery: false,
        type: [],
        resource: '',
        desc: '',
        key0:'',
        key1:'',
        key2:'',
        check: false,
        i: 0,
        detelelist:[],
        listData: [],
        //查询参数
        queryparameter:{
          contractCode:'',//合同编号
          assetName:'',//资产名称
          useStatusData:'',//使用状态
          status:'',//状态
          startTime:'',//开始日期
          endTime:''//结束日期
        },
        curPageIndex:1,
        pageSize:5,
        totalCount:1,
        tableData: []
      }
    },
    watch: {
      tableData: {
        handler: function (val, oldVal) {
          this.handleCurrentChange(this.curPageIndex)
//      console.log(val)
        },
        deep: true
      }
    },
    mounted() {
      //console.log(this.$route.params.id); //获取当前页路由地址
      this.getData();
      this.handleCurrentChange(this.curPageIndex)
    },
    methods: {
      getData(){
        console.log(this.queryparameter);
        this.$xhr.get(`/LzPmAssetController/list`,{
          params:{
            contractCode:this.queryparameter.contractCode,//合同编号
            assetName:this.queryparameter.assetName,//资产名称
            useStatusData:this.queryparameter.useStatusData,//使用状态
            status:this.queryparameter.status,//状态
            startTime:this.queryparameter.startTime,//开始日期
            endTime:this.queryparameter.endTime,//结束日期
            page:1,
            limit:10
          }
        })
          .then((res) => {
            this.tableData=res.page.list;
            this.totalCount = res.page.totalCount
            this.pageSize = res.page.pageSize
            this.totalCount = res.page.totalCount
        	console.log(res)
          })
          .catch((req)=>{
            console.log(res)
          })

      },
      //上传
      handleSuccess(res, file, list) {
        console.log(res, file, list);
        this.fileList.push(res.content.originalFilename);
        this.$refs.uploadxxx.clearFiles();
      },
      handleError() {
        console.log(444);
      },
      //搜索
      source(){
        this.getData();
      },
      //全选
      selectAll() {
        this.check = !this.check;
        this.tableData.forEach((val, index, arr) => {
          val.check = this.check
        })
      },
      //单选
      select(i) {

        this.tableData[i].check = !this.tableData[i].check;
        let item = 0;
        this.tableData.forEach((val, index, arr) => {
          if (val.check == true) {
            item++;
          }
          if (item == this.tableData.length) {
            this.check = true
          } else {
            this.check = false
          }
        })
      },
      //单个删除
      remove(i) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$xhr.delete(`/LzPmAssetController/delete?ids=${i}`)
            .then((res)=>{
              this.getData();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            })
            .catch((req)=>{
              console.log(res)
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //修改
      update(i)
      {
        this.$router.push({path:'asset/add',query:{id:i}});
      },
      //批量删除
      batchRemove() {
        this.detelelist = [];
        this.listData.forEach((val, index, arr) => {
          if (val.check == true) {
            this.detelelist.push(this.listData[index].id)
          }
        })

        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$xhr.delete(`/LzPmAssetController/delete?ids=${i}`)
            .then((res)=>{
              this.getData();
              this.$xhr.delete(`/LzPmAssetController/delete?ids=${this.detelelist.toString()}`)
                .then((res) => {
                  this.getData();
                  this.$message({
                    type: 'success',
                    message: '删除成功!'
                })
                .catch((req) => {
                  console.log(res)
                })
              });
            })
            .catch((req)=>{
              console.log(res)
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //分页
      handleCurrentChange(val) {
        this.curPageIndex = val
      }
    },
    computed: {
      filterList:function(){

        let tableData = this.listData;
        return tableData.filter((val,index,arr)=>{
          if(this.key0 != ""){
            return val.propertyName.indexOf(this.key0) != -1
          }
          if(this.key1 != ""){
            return val.date.indexOf(this.key1) != -1
          }
          if(this.key2 != ""){
            return val.region.indexOf(this.key2) != -1
          }
          else{
            return arr
          }
        })
      }
    },
    components: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";

  .asset-main {
    .asset-wrap {
      width: 100%;
      overflow: hidden;
      .basetitle {
        .register {
          width: 110px;
          height: 28px;
          line-height: 28px;
          background-color: #345de7;
          border-radius: 14px;
          color: #fff;
          border: 0;
          outline: none;
          &:hover {
            background: #457de7;
          }
          .icon-jiahao {
            font-size: 14px;
          }
        }
      }
      .select-wrap {
        /*height: 126px;*/
        padding: 30px 20px 20px 20px;
        background: #fff;
        box-sizing: border-box;
        .select-box {
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          li {
            display: flex;
            justify-content: space-between;
            width: 28%;
            /*border: 1px solid red;*/
            margin-bottom: 15px;
            &.entering-data {
              width: 32%;
              .p_date{
                width: 36%;
                .el-input__suffix{
                  right:22px;
                  .el-input__icon{
                    line-height: 12px;
                  }
                }
              }
            }
            &.status {
              margin-right: 36%;
            }
            label {
              line-height: 28px;
            }
            .p_text {
              width: 75%;
            }
            .p_select {
              width: 75%;
            }
          }
        }
        .btn-box {
          margin: 0 auto;
          text-align: right;
          button {
            margin: 0 10px;
            box-sizing: border-box;
          }
          .p_submit {
            min-width: 80px;
            padding: 0 18px;
          }
        }
      }
      .pact-list {
        padding: 20px 20px 0 20px;
        background: #fff;
        margin-top: 12px;
        .btn-batch {
          .batch-delete {
            min-width: 104px;
            height: 28px;
            line-height: 20px;
            background: rgba(255, 100, 0, .1);
            color: #ff6400;
            font-size: 14px;
            border-radius: 13px;
            border: solid 1px #ff6400;
            margin-right: 10px;
            i {
              vertical-align: top;
              margin-right: 3px;
              font-size: 18px;
            }
            &:hover {
              background: rgba(255, 100, 0, .2);
            }
          }
          .batch-import {
            min-width: 104px;
            height: 28px;
            background: rgba(52, 93, 231, .1);
            color: #345DE7;
            font-size: 14px;
            border-radius: 13px;
            border: solid 1px #345DE7;
            i {
              vertical-align: top;
              margin-right: 3px;
              font-size: 14px;
            }
            &:hover {
              background: rgba(52, 93, 231, .2);
            }
          }
        }
        .pact-table {
          width: 100%;
          margin-top: 15px;
          border: 1px solid #E7ECFF;
          thead {
            th {
              height: 42px;
              background-color: #F3F6FE;
              color: #333333;
              font-weight: bold;
              text-align: center;
              border: 1px solid #E7ECFF;
            }
          }
          tbody {
            td {
              height: 42px;
              text-align: center;
              border: 1px solid #E7ECFF;
              &.propertyName {
                color: #345DE7;
                &:hover {
                  cursor: pointer;
                  color: #345DE7;
                }
              }
              &.state0{
                color: #345DE7 !important;
              }
              &.state1{
                color: #15BD68 !important;
              }
              .pact-del {
                width: 80px;
                height: 28px;
                border-radius: 14px;
                background: #fff;
                border: solid 1px #fa4a21;
                color: #fa4a21;
                &:hover {
                  background: rgba(255, 100, 0, .1);
                }
              }
              .p-edit {
                width: 80px;
                height: 28px;
                border-radius: 14px;
                background: #345DE7;
                color: #fff;
                border: 0;
                outline: none;
                &:hover {
                  background: #337DE7;
                }
              }
            }
          }
        }
      }
    }
  }
</style>



