<script type="text/javascript" scoped>
  import step from 'components/step.vue'
  import leftBar from 'components/leftBar/leftBar'
  import headerWrap from 'components/header/header'
  import footerWrap from 'components/footer'
  import * as Api from '../../api/api.js'

  export default {
    name: 'home',
    props: {},
    data() {
      return {
        testData: [],
        tableData: [],
        // fileList 是已上传的文件列表
        fileList: ['111.txt'],
        myheaders: {
          'Authorization': 'bearer ce6e1d40-495d-4c7d-a870-dd9d384b5058',
        },
        aa: 11,
        time: new Date().toLocaleDateString().replace(/\//g, "-"),
        ruleForm: {
          name: '',
          region: '',
          time0: '',
          date0: '',
          date2: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        rules: {
          name: [
            {required: true, message: '请输入活动名称', trigger: 'blur'},
            {min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur'}
          ],
          region: [
            {required: true, message: '请选择活动区域', trigger: 'change'}
          ],
          date1: [
            {type: 'date', required: true, message: '请选择日期', trigger: 'change'}
          ],
          date2: [
            {type: 'date', required: true, message: '请选择时间', trigger: 'change'}
          ],
          type: [
            {type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change'}
          ],
          resource: [
            {required: true, message: '请选择活动资源', trigger: 'change'}
          ],
          desc: [
            {required: true, message: '请填写活动形式', trigger: 'blur'}
          ]
        },
        curPageIndex: 5,
      }
    },
    mounted() {
      let data = [{
        equipmentName: "1",
        doCode: 'a',
        materialCode: '2',
        productionNum: '我',
        status: '使用',
        pNum: '20'
      },
        {
          equipmentName: "1",
          doCode: 'a',
          materialCode: '2',
          productionNum: '你',
          status: '使用',
          pNum: '30'
        },
        {
          equipmentName: "1",
          doCode: 'b',
          materialCode: '2',
          productionNum: '你',
          status: '使用',
          pNum: '40'
        },
        {
          equipmentName: "1",
          doCode: 'b',
          materialCode: '2',
          productionNum: '你',
          status: '未使用',
          pNum: '80'
        },
        {
          equipmentName: "1",
          doCode: 'c',
          materialCode: '2',
          productionNum: '你',
          status: '未使用',
          pNum: '80'
        },
        {
          equipmentName: "1",
          doCode: 'c',
          materialCode: '2',
          productionNum: '你',
          status: '未使用',
          pNum: '80'
        }
      ]

      this.testData = this.combineCell(data)
    },
    computed: {},
    methods: {
      combineCell(testData) {
        console.log(testData[0])
        for (var field in testData[0]) {
          var k = 0;
          while (k < testData.length) {
            testData[k][field + 'span'] = 1;
            testData[k][field + 'dis'] = false;
            for (var i = k + 1; i <= testData.length - 1; i++) {
              if (testData[k][field] == testData[i][field] && testData[k][field] != '') {
                testData[k][field + 'span']++;
                testData[k][field + 'dis'] = false;
                testData[i][field + 'span'] = 1;
                testData[i][field + 'dis'] = true;
              } else {
                break;
              }
            }
            k = i;
          }
        }
        console.log(testData)
        return testData;
      },

      mergeCell() {
        this.tableData = [{f1: "a", f2: "1", f3: "2", f4: "2"}, {f1: "a", f2: "1", f3: "2", f4: "3"}, {
          f1: "a",
          f2: "2",
          f3: "2",
          f4: "3"
        }, {f1: "a", f2: "2", f3: "3", f4: "3"}];

        function mc(tableId, startRow, endRow, col) {
          var tb = document.getElementById(tableId);
          if (col >= tb.rows[0].cells.length) {
            return;
          }
          if (col == 0) {
            endRow = tb.rows.length - 1;
          }
          for (var i = startRow; i < endRow; i++) {
            if (tb.rows[startRow].cells[col].innerHTML == tb.rows[i + 1].cells[0].innerHTML) {
              tb.rows[i + 1].removeChild(tb.rows[i + 1].cells[0]);
              tb.rows[startRow].cells[col].rowSpan = (tb.rows[startRow].cells[col].rowSpan | 0) + 1;
              if (i == endRow - 1 && startRow != endRow) {
                mc(tableId, startRow, endRow, col + 1);
              }
            } else {
              mc(tableId, startRow, i + 0, col + 1);
              startRow = i + 1;
            }
          }
        }

        this.$nextTick(function () {
          mc('mergeCellTable', 0, 0, 0);
        })

      },
      handleSuccess(res, file, list) {
        console.log(111);
        // this.fileList.push(res.content.originalFilename);
        this.$refs.uploadxxx.clearFiles();
      },
      handleError() {
        console.log(444);
      },
      openMsg() {
        this.$message({
          message: '普通消息',
        })
      },
      openSuccess() {
        this.$message({
          message: '消息内容',
          type: 'success'
        })
      },
      openConfirm() {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`, this.curPageIndex);
      },
      resetPage() {
        this.curPageIndex = 1;
      }
    },
    components: {
      leftBar
    },
  }

</script>
<style lang="scss" scoped>
