    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.7);
    z-index: -1;
    background: #fff;
  }

  .hidden {
    display: none;
  }
  tr {
    height: 40px;
  }
  td {
    height: 40px;
  }

</style>



