<template>
  <div ref="topRight" class="pie"></div>
</template>

<script type="text/javascript">
  import { echartsMixin } from 'assets/js/mixin'
  export default {
    name: '',
    mixin: [echartsMixin],
    props: {
      title: {
        type: String,
        default: ''
      },
      data: {
        type: Object,
        default: () => {}
      },
    },
    data() {
      return {}
    },
    mounted() {
      this.initOption();
      this.initChart();
    },
    methods: {
      initOption() {
        // initOption 作用： 一次性捕获 echats 图表的挂载点，声明好 echats 图表的 基本配置
        this.chart = this.$echarts.init(this.$refs.topRight);
        this.option = {
          color: ['#008fff','#FFB02C'],
          tooltip: {
            trigger: 'item',
            formatter: "{b}: {c} ({d}%)"
          },
          series: [{
            name: '',
            type: 'pie',
            center: ['50%', '50%'],
            radius: ['25%', '40%'],
            label: {
              normal: {
                position: 'outside',
                show: true,
                formatter: "￥{c}\n{b}{d}%",
                fontSize:14
              }
            },
            labelLine: {
              normal: {
                length: 15,
                length2: 15
              }
            },
            data: []
          }]
        };
      },
      initChart() {
        this.option.series[0].data = this.data.data;
//        console.log(this.data)
        this.chart.setOption(this.option, true);
      },

      // echats 图表自适应
      _resizeHanlder() {
        this.chart.resize()
      },
      // 销毁echats图表方法
      _destroyHandler() {
        this.chart.dispose()
      }
    },
    components: {},
    watch: {
      data: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";
  .pie {
    width: 100%;
    height: 370px;
  }
</style>



