<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";

  .information-box{
    margin-top:10px;
    padding: 20px;
    background: #fff;
    font-size: 14px;
  }
  .el-select {
    width: 100%;
  }
  .accessory {
    position: relative;
    .detail-text {
      background: #F3F3F3;
      border-radius: 13px;
      color: #345de7;
      height: 28px;
      line-height: 28px;
      padding-left: 15px;
    }
    .examine {
      position: absolute;
      top: 0;
      right: 0;
      color: #345DE7;
      width: 82px;
      height: 28px;
      line-height: 10px;
      background: #FDFDFD;
      border: 1px solid #E2E2E2 !important;
      border-radius: 0 25px 25px 0 !important;
    }
  }
  .information-box{
    .icon-xiazai2,.icon-jianhao{
      margin-left: 10px;
      display:inline-block;
      vertical-align: middle;
      cursor: pointer;
      color:#345DE7;
      font-size: 24px;
      &:hover{
        color: #409EFF;
      }
    }
  }
  .duty-box {
    margin-top:10px;
    padding: 20px;
    background: #fff;
    font-size: 14px;
    .duty-table {
      width: 100%;
      margin-top: 20px;
      border: 1px solid #E9EEFF;
      thead {
