          height: 40px;
          border: 1px solid #E9EEFF;
          text-align: center;
          background: #F3F6FE;
          font-weight: bold;
          &.principal-header {
            width: 40%;
          }
        }
      }
      tbody {
        td {
          height: 40px;
          border: 1px solid #E9EEFF;
          text-align: center;
          &.principal {
            /*width: 50%;*/
            display: flex;
            border: 1px solid #E9EEFF;
            justify-content: space-between;
            line-height: 40px;
            padding: 0 20px 0 30px;
            a {
              color: #3A61E7;
            }
          }
          .sure {
            display: inline-block;
            width: 80px;
            height: 28px;
            line-height: 28px;
            background: #6786ED;
            color: #fff;
            border-radius: 14px;
            &:hover {
              cursor: pointer;
              background: #345DE7;
            }
          }
          .green {
            color: #15bd68;
          }
        }
      }
    }
  }
  .txt-c {
    margin-top: 30px;
    text-align: center;
    button {
      margin: 0 15px;
    }
  }
</style>



