<template>
  <div class="base-step">    
    <div class="step-icon-wrap">
      <div class="icon-item" v-for="(item,index) in steps" :class="{sm:item.type&&item.type=='sm','done':item.state=='done','doing':item.state=='doing','pre':item.ispreStep}" >
        <div class="img-wrap">
          <img :src="item.src" v-if="item.src&&item.src!=''">
        </div>        
        <span>{{item.name}}</span>
      </div>                   
    </div>            
    <div class="step-content">
      <div class="step-item" v-for="(item,index) in steps" :class="{sm:item.type&&item.type=='sm','done':item.state=='done','doing':item.state=='doing','pre':item.ispreStep}"  >
        <div class='center'></div>
        <div class="circle"></div>
        <div class="line"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: 'base-step',
    props: {
        steps:{
           type:Array,
           default:function(){
             return [];
           }
        },
        curStep:{
           default:''
        },
    },
    data() {
      return {
        // steps:[
        //   {value:'xxx1',name:'xxx1',src:'static/img/stepicon/s_100_0.png'},
        //   {value:'xxx2',name:'xxx2',src:'static/img/stepicon/s_100_0.png',type:'sm'},
        //   {value:'xxx3',name:'xxx3',src:'static/img/stepicon/s_100_0.png'},
        //   {value:'xxx4',name:'xxx4',src:'static/img/stepicon/s_100_0.png'},
        //   {value:'xxx5',name:'xxx5',src:'static/img/stepicon/s_100_0.png',type:'sm'},
        //   {value:'xxx6',name:'xxx6',src:'static/img/stepicon/s_100_0.png'}
        //   ],
        // curStep:'xxx3',
      }
    },
    mounted() {
      // console.log(this.steps,this.curStep);
      // this.init();
    },
    methods: {
        init(){
          if(!this.steps) return ;
          let preStep;
          for(var obj of this.steps){
            if(obj.value === this.curStep){
              obj.state = 'doing';
              if(preStep){
                preStep.ispreStep = true;
              }              
              break;
            }
            else{
              obj.state = 'done';       
            }
            preStep = obj;
          }
          this.$set(this.steps)          
        }
    },
    watch:{
        steps:function(){
          this.init();
        }
    },
    components: {

    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
  .step-icon-wrap{
    display: flex;
    flex-wrap:nowrap;
    margin-bottom: 10px;
    .icon-item{
      position: relative;
      flex-grow:2;
      text-align: center;      
      width: 100px;
      display: flex;
      flex-direction:column;
      justify-content: space-between;
      align-self: center;
      .img-wrap{
        height: 56px;
      }
      img{
        margin-bottom: 5px;
      }
      span{
        font-weight: 600;
        color: #666666;
        font-size: 14px;
      }
      &.sm{
        flex-grow:1;
      }
      &.done{
        span{color: #566de1;}
      }
      &.doing{
        span{color: #f7d404;}
      }
    }

  }

  .step-content{
    height: 28px;
    display: flex;
    flex-wrap:nowrap;
    .step-item{
      position: relative;
      flex-grow:2;
      width: 100px;
      .center{        
        position: absolute;
        top:5px;
        width: 100%;
        height: 18px;
        background: url(../../../assets/img/linebg001.png);
      }
      .circle{
        position: absolute;        
        border-radius: 50%;
        background-color: #d3d8e1;
        box-shadow: 0px 3px 8px 0px rgba(123, 124, 147, 0.2);
        width: 24px;
        height: 24px;  
        left:50%;
        margin-left: -12px;        
        z-index: 1;
        border: 2px solid #fff;
        box-sizing: border-box;
        top: 2px;
      }
      .line{
        top: 13px;
        position: absolute;
        width: 100%;
        height: 2px;
        background: #d3d8e1;
        z-index: 2;
      }
      &.sm{
        flex-grow:1;
        .circle{
          width: 18px;
          height: 18px;
          margin-left: -9px;
          margin-top: 3px;
        }        
      }
      &:first-child{
        .center{
          width: 50%;
          left:50%;
        }        
        .line{
          width: 50%;
          left:50%;
        }
        &.doing .line{          
          background: linear-gradient(90deg,#f7d404 50%, #edd86e 75%, #d3d8e1 100%);
        }
      }
      &:last-child{
        .center{
          width: 50%;
        }        
        .line{
          width: 50%;
        }
        &.doing .line{          
          background: linear-gradient(90deg,#b3a755 0%, #e2c513 25%, #f7d404 50%);
        }        
      }
      &.done{
        .circle{
          background: #566de1;
        }
        .line{
          background: #566de1;
        }
      }
      &.pre .line{
        background: linear-gradient(90deg,#566dd1 50%,#7c84a8 75%,#b3a755 100%);
      }
      &.doing{
        .circle{
          background: #f7d404;       
        }
        .line{
          background: linear-gradient(90deg, #b3a755 0%, #e2c513 25%, #f7d404 50%,#edd86e 75%,#d3d8e1 100%);
        }
      }
    }
  }


</style>



