<template>
<div>
	    	<div class="warpmain">
    		  <projectStep class="mtm" :stepType="2" :curStep='curStep' style='padding-top:35px;'></projectStep>
     		<div class="wapcont">
     			<div class="p_title">
		        <i class="iconfont icon-wendang1"></i><p>填写里程碑信息</p>
		      </div>
		      
		        <div class="my-tab-wrap">      
		          <ul class="my-tab-header">
		            <li :class="{active:prams.tabName=='0'}" @click="handleClick('0')">准备阶段</li>
		            <li :class="{active:prams.tabName=='1'}" @click="handleClick('1')">调研阶段</li>
		            <li :class="{active:prams.tabName=='2'}" @click="handleClick('2')">设计阶段</li>
		            <li :class="{active:prams.tabName=='3'}" @click="handleClick('3')">实施阶段</li>
		            <li :class="{active:prams.tabName=='4'}" @click="handleClick('4')">验收阶段</li>
		            <li :class="{active:prams.tabName=='5'}" @click="handleClick('5')">移交</li>
		          </ul>
		          <div class="my-tab-panel" style="margin-top: 20px;">
		      		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm"> 
			         	<el-row>
							  <el-col :span="24">
							  	        <span class="lable">阶段负责人: </span>   褚甜甜
								</el-col>
							 			
							</el-row>
							<el-row>
								 <el-col :span="12">
			  	       		 <span class="lable">计划起始时间: </span>
			  	       		 <el-form-item prop="planstartTime">
			  	       		 	 <el-date-picker class="p_date" v-model="ruleForm.planstartTime" :disabled="!prams.showInput" type="date" placeholder="选择日期" >
	               			 </el-date-picker> 
               			</el-form-item>
								 </el-col>							 
								<el-col :span="12">
						  	    	<span class="lable">计划完成时间: </span>
										  <el-form-item prop="planendTime">
			  	       		 	 <el-date-picker class="p_date" v-model="ruleForm.planendTime" :disabled="!prams.showInput" type="date" placeholder="选择日期" >
	               			 </el-date-picker> 
               				</el-form-item> 
								</el-col>	
							</el-row>
							<el-row>
								 <el-col :span="12">
			  	       		 <span class="lable">计划付款金额: </span>
			  	       		<el-form-item prop="payMoney">
		  	       			 <el-input v-model="ruleForm.payMoney" class="p_text" :disabled="!prams.showInput"></el-input>
		  	       			</el-form-item> 
								 </el-col>							 
								<el-col :span="12">
						  	        <span class="lable">计划付款时间: </span>
						 		 <el-form-item prop="planPayTime">
			            <el-date-picker class="p_date" v-model="ruleForm.planPayTime" :disabled="!prams.showInput" type="date" placeholder="选择日期" >
	                </el-date-picker>  
	               	</el-form-item> 
								</el-col>	
							</el-row>			
						 
						 <div class="planListBox">
						  	 <span class="lable">计划提交附件: </span>
							 	 <div class="file-group planFilename" > 
              		 	<p v-for="(item,index) in prams.planFileList" class="planFilename">
              
              		 		<el-input 
              		 			v-if='index==0'
              		 			placeholder="请输入名称" 
              		 			:value="item"  
              		 			class="p_text" 
              		 			disabled="">
              		 		</el-input>              		 		 
              		 		<el-input 
              		 			v-else
              		 			placeholder="请输入名称" 
              		 			:value="item"  
              		 			class="p_text" 
              		 			:disabled="!prams.showInput">
              		 		</el-input>
              		 		<span class="iconfont icon-jianhao" v-if='index!=0' @click="remove(index)"></span>
              		 		<el-button v-if='index==0' size="small" type="primary" @click="addPlanList">定义其他附件</el-button>
              		 	</p>								 	
			           </div> 
		 					</div>
		 				</el-form>
		          </div>
		          
		        </div>
		        
     		</div>
    	</div>
	
	
	
    	<div v-if='prams.showInput' style='display: flex;justify-content: center; padding-top: 20px;'>  	
      	 <button type="submit"  @click="submitForm('ruleForm')" class="p_submit" style="margin-right: 20px;"><i class="iconfont icon-submit"></i>提交</button>
      	<!--保存按钮-->   
      	 <button class="p_save"><i class="iconfont icon-baocun"></i>保存</button>
 
    	</div>	
	
	
  					
						
			 
	
</div>  
</template>

<script type="text/javascript">

import projectStep from '../components/projectStep'
  export default {
    name: 'milepostStep',
    props: {
      ruleForms:{
      	type:Object,
      	default:()=>{}
      },
      tab:{
      	type:Number,
      	default:0
      }
    },
    data() {
      return {
      	curStep:'6',
      	ruleForm:{
      		planstartTime:this.getDate('2017-10-22'),
      		planendTime:this.getDate('2017-10-22'),
      		payMoney:'5000',
      		planPayTime:this.getDate('2017-10-22')
      	},
     		rules: {
          planstartTime: [
            {type: 'date', required: true, message: '请输入计划起始时间', trigger: 'change' },
          ],
        	planendTime: [
            {type: 'date', required: true, message: '请输入计划完成时间', trigger: 'change' },
          ],
           payMoney: [
            { required: true, message: '请输入计划付款金额', trigger: 'blur' },
          ],
        	planPayTime: [
            { type: 'date',required: true, message: '请输入计划付款时间:', trigger: 'change' },
          ]
        
       },
 				 prams:{
 				 	tabName:0,  			
 					showInput:true,
       		planFileList:['abc.doc','bbb.ppt','ccc.jpg']
 				 }
      }
    },
    mounted() {
       console.log(this.prams.planFileList)  
    },
    methods: {
   			addPlanList:function(){
   				this.prams.planFileList.push('')
   			},
				remove(num){
					console.log(num)
   				this.prams.planFileList.splice(num,1)
   				console.log(this.prams.planFileList)
   		},
 			submitForm(formName) {
     		//centerDialogVisible = false
        this.$refs[formName].validate((valid) => {
          if (valid) {
           console.log(this.ruleForm)
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },    		 	
    	handleClick(tabs) {
	        this.prams.tabName = tabs;
     },
    	getDate(strDate){
		   let date = eval('new Date(' + strDate.replace(/\d+(?=-[^-]+$)/, 
		   function (a) { return parseInt(a, 10) - 1; }).match(/\d+/g) + ')');
		  return date;
		}
    },
    components: {
 			 projectStep,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
 .planListBox{
 	display: flex;
 }
   	.el-date-editor.el-input, .el-date-editor.el-input__inner{
 			 		width: 300px;
 			 	}
 			 	.el-col-12{
 			 		 display: flex;
			   	 align-items: center;
			   
	 				&:nth-child(2n){
	 					justify-content: flex-end;
	 				}
 			 	}
 			 	.el-col-24{
 			 		display: flex;
 			 		justify-content: flex-start;
 			 		align-items: center;
 			 	}
 			 	.el-row{
 			 		margin-bottom: 20px;
	 		    display: flex;
			    justify-content: center;
			    flex-wrap: wrap;
			    align-items: center;
 			 	}
 			 		.el-input{
 			 			width: 300px;
 			 		}
 			 		span{
			   	 	width: 100px;
			   	 	display: inline-block;
			   	 	text-align: left;
			   	 }
			   	 	
			   	 p.planFilename{
			   	 		margin-bottom: 10px;
			   	 	 	position: relative;
					    display: flex;
					    align-items: center;
					    .icon-jianhao:before{
					    	 
							  	margin-left: 10px;
							    cursor: pointer;
							    color: #345DE7;
							    font-size: 24px;
					    }
			   	 }
			   .planFilename{			
			   	 .el-input{
			   	 		width: 200px !important;
			   	 		margin-left: 10px;
			   	 }
			   
			   	 button{
			   	 	    color: #345DE7;
						    min-width: 82px;
						    height: 28px;
						    line-height: 10px;
						    background: #FDFDFD;
						    border: 1px solid #E2E2E2 !important;
						    border-radius: 25px!important;
			   	 }
			   }
		.warpmain{
 			 	background:#fff;
 			 	padding:40px; 
 			 	padding-bottom: 10px;			  
 			 	.right{
 			 		float: right;
 			 	}
 			
 			 	.wapcont{
 			 		margin-top: 20px;
 			 	
 			 	}
 		}
 		
 .my-tab-wrap{
  	margin:30px;
  	li{
  		width:88px;
  		height: 30px;
  		border: 1px solid #9899ad;
  		border-radius: 30px;
  		line-height: 30px;
  		text-align: center;
  		display: inline-block;
  		margin-right: 10px;
  		cursor:pointer;
	  	&.active{
	  		border-color: #635fc0;
	  		color: #635FC0;
	  	}
  	}
  }
</style>



