<template>
	 <div class="leftBar" style=" width: 130px;">
      <el-menu
                    :default-active="defaultActive"
                    class="el-menu-vertical-demo"
                    @open="handleOpen"
                    unique-opened
                    @close="handleClose"
                    @select='selectDom'
      							text-color="#fff"
                    >
                <el-submenu index="0" >
                    <template slot="title" >
                    	<i class="iconfont icon-arrowDown seticon"></i>
                        <span>设立管理</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="setupManage" >

                         <router-link :to="{name:'setupManage'}" class='iconfont icon-ketilixiang' >
                           		立项管理
                           </router-link>
                          </el-menu-item>
                        <el-menu-item index="capitalManage">
                         <router-link :to="{name:'capitalManage'}" class='iconfont icon-zijin'>
                         		 资金管理
                           </router-link>
                        </el-menu-item>
                        <el-menu-item index="tenderManage" >
                         <router-link :to="{name:'tenderManage'}" class='iconfont icon-shouquanbiaoshu'>
                          		 招标管理
                           </router-link>
                        </el-menu-item>
                        <el-menu-item index="contractManage" >
                         <router-link :to="{name:'contractManage'}" class='iconfont icon-hetong' >
                          		 合同管理
                           </router-link>
                        </el-menu-item>

                    </el-menu-item-group>
                </el-submenu>
                <el-submenu index="1">
                    <template slot="title">
                         	<i class="iconfont icon-arrowDown seticon"></i>
                        <span>进度管理</span>
                    </template>
                    <el-menu-item-group>
            				<el-menu-item index="information" >
                     		   <router-link :to="{name:'information'}" class='iconfont icon-xinxiweihu' >
                          		 信息维护
                           </router-link>
                        </el-menu-item>
                        <el-menu-item index="milepost" >
                     		   <router-link :to="{name:'milepost'}" class='iconfont icon-cn_lichengbei' >
                          		 里程碑定义
                           </router-link>
                        </el-menu-item>

                          <el-menu-item index="capitalpay" >
                     		   <router-link :to="{name:'capitalpay'}" class='iconfont icon-dun001' >
                          		 资金支付
                           </router-link>
                        </el-menu-item>

                      	<el-menu-item index="ready" >
                     		   <router-link :to="{name:'ready'}" class='iconfont icon-chart58' >
                          		 准备阶段
                           </router-link>
                        </el-menu-item>

                         <el-menu-item index="ready1" >
                     		   <router-link :to="{name:'ready1'}" class='iconfont icon-tiaoyan' >
                          		 调研阶段
                           </router-link>
                        </el-menu-item>
                        <el-menu-item index="ready2" >
                     		   <router-link :to="{name:'ready2'}" class='iconfont icon-sheji' >
                          		设计阶段
                           </router-link>
                        </el-menu-item>

                         <el-menu-item index="ready3" >
                     		   <router-link :to="{name:'ready3'}" class='iconfont icon-jichu' >
                          		 实施阶段
                           </router-link>
                        </el-menu-item>

                         <el-menu-item index="ready4" >
                     		   <router-link :to="{name:'ready4'}" class='iconfont icon-hetong' >
                          		 验收阶段
                           </router-link>
                        </el-menu-item>

                         <el-menu-item index="over" >
                     		   <router-link :to="{name:'over'}" class='iconfont icon-plan-transfer-o' >
                          		 移交阶段
                           </router-link>
                        </el-menu-item>






                    </el-menu-item-group>
                </el-submenu>
                <el-submenu index="2" style="margin-bottom:10px">
                    <template slot="title">

                       		<router-link :to="{name:'document'}" class='' tag='span' >
                          		文档管理
                           </router-link>
                    </template>

                </el-submenu>
                <el-submenu index="3">
                    <template slot="title">

                       <!-- <span>资产管理</span>-->
                           <router-link :to="{name:'asset1'}" class='' tag='span' >
                          		资产管理
                           </router-link>
                    </template>

                </el-submenu>
            </el-menu>
    </div>

</template>


<script type="text/javascript">
export default {
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
		 defaultActive:'',
    }
  },
	mounted() {
		  this.defaultActive=this.$route.name
	},
  methods: {
  	selectDom(k,v){
  		console.log(k)
  	},
    fetchDate(n,o){
      this.defaultActive= n.name;
      console.log(n,o,this.$route.path)
      // this.defaultActive='1-2'
    },
    handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
    }
  },
  watch: {
    "$route": "fetchDate"
  }
}
</script>
/*
export default {
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {

    }
  }
}*/
</script>

<style lang="scss">
@import "./../../assets/css/variable.scss";
@import "./../../assets/css/mixin.scss";
//leftBar
.leftBar{
    .el-menu{
        background: transparent;
        border: none;
       &>li{
            width: 130px;
 						margin-bottom: 10px;
            color: #333333;
            line-height: 50px;
            &.is-opened{
            	margin-bottom: 0;
            }
            span{
              background-color: #ffffff;
              color: #333333 !important;
             line-height: 50px;
             font-size: 14px;
             font-weight: bold;
            }
          ul{
              width: 122px;

                background-color: #eef2f7;
                box-shadow: 0px 0px 5px 1px
                rgba(154, 165, 190, 0.23);
                margin: 0px auto;
            li{
              margin: 0;
              width: 100%;

              a{
              	position: relative;
              	display: block;
              	font-size: 14px;
              	color: #666;
            		text-align: left;
   							width: 90px;
              	&:before{
              		font-size: 14px;
              		color: #345ded;
              	}
              	 &:after{
              		background-image: linear-gradient(90deg,transparent,#bfc6cf,transparent);

									border-radius: 0px;
									height: 2px;
									content:'';
									position: absolute;
									left: 0;
									bottom: 0px;
									width: 100%;
              	}

              }
            }
          }
       }
    }
    .el-icon-location{
        display: none !important;
    }
    .el-menu-item-group__title{
        margin: 0px;
        display: none;
    }
    .el-submenu__title{
        position: relative;
        text-align: center;
        background: #fff;
        box-shadow: 3px 3px 15px 0px
		rgba(154, 165, 190, 0.3);
		    position: relative;
   			z-index: 22;
    }
    .el-submenu .el-menu-item{
        padding: 0 !important;
        min-width: auto;
        display: flex;
		   	text-align: center;
		   	justify-content: center;
    }
    .el-submenu__icon-arrow{
        margin-top: -5px;
        right: auto;
        left: 20px;
        display: none;
    }
   	.el-submenu.is-opened{
   		.seticon{
           &:before{
               color: #345de7;
           }

           transform: rotate(0deg);
        }
   	}

    .seticon{
        display: inline-block;
        transform: rotate(-90deg);
        transition: all .3s;
        color: #999999;
        &.none{
           &:before{
               color: #345de7;
           }

           transform: rotate(0deg);
        }
    }
    .el-menu-item.is-active{
        a{
        	 &:before{
               color: #ff6c00;
           }
            color: #ff6c00;
            font-size: 14px;
        }
    }
}
</style>



