<template>
  <div>
  	<div class="newSection">
    	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" action="" method="post">
    		<div class="p_title">
      	 	<i class="iconfont icon-add"></i><p>新建部门</p>
      	</div>
      	<div class="form-add">
      		<el-col :span="10">
      			  <el-form-item label="部门名称：" prop="section">
						    <el-input class="p_text" placeholder="请输入部门名称"  v-model="ruleForm.section"></el-input>
						  </el-form-item>
      			  <el-form-item label="创建人："  class="indent">
      			  	 <span>{{name}}</span>
						  </el-form-item>
      		</el-col>
      		<el-col :span="4">
      			
      		</el-col>
      		<el-col :span="10">
              <el-form-item label="部门编号：" prop="sectionNum">
						    <el-input class="p_text" placeholder="请输入部门编号" v-model="ruleForm.sectionNum"></el-input>
						  </el-form-item>
						  <el-form-item label="创建日期：" class="indent">
      			  	 <span>{{time}}</span>
						  </el-form-item>
      		</el-col>
      		<div class="remark-box">
      			<label for="remark">备注：</label>
      			<textarea placeholder="请输入备注信息" id="remark"  v-model="remark"></textarea>
      		</div>
      	</div>
      	<div class="form-submit">
      		<el-form-item>
      			<button type="button" @click="submitForm('ruleForm')"  class="p_submit" ><i class="iconfont icon-baocun"></i>提交</button>
				    <router-link tag="button" :to="{path:'/system/sectionManage'}" class="p_save qx" ><i class="iconfont icon-quxiao"></i>取消</router-link>
				  </el-form-item>
      	</div>

    	</el-form>
    </div>
  </div>
</template>

<script type="text/javascript">
import axios from "axios"
export default {
  name: 'component_name',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
    	id:'',
    	time:null,
    	name:null,
    	date:"",
    	remark:"",
       ruleForm:{
    		section:'',
    		sectionNum:'',
//  		sectionName:'',
    	},
    	rules:{
      	section: [
          { required: true, message: '请输入部门名称', trigger: 'blur' },
//        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
      	sectionNum: [
          { required: true, message: "请输入部门编号", trigger: 'blur' },
//        { min: 5, max: 10, message: '长度在 5 到 10 个字符', trigger: 'blur' }
        ],
      },
    }
  },
  mounted(){
  	this.getData()
//	console.log(this.$router.query)
  },
  created: function() {
//  console.log( this.$route.query.id)
    this.id = this.$route.query.id
    this.getEditData(this.$route.query.id)
 },
  methods:{
  	//编辑
  	getEditData(i){
  		console.log(i)
		this.$xhr.get(`/api/sysGov/info?id=${i}`).then((res)=>{
//			  console.log(res)
				this.ruleForm.section = res.sysGov.name
				this.ruleForm.sectionNum = res.sysGov.govNo
				this.time = res.sysGov.createDate   
				this.name = res.sysGov.creator   
				this.remark = res.sysGov.extra   
			}).catch(()=>{
				 console.log(111)
			})
  	},
  	//提交
  	submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.id)
        if(this.id){
        	console.log(1)
        	this.$xhr.put("/api/sysGov/update",{
        		  extra: this.remark,
          		govNo: this.ruleForm.sectionNum,
          		name:  this.ruleForm.section,
          		id: this.id,
          		createDate:this.time,
          		creator: this.name
        	})
        }
        else{
        	console.log(2)
          this.$xhr.post("/api/sysGov/addSysGov",{
          		extra: this.remark,
          		govNo: this.ruleForm.sectionNum,
          		name:  this.ruleForm.section,
          		id: parseInt(Math.random()*1000),
          		createDate:this.time,
          		creator: this.name
          }).then((res)=>{
//        	console.log(res.data)
          	this.ruleForm.sectionNum = ""
          	this.ruleForm.section = ""
          })
        
        }

//         this.$router.push("/system/sectionManage") 				    

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      //初始化
      getData(){
      	this.$xhr.get("/api/sysGov/initializationSysGov")
		      	 .then((res)=>{
//		      		  console.log(res)
		      		  this.time = res.sysGov.createDate
		      		  this.name = res.sysGov.creator
                
		      	})
		      	 .catch(function (response) {
						    console.log(response);
						  });
      	
      },
      
  }
}
</script>

<style lang="scss" >
  .newSection{
   	 	/*height: 270px;*/
   	 	/*box-sizing: border-box;*/
   	 	padding: 45px 0;
   	 	position: relative;
   	 	.p_title{
   	 		i{
   	 			font-weight: bold;
   	 			font-size: 16px;
   	 		}
   	 	}

   	 	.el-col{
   	 		 .el-form-item__error{ 
   	 		 	   text-indent: 15px !important;
   	 		 }
   	 	}
   	 	.form-add{
   	 		box-sizing: border-box;
   	 		padding: 0 20px;
   	 		display: flex;
   	 		flex-wrap: wrap;
   	 		justify-content: space-between;
   	 		padding-top: 26px;
   	 		span{
   	 			padding-left: 18px;
   	 		}
   	 		.el-form-item__label{
   	 			text-align: left;
   	 		}
   	 		.indent{
   	 			text-indent: 11px;
   	 		}
   	 		.remark-box{
   	 			label{
   	 				display: inline-block;
   	 				vertical-align: top;
   	 				width: 100px;
   	 				text-indent: 11px;
   	 			}

   	 			&>textarea{
   	 				width: 980px;
   	 				height:165px;
   	 				background: #f3f3f3 ;
            border-radius: 5px;
            border:none;
            text-indent: 8px;
            box-sizing: border-box;
            padding: 10px;
   	 			}
   	 		}
   	 		.p_text{
   	 			width: 330px;
   	 		}
   	 	}
   	 	.form-submit{ 
   	 		width: 100%;
   	 		position: absolute;
   	 		bottom: -72px;
   	 		text-align: center;
   	 		margin-top: 50px;

   	 		.p_submit{
   	 			margin-right: 25px;
   	 		}
   	
   	 	}
   	 }
</style>



