<template>
  <div class="detail-main">
    <div class="detail-wrap">
      <div class="basetitle">
        <span class="base-title-icon"></span>
        <span class="base-title">资产详情</span>
        <router-link class="p_return sm fr" tag="button" :to="{path:'../asset'}"><i class="iconfont icon-zhongzhi"></i>返回</router-link>
      </div>
      <div class="detail-container">
        <ul class="detail-list">
          <li class="detail-item" v-if="item.detailName !== '二维码' && item.detailName !== '附件'" v-for="(item, index) in detailData" :class="[{'remark' : item.detailName === '备注'}]">
            <div class="detail-name">{{detailData[index].detailName}}：</div>
            <div class="detail-text">{{detailData[index].value}}</div>
          </li>
          <li class="detail-item accessory" v-else>
            <div class="detail-name">{{detailData[index].detailName}}：</div>
            <div class="detail-text"><i class="iconfont icon-wendang"></i>&nbsp;{{detailData[index].value}}</div>
            <button type="button" class="examine">点击查看</button>
          </li>
          <li class="detail-item QRCode">
            <div class="detail-name">二维码：</div>
            <div class="QRCode-con">
              <div class="codePic">二维码图片</div>
              <ul class="codePic-list">
                <li class="codePic-item">
                  <div class="code-name">资产名：</div>
                  <div class="code-text">扫描仪THDJ&nbsp;<i class=""></i></div>
                </li>
                <li class="codePic-item">
                  <div class="code-name">合同名称：</div>
                  <div class="code-text">4567890234</div>
                </li>
                <li class="codePic-item">
                  <div class="code-name">合同编号：</div>
                  <div class="code-text">4567890</div>
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: '',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        detailData: [
          {detailName: '合同编号', value: '34567890-456'},
          {detailName: '合同名称', value: '数据铁笼'},
          {detailName: '资产卡片编号', value: '34567890-456'},
          {detailName: '资金预算号', value: '34567890-456'},
          {detailName: '资产分类', value: '扫描仪'},
          {detailName: '资产名', value: '扫描仪Hp-2324'},
          {detailName: '使用方向', value: '自用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '销售商联系方式', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '附件', value: '财政批复文件.jpg'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '使用状态', value: '在用'},
          {detailName: '备注', value: '在用'},
        ]
      }
    },
    mounted() {
    },
    computed: {},
    methods: {},
    components: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";

  .detail-main {
    .detail-wrap {
      width: 100%;
      overflow: hidden;
    }
    .detail-container {
      width: 100%;
      padding: 20px 40px;
      background: #fff;
      box-sizing: border-box;
      .detail-list {
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .detail-item {
          display: flex;
          justify-content: space-between;
          width: 40%;
          margin-bottom: 15px;
          &.accessory {
            position: relative;
            .detail-text {
              background: #F3F3F3;
              border-radius: 13px;
              color: #345de7;
            }
            .examine {
              position: absolute;
              top: 0;
              right: 0;
              color: #345DE7;
              width: 82px;
              height: 28px;
              line-height: 10px;
              background: #FDFDFD;
              border: 1px solid #E2E2E2 !important;
              border-radius: 0 25px 25px 0 !important;
            }
          }
          &.remark {
            width: 100%;
            .detail-text {
              width: 88%;
            }
          }
          &.QRCode {
            display: flex;
            justify-content: flex-start;
            width: 100%;
            .QRCode-con {
              display: flex;
              justify-content: space-between;
              margin-left: 7%;
              .codePic {
                display: inline-block;
                width: 94px;
                height: 94px;
                border: 1px solid red;
                margin-right: 15px;
              }
              .codePic-list {
                /*border: 1px solid red;*/
                padding: 15px 0;
                box-sizing: border-box;
                .codePic-item {
                  display: flex;
                  justify-content: space-between;
                  .code-name {
                    width: 80px;
                    /*height: 25px;*/
                    line-height: 22px;
                  }
                  .code-text {
                    width: 200px;
                    /*height: 25px;*/
                    line-height: 22px;
                    box-sizing: border-box;
                  }
                }
              }
            }
          }
          .detail-name {
            display: inline-block;
            height: 28px;
            line-height: 28px;
          }
          .detail-text {
            display: inline-block;
            width: 70%;
            height: 28px;
            line-height: 28px;
            padding: 0 15px;
            box-sizing: border-box;
          }
        }
      }
    }
  }
</style>



