<template>
  <div>
  	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" action="" method="post">


  	 <div class="limitManage-add" >
    	<div class="p_title">
      	 	<i class="iconfont icon-add"></i><p>新建角色</p>
      </div>
      <div class="add-content">
      	<div class="add-role" >

          <el-form-item label="角色：" prop="name">
						    <el-input class="p_text" placeholder="请输入角色" style="width: 100%;"
						    	  v-model="ruleForm.name"></el-input>
						  </el-form-item>
      	</div>
      	<div class="add-title">分配权限</div>
      	<div class="add-limit">
      		<p class="system-title">
      			<input type="checkbox" :checked="systemCheck" @click="systemSelectall()"/>系统管理权限
      		</p>
      	    <ul class="system-list">
      	    	<li v-for="(val,index) in systemData">
      	    		<input type="checkbox" :checked="val.check"  :id="val.moduleId" @click="systemSelect(index)" value="" />
      	    		{{val.moduleName}}
      	    	</li>
      	    </ul>
      		<p class="system-title">
      			<input type="checkbox" :checked="projectCheck" @click="projectSelectall()"/>项目管理权限
      		</p>
      	    <ul class="system-list project">
      	    	<li v-for="(val,index) in projectData">
      	    		<input type="checkbox" :checked="val.check" :id="val.moduleId" @click="projectSelect(index)" value="" />
      	    		{{val.moduleName}}
      	    	</li>
      	    </ul>
      	</div>
      	<div class="form-submit">
      		<el-form-item>
				    <button type="button" @click="submitForm('ruleForm')"  class="p_submit" ><i class="iconfont icon-baocun"></i>提交</button>
				    <router-link tag="button" type="button" :to="{path:'/system/limitManage'}" class="p_save qx" ><i class="iconfont icon-quxiao"></i>取消</router-link>
      		</el-form-item>
      	</div>
      </div>

    </div>
  	</el-form>

  </div>
</template>

<script type="text/javascript">


export default {
  name: 'component_name',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
    	projectCheck:false,
    	systemCheck:false,
	    ruleForm:{
          permssion :"",
          name :'',
          extra:''
        },
      projectlist:{},
      systemlist:{},
	    	rules: {
	        name: [
	          { required: true, message: '请输入角色名称', trigger: 'blur' },
//	          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
	        ]
	      },
        systemData:[],
        projectData:[],
      projectManagementAuthority:{
        moduleId:''
      },
      systemManagementAuthority:{
        moduleId:''
      },
      dataid:'',

    }
  },
  mounted(){
    this.getProjectAndSystemItem();
    this.loaddata();
  },
  methods: {
    loaddata() {
      if (this.$route.query.id) {
        this.dataid = this.$route.query.id;
        this.$xhr.get(`/api/sysRole/info`, {
          params: {
            id: this.dataid
          }
        }).then((res) => {
          console.log(res)
          this.ruleForm.name=res.sysRole.name
            res.sysRole.projectManagementAuthority.forEach((valid, indexs, arr) => {
              this.projectData.forEach((val, index, arr) => {
                if (valid.moduleId == val.moduleId) {
                  val.check=true;
                }
              })})
              res.sysRole.systemManagementAuthority.forEach((valid, indexs, arr) => {
                this.systemData.forEach((val, index, arr) => {
                  if (valid.moduleId == val.moduleId) {
                    val.check=true;
                  }
                })})
              this.$set(this.projectData);
              this.$set(this.systemData);
          }).catch((req) => {
            console.log(res)
          })
      }
    },
    getProjectAndSystemItem() {
      this.$xhr.get('/api/sysRole/initializationSysRole', {
        params: {}
      })
        .then((res) => {
          this.systemData = res.SysRolePojo.systemManagementAuthority;
          this.projectData = res.SysRolePojo.projectManagementAuthority;
        })
        .catch((req) => {
          console.log(res)
        })
    },
    //system//系统权限全部选择
    systemSelectall() {
      this.check = !this.check;
      this.systemData.forEach((val, index, arr) => {
        val.check = this.check
      })
    },
    //project/项目权限全部选择
    projectSelectall() {
      this.check = !this.check;
      this.projectData.forEach((val, index, arr) => {
        val.check = this.check
      })
    },
    //system//系统权限选择
    systemSelect(i) {
      this.systemData[i].check = !this.systemData[i].check;
      let item = 0;
      this.systemData.forEach((val, index, arr) => {
        if (val.check == true) {
          item++;
        }
        if (item == this.systemData.length) {
          this.check = true
        } else {
          this.check = false
        }
      })
    },
    //project/项目权限选择
    projectSelect(i) {
      this.projectData[i].check = !this.projectData[i].check;
      let item = 0;
      this.projectData.forEach((val, index, arr) => {
        if (val.check == true) {
          item++;
        }
        if (item == this.projectData.length) {
          this.check = true
        } else {
          this.check = false
        }
      })
    },
    //得到选择的系统项
    getsystemselects() {
      this.systemlist = [];
      this.systemData.forEach((val, index, arr) => {
        if (val.check == true) {
          this.systemlist.push({"moduleId": val.moduleId})
        }
      })
    },
    //得到选择的项目项
    getprojecselects() {
      this.projectlist = [];
      this.projectData.forEach((val, index, arr) => {
        if (val.check == true) {
          this.projectlist.push({"moduleId": val.moduleId})
        }
      })
    },
    //提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.getsystemselects();
          this.getprojecselects();
          if(this.dataid) {
            this.$xhr.post("/api/sysRole/update", {
              permssion: this.dataid,
              name: this.ruleForm.name,
              projectManagementAuthority: this.projectlist,
              systemManagementAuthority: this.systemlist
            }).then((res) => {
              this.$router.push({path: '/system/limitManage'});
            })
          }
          else
          {
            this.$xhr.post("/api/sysRole/addSysRole", {
              permssion: this.dataid,
              name: this.ruleForm.name,
              projectManagementAuthority: this.projectlist,
              systemManagementAuthority: this.systemlist
            }).then((res) => {
              this.$router.push({path: '/system/limitManage'});
            })
          }
        }
      });
    },
  }
}
</script>

<style lang="scss" >
    .limitManage-add{
    	input{
    		cursor: pointer;
    		margin-top: -2px;
    	}
   	 	box-sizing: border-box;
   	 	padding-top: 45px;
   	 	padding-bottom: 40px;
   	 	position: relative;
   	 	.add-content{
   	 		padding: 0 16px;
   	 		.add-role{
   	 			padding: 36px 0;
   	 			/*height: 150px;*/
          &>div{
          	width: 36% !important;
          	.el-form-item__label{
          		text-align: left;
          	}

          	.el-form-item__error{
          		text-indent: 15px !important;
          		/*background: red;*/
          		width: 100%;
          	}
          }
   	 		}
   	 		.add-title{
   	 			height: 30px;
   	 			color: #2F56DF;
   	 		}
   	 		.add-limit{
   	 			border: 1px solid #E7ECFF;
   	 		}
   	 		.system-title{
   	 			height: 40px;
   	 			border-bottom: 1px solid #E7ECFF;
   	 			background: #F3F6FE;
   	 			line-height: 40px;
   	 			font-weight: bold;
   	 			input{
   	 				  margin-right: 11px;
              margin-left: 10px;
              vertical-align: middle;
   	 			}
   	 		}
   	 		.system-list{
   	 			box-sizing: border-box;
   	 			padding: 10px 0;
   	 			height: 109px;
   	 			display: flex;
   	 			flex-wrap: wrap;
   	 			align-content: space-around;
   	 			li{
   	 				width: 25%;
   	 				box-sizing: border-box;
   	 				padding-left: 30px;

   	 				input{
   	 					margin-right: 11px;
              margin-left: 10px;
   	 					vertical-align: middle;
   	 				}
   	 			}
   	 		}
   	 		.project{
   	 			height: 193px;

   	 		}
   	 		.form-submit{
   	 			/*position: absolute;
   	 			left: 0;
   	 			bottom: -60px;*/
	   	 		width: 100%;
	   	 		text-align: center;
	   	 		margin-top: 40px;

	   	 		.p_submit{
	   	 			margin-right: 25px;
	   	 		}

   	 	}
   	 	}

   	 }
</style>



