<template>
  <div class="sectionManage-wrap">
  	<div class="sectionManage-content">
  		
  	  <div class="clearfix sectionManage-top">
	  		<p class="fl">
	  		  <button class="p_return sm removes" @click="batchRemove()"><i class="icon iconfont icon-shanchu"></i>批量删除</button>
	  		</p>
	  		<p class="fr">
	  		  <router-link :to="{path:'/system/sectionManage/add'}" tag="button" class="p_return sm  newSction"><i class="icon iconfont icon-jiahao"></i>新建部门</router-link>
	  		</p>
	  		
	  	</div>
  	  <table class="sectionManage-table">
	  		<tr>
	  			<th><input @click="selectAll()" class="checkbox" :checked="check" type="checkbox"  /></th>
	  			<th>部门</th>
	  			<th>部门编号</th>
	  			<th>创建人</th>
	  			<th>创建日期</th>
	  			<th>备注</th>
	  			<th>操作</th>
	  		</tr>
	  		<tr v-for="(val, index) in tableData">
	  			<td><input @click="select(index)" class="checkbox" type="checkbox" :checked="val.check" name="" id="" value="" /></td>
	  			<td>{{val.name}}</td>
	  			<td>{{val.govNo}}</td>
	  			<td>{{val.creator}}</td>
	  			<td>{{val.createDate}}</td>
	  			<td><div>{{val.extra}}</div></td>
	  			<td>
	  				<button class="p_return remove sm " type="button" @click="openConfirm(val.id)">删除</button> 
	  				<button @click="update(val.id)" class="p_return sm update " type="button" >编辑</button>
	  			</td>
	  		</tr>
	  		
	  	</table>
	  	<div v-if="noData == true">暂时没有数据</div>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="selectParms.curPageIndex"
        layout="total, prev, pager, next, jumper"
        :total="selectParms.totalCount" :page-size="selectParms.pageSize">
      </el-pagination>
  	</div>
    
  	</div>
</template>

<script type="text/javascript">

import axios from "axios"
export default {
  name: 'component_name',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
    	
      check: false,
      i: 0,
      listData:null,
      noData:false,
      tableData:null,
      ids:[],
      selectParms:{
      	//当前显示页数
      	curPageIndex:1,
      	//每页显示条数
      	pageSize:null,
      	//总条数
      	totalCount:null,
      	
      }
    }
  },
  watch:{
  	selectParms:{
  		handler:function(val, oldVal){
     		 this.getData()
  		},
  		deep:true
  	},
  
  },
	mounted(){
     this.getData()

	},
  methods:{

  	getData(){
		 this.$xhr.get("/api/sysGov/getSysGovList",{
		 	  params:{
		 	  	page:this.selectParms.curPageIndex,
		 	  	limit:10
		 	  }
		 })
	      .then((res) => {
	      	this.tableData = res.data.pageList
	      	this.selectParms.totalCount = res.data.totalCount 
	      	this.selectParms.pageSize = res.data.pageSize 
           console.log(res.data.pageList)
	      	if(this.tableData==null){
	      		this.noData = true 
	      	}
	      	else{
	      		this.noData = false
	      	}
	      })
         .catch((req)=>{
         	console.log(111)
            this.tableData=[];
            this.noData = true 
         })

  	},
  	//删除提示框
  	openConfirm(i) {
        this.$confirm('是否确定要删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        	this.$xhr.delete(`/api/sysGov/delete?ids=${i}`)
		      .then((res)=>{
		      	this.getData()
		      })
		      .catch((req)=>{
		      	console.log("请求失败")
		      })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },


  	//全、反选
  	selectAll(){
  		this.check = !this.check
  		this.tableData.forEach((val,index,arr)=>{
  			 val.check = this.check
  			 this.ids.push(val.id)
  		})
  	},

  	//单选
  	select(i){
  		this.tableData[i].check = !this.tableData[i].check;
      let item = 0;
      this.tableData.forEach((val,index,arr)=>{
				 if(val.check == true){
						 	 item++
					}
				})
        if(item == this.tableData.length){
      		this.check = true;
      	}else{
      		this.check = false
      	}
      	
      	this.ids.push(this.tableData[i].id)
//    	console.log(this.tableData[i].check)
  	},
  	//选删
  	batchRemove(){
  		this.tableData.forEach((val, index, arr) => {
        if (val.check == true) {
          this.ids.push(this.tableData[index].id)
        }
      })
  		this.$confirm('是否确定要删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
		       this.$xhr.delete(`/api/sysGov/delete?ids=${this.ids.toString()}`)
				      .then((res)=>{
				      	console.log(111)
				      	this.getData()
				      })
				      .catch((req)=>{
				      	console.log("请求失败")
				      }) 
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
  		if(this.ids.length == 10){
  			  this.check = false
  		}
		      
  },
  	//分页
  	handleCurrentChange(val) {
  		this.selectParms.curPageIndex = val
     },
    //修改
    update(i){
      this.$router.push({path:'/system/sectionManage/add',query:{id:i}});
    },
  },

  computed:{

  }
}
</script>

<style lang="scss" scoped>
   .sectionManage-wrap{
   	 .sectionManage-top{
   	 	  height: 88px;
   	 	  box-sizing:border-box;
   	 	  padding-top: 45px;
   	 	  
   	 }
   	 .sectionManage-table{
   	 	  width: 1158px;
   	 	  border: 1px solid #E8EAF6;
   	 	  tr{
   	 	  	height: 40px;
   	 	  	text-align: center;
   	 	  	.checkbox{
   	 	  		background: #fff;
   	 	  	}
   	 	  	th{
   	 	  		background: #F3F6FE;
   	 	  		text-align: center;
   	 	  		font-weight: bold;
   	 	  		border: 1px solid #E8EAF6;
   	 	  	}
   	 	  	td{
   	 	  		border: 1px solid #E8EAF6;
   	 	 
   	 	  		i{
   	 	  			margin-right: 5px;
   	 	  		}
   	 	  	}
          td:nth-child(6){
   	 	  		width: 27%;
   	 	  		div{
   	 	  			width: 300px;
   	 	  			padding: 0 10px;
   	 	  			display:block;
   	 	  			white-space:nowrap; 
   	 	  			overflow:hidden; 
   	 	  			text-overflow:ellipsis;
   	 	  		}
 
   	 	  	}

   	 	  }
   	 }
   	 
   }
</style>



