<template>
  <div class="userManage-wrap">
    	<form action="" method="post">
    	
      <div class="userManage-content" >  	
       <div class="userManage-add">
  			 <div class="userManage-title">
  				<div class="title-input">
  					<p>
  						<label for="name">姓名：</label>
  						<el-input type="text" id="name" class="p_text" v-model="name" placeholder="请输入姓名"></el-input>
  					</p>
  					<p>
  						<label for="section">部门：</label>
  					   <el-select  v-model="section" placeholder="请选择部门" class="p_select">
				          <el-option v-for="(val1,index1) in sectionData" :value="val1.govNo"  :label="val1.name" :key="val1.id"></el-option>
				       </el-select>
  					</p>
  					<p>
  						<label for="role">角色：</label>
  						<el-select  v-model="role" placeholder="请选择角色" multiple class="p_select ">
				          <el-option v-for="(val2,index2) in roleData" :label="val2.name" :value="val2.id"  :key="val2.id" ></el-option>
				       </el-select>
  					</p>
  					
  				</div>
  				<div class="title-source">
  					<button class="p_return sm source" type="button" @click="source()"><i class="icon iconfont icon-sousuo"></i>查询</button>
  					<button class="p_return sm reset" type="reset"><i class="icon iconfont icon-zhongzhi"></i>重置</button>
  				</div>
	  		</div>
	     	<div class="clearfix userManage-top">
			  		<p class="fl">
			  		  <button class="p_return sm removes" type="button" @click="batchRemove()"><i class="icon iconfont icon-shanchu"></i>批量删除</button>
			  		</p>
			  		<p class="fr">
			  		  <router-link :to="{path:'/system/userManage/add'}" tag="button" class="p_return sm  newSction"><i class="icon iconfont icon-jiahao"></i>新建角色</router-link>

			  		</p>
		  		
		  	</div>

  	</div>
    
  	<table class="userManage-table">
  		<tr>
  			<th><input @click="selectAll()" :checked="check" type="checkbox" name="" id="" value="" /></th>
  			<th>人员编号</th>
  			<th>姓名</th>
  			<th>部门</th>
  			<th>职务</th>
  			<th>账号</th>
  			<th>角色</th>
  			<th>操作</th>
  		</tr>
  		<tr v-for="(val, index) in tableData ">
  			<td><input @click="select(index)" type="checkbox" :checked="val.check" /></td>
  			<td>{{val.userId}}</td>
  			<td>{{val.realName}}</td>
  			<td>{{val.govName}}</td>
  			<td>{{val.job}}</td>
  			<td>{{val.name}}</td>
  			<td><span>{{val.roleNames}}</span></td>
  			<td>
	  				<button class="p_return remove sm " type="button" @click="remove(index)">删除</button>
	  				<button @click="update(val.id)" class="p_return sm update " type="button" >编辑</button>
	  		</td>
  		</tr>
  		
  	</table>
  	<div v-if="noData == true">暂时没有数据</div>
     <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="selectParms.page"
        layout="total, prev, pager, next, jumper"
        :total="totalCount" :page-size="pageSize">
      </el-pagination>
   </div>
  </form>
  </div>
</template>

<script type="text/javascript">

import axios from "axios"
export default {
  name: 'component_name',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
    	item:0,
    	name:"",
    	section:'',
    	role:"",
      check:false,
      i:0,
      ids:[],
      noData:false,
      selectParms:{
      	page:1,
      	limit:10
      },
 
      //每页显示条数
      pageSize:null,
      //总条数
      totalCount:null,
      listData:[],
       sectionData:[],
       roleData:[],
       tableData:null
    }
  },
  mounted(){
  	this.getSection();
  	this.getListData()
  	this.getRole()
  },
  methods:{
  	//获取用户列表
  	getListData(){
					  this.$xhr.get("/api/sysUser/list",{
					 	  params:{
				      	page:this.selectParms.page,
				      	limit:10				 	  	
					 	  }
					 })
          .then((res) => {
          	this.tableData = res.data.pageList
	      	  this.totalCount = res.data.totalCount 
	      	  this.pageSize = res.data.pageSize 
	      	  console.log(res)
            if(this.tableData==null){
		      		this.noData = true 
		      	}
		      	else{
		      		this.noData = false
		      	}
          })
         .catch((req)=>{
            this.tableData=[];
            this.noData = true 
         })
  	},
  	//获取部门列表
  		getSection(){
					 this.$xhr.get("/api/sysGov/getSysGovList",{
					 })
          .then((res) => {
					this.sectionData=res.data.pageList
					console.log(res)
          })
         .catch((req)=>{
            console.log(111)
         })

  	},
  	//获取角色列表
  	getRole(){
					 this.$xhr.get("/api/sysRole/getSysRoleList",{
					 })
          .then((res) => {
					this.roleData=res.data.pageList
					console.log(res)
          })
         .catch((req)=>{
            console.log(111)
         })
  	},
  	//编辑
    update(i)
    {
      this.$router.push({path:'/system/userManage/add',query:{id:i}});
    },
    
  	//搜索
  	source(){


  	},

  	//单删
  	remove(i){
  		this.tableData.splice(i,1)
  	},
  	//全、反选
  	selectAll(){
  		this.check = !this.check
  		this.tableData.forEach((val,index,arr)=>{
  			 val.check = this.check
  			 
  		})
  	},
  	//单选
  	select(i){
  		this.tableData[i].check = !this.tableData[i].check;
  		this.ids.push(this.tableData[i].userId)

      let item = 0;
      this.tableData.forEach((val,index,arr)=>{
  			 if(val.check == true){
  			 	 item++
  			 }
  		})
        if(item == this.tableData.length){
      		this.check = true;
      	}else{
      		this.check = false
      	}
//    	console.log(this.tableData[i].userId)
  	},
  	//选删
  	batchRemove(){
		if(this.ids.length == 10){
			  this.check = false
		}
  		this.$confirm('是否确定要删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
		       axios.delete(`http://192.168.200.55:8090/api/sysUser/delete?ids=${this.ids.toString()}`)
				      .then((res)=>{
				      	console.log(res)
				      })
				      .catch((req)=>{
				      	console.log("请求失败")
				      }) 
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
  		
		      
  },
  	handleCurrentChange(val){
			this.selectParms.page = val
  	},
  },
	watch:{
  	dataParms:{
  		handler:function(val, oldVal){
     		 this.getListData()
  		},
  		deep:true
  	},
  
  },
  computed:{
  	
  	  dataParms() {
        return this.selectParms
      },
  }
}
</script>

<style lang="scss" scoped>
   .userManage-wrap{

   	.userManage-add{
   		height: 165px;
   		.userManage-title{
   			height: 125px;
   			box-sizing: border-box;
   			padding-top: 30px;
   			.title-input{
   				display: flex;
   				justify-content: space-between;
   			}
   			.title-source{
   				margin-top: 20px;
   				text-align: right;
   				.source{
   					background: #FF6400;
   					color: #fff;
   					border:none;
   					font-size: 14px;
   					margin-right: 16px;
   					i{
   						font-size: 16px;
   						margin-right: 8px;
	            margin-left: -6px;
   					}
   				}
          .reset{
          	color:#345DE7;
          	i{
   						font-size: 14px;
   						margin-right: 8px;
	            margin-left: -6px;
   					}
          }
   			}
   		}
	   	 .userManage-top{
	   	 	  height: 40px;
	   	 	  box-sizing:border-box;
	   	 	  /*padding-top: 45px;*/
	   	 	  
	   	 }
   	 }
   	 .p_text{
   	 	width: 265px;
   	 	}
   	 	.p_select{
   	 		width: 265px ;
   	 		& /deep/ input{
   	 			height: 31px !important;
   	 		}
   	 	}
   	 	.big{
   	 		width: 330px;
   	 	}
   	 .userManage-table{
   	 	  width: 1158px;
   	 	  border: 1px solid #E8EAF6;
   	 	  tr{
   	 	  	height: 40px;
   	 	  	text-align: center;
   	 	  	th{
   	 	  		background: #F3F6FE;
   	 	  		text-align: center;
   	 	  		font-weight: bold;
   	 	  		border: 1px solid #E8EAF6;
   	 	  	}
   	 	  	td{
   	 	  		border: 1px solid #E8EAF6;
   	 	  		span{
   	 	  			display: inline-block;
   	 	  			width: 120px;
   	 	  			overflow: hidden;
   	 	  			white-space: nowrap;
   	 	  			text-overflow: ellipsis;
   	 	  		}
   	 	  		/*.p_return {
   	 	  			background: #fff;
   	 	  			color:#FA4A21;
   	 	  			border:1px solid #FEDBD3;
   	 	  		}*/
   	 	  	}


   	 	  }
   	 }
   	 
   }
</style>



