<template>
  <div>
    <baseInfo :projectId='1'></baseInfo>
    
    <div class="warpper">
    	<Div class="tab">
    			<button class="iconfont icon-yingjian" :class="{active:tabStatus=='0'}" @click="handleClick('0')">数据铁笼软件采购</button>
    		<button class="iconfont icon-screen" :class="{active:tabStatus=='1'}" @click="handleClick('1')">数据铁笼硬件采购</button>    		 		
    	</Div>
    	<milepostStep :tab='tab'></milepostStep>
	
    	
    </div>
  </div>
</template>

<script type="text/javascript">
import baseInfo from '../components/baseinfo'
import milepostStep from '../components/milepostStep'
  export default {
    name: 'information',
    data() {
      return {
        active:5,
       	fileList: [],      	
      	tab:0, 
      	tabStatus:0,
	      curStep:'6',
       	ruleForm:{
       		showInput:false,
       		time1:'',
       		time2:'',
       		time3:'',
       		time4:'',
       		planFileList:['abc.doc','bbb.ppt','ccc.jpg']
       	}
      }
    },
    mounted() {
      //console.log(this.$route.params)
    },
    methods: {
   			handleClick(tabs) {
	        this.tabStatus = tabs;	    
     	 },  
    },
    components: {
      baseInfo,    
      milepostStep
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";

  
  .warpper{
  

  	.tab{
  		margin-top:20px;
  		button{
  			width: 176px;
			height: 46px;
			background:#eaeefd;	 
			font-size: 16px;
			font-weight: normal;
			font-stretch: normal;
			line-height: 45px;
			letter-spacing: 0px;
			color: #456cf0;
			border: none;
			&:before{
					margin-right: 5px;
			}
		    display: inline-block;
		    margin-right: -4px;
		    border-bottom: 4px solid #eaeefd;
			&.active{
				background: #fff;
				border-bottom: 4px solid #345de7;
			}
  		}
  	}
  }
  
  

</style>



