import Vue from 'vue'
import Router from 'vue-router'

import login from '@/pages/login'
import main from '@/pages/home/home.vue'

import testPage from '@/pages/home/test.vue'

import h5 from '@/pages/h5'
import overh5 from '@/pages/h5/overh5'
// 资产管理
import asset from '@/pages/asset'
import assetAdd from '@/pages/asset/add'
import assetDetail from '@/pages/asset/assetDetail'

//文档管理
import document from '@/pages/document'

// 数据统计
import statistics from '@/pages/statistics'
import bodyEcharts from '@/pages/statistics/pages/bodyEcharts'
import statisticsProject from '@/pages/statistics/pages/project'
import statisticsAddProject from '@/pages/statistics/pages/addProject'
import statisticsUnderwayPact from '@/pages/statistics/pages/underwayPact'
import statisticsPayOverdue from '@/pages/statistics/pages/payOverdue'
import statisticsProgressOverdue from '@/pages/statistics/pages/progressOverdue'

// 系统管理z
import project from '@/pages/project'
import projectMain from '@/pages/project/main'
import setupManage from '@/pages/project/pages/setupManage'
import capitalManage from '@/pages/project/pages/capitalManage'
import tenderManage from '@/pages/project/pages/tenderManage'
import contractManage from '@/pages/project/pages/contractManage'


//进度管理
import information from '@/pages/project/pages/information' //信息管理
import milepost from '@/pages/project/pages/milepost'//里程碑
import capitalpay from '@/pages/project/pages/capitalPay'//里程碑
import ready from '@/pages/project/pages/ready'//准备阶段
import ready1 from '@/pages/project/pages/ready1'//调研阶段
import ready2 from '@/pages/project/pages/ready2'//设计阶段
import ready3 from '@/pages/project/pages/ready3'//验收阶段
import ready4 from '@/pages/project/pages/ready4'//验收阶段
import over from '@/pages/project/pages/over'//移交阶段
import capitalDept from '@/pages/project/pages/capitalDept'//部门资金
import capitalSpecial from '@/pages/project/pages/capitalSpecial'//专项资金

// 系统管理
import system from '@/pages/system'
import sectionManage from "@/pages/system/pages/sectionManage"
import userManage from "@/pages/system/pages/userManage"
import milestone from "@/pages/system/pages/milestone"
import assetsClassManage from "@/pages/system/pages/assetsClassManage"
import logRecord from "@/pages/system/pages/logRecord"
import limitManage from "@/pages/system/pages/limitManage"
import addRole from "@/pages/system/pages/addRole"
import addLimit from "@/pages/system/pages/addLimit"
import addSection from "@/pages/system/pages/addSection"

Vue.use(Router)
export default new Router({
  // mode:"history",
  routes: [
    {
      path: '/',
      component: main,
      children: [
        {
          path: '',
          redirect: 'project',
        },
        {
          path: 'asset',
          name: 'asset',
          component: asset
        },
        {
          path: 'asset/add',
          name: 'assetAdd',
          component: assetAdd
        },
        {
          path: 'asset/assetDetail',
          name: 'assetDetail',
          component: assetDetail
        },
        {
          path: 'system',
          name: 'system',
          component: system,
          children: [
            {
              path: '/system',
              redirect: 'sectionManage',
            },
            {
              path: "sectionManage",
              name: "sectionManage",
              component: sectionManage,
            },
            {
              path: "sectionManage/add",
              name: "addSection",
              component: addSection,
            },
            {
              path: "userManage/add",
              name: "addRole",
              component: addRole,
            },
            {
              path: "limitManage/add",
              name: "addLimit",
              component: addLimit,
            },
            {
              path: "userManage",
              name: "userManage",
              component: userManage
            },
            {
              path: "milestone",
              name: "milestone",
              component: milestone
            },
            {
              path: "assetsClassManage",
              name: "assetsClassManage",
              component: assetsClassManage
            },
            {
              path: "logRecord",
              name: "logRecord",
              component: logRecord
            },
            {
              path: "limitManage",
              name: "limitManage",
              component: limitManage
            },
          ]
        },
        {
          path: 'project',
          name: 'project',
          component: project
        },
        {
          path: 'project',
          component: projectMain,
          children: [
            {
              path:"setupManage",
              name:"setupManage",
              component:setupManage
            },
            {
              path:"capitalManage",
              name:"capitalManage",
              component:capitalManage
            },
            {
              path:"tenderManage",
              name:"tenderManage",
              component:tenderManage
            },
            {
              path:"contractManage",
              name:"contractManage",
              component:contractManage
            },
            {
              path: 'document',
              name: 'document',
              component: document
            },
            {
              path: 'asset',
              name: 'asset1',
              component: asset
            },
            {
              path: 'asset/add',
              name: 'assetAdd1',
              component: assetAdd
            },
            {
              path: 'asset/assetDetail',
              name: 'assetDetail1',
              component: assetDetail
            },
            {
              path: "information",
              name: "information",
              component: information
            },
            {
              path: "capitalSpecial",
              name: "capitalSpecial",
              component: capitalSpecial
            },
            {
              path: "capitalDept",
              name: "capitalDept",
              component: capitalDept
            },
            {
              path: "milepost",
              name: "milepost",
              component: milepost
            },
            {
              path: "capitalpay",
              name: "capitalpay",
              component: capitalpay
            },
            {
              path: "ready",
              name: "ready",
              component: ready
            },
            {
              path: "ready1",
              name: "ready1",
              component: ready1
            },
            {
              path: "ready2",
              name: "ready2",
              component: ready2
            },
            {
              path: "ready3",
              name: "ready3",
              component: ready3
            },
             {
              path: "ready4",
              name: "ready4",
              component: ready4
            },
            {
              path: "over",
              name: "over",
              component: over,

            }
          ]
        },
        {
          path: 'statistics',
          component: statistics,
          children:[
            {
              path: "",
              name: 'statistics',
              component: bodyEcharts,
            },
            {
              path: "project",
              name: "statistics-project",
              component: statisticsProject,
            },
            {
              path: "addProject",
              name: "statistics-addProject",
              component: statisticsAddProject,
            },
            {
              path: "underwayPact",
              name: "statistics-underwayPact",
              component: statisticsUnderwayPact,
            },
            {
              path: "payOverdue",
              name: "statistics-payOverdue",
              component: statisticsPayOverdue,
            },
            {
              path: "progressOverdue",
              name: "statistics-progressOverdue",
              component: statisticsProgressOverdue,
            }
          ]
        },
      ]

    },
    {
      path: '/testPage',
      name: 'testPage',
      component: testPage
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    // h5 的页面 待定
    {
      path: '/h5/assetif/:id',
      name:'h5',
      component: h5
    },
     {
      path: "/overh5/:id",
      name: "overh5",
      component: overh5
		 },
  ],
  linkActiveClass: 'active'
})




