<template>
  <div class="login-wrap">
    <div class="login-title">
      <!--<h1>柳州市信息化建设管理中心项目管理平台</h1>-->
    </div>
    <div class="small-wrap">
      <div class="login-container">
        <form action="">
          <h2>用户登录</h2>
          <input type="text" placeholder="请输入用户名">
          <i class="iconfont icon-yonghuming"></i>
          <input type="text" placeholder="请输入密码">
          <i class="iconfont icon-mima"></i>
          <div class="btn-box">
            <input type="submit" class="submit" value="提交">
            <input type="reset" class="reset" value="重置">
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript" scoped>
  import {xhr} from '../../api/api.js'
  import axios from 'axios'

  export default {
    name: 'login-wrap',
    props: {},
    data() {
      return {
        activenum: 2
      }
    },
    mounted() {
      xhr.get('/user/login', {data: {username: 'admin', password: 'shdf0925'}}).then(res => {
        console.log(res);
      }).catch(err => {
        console.log(err);
      })
    },
    components: {},
  }
</script>

<style lang="scss" scoped>
  @import "./../../assets/css/variable.scss";
  @import "./../../assets/css/mixin.scss";

  .login-wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url("../../assets/img/bg.png") no-repeat;
    color: #666;
    background-size: cover;
    overflow: hidden;
    .login-title {
      width: 1240px;
      height: 218px;
      background: url('../../assets/img/bg-title4.png');
      margin: 0 auto;
    }
    .small-wrap {
      width: 1032px;
      height: 660px;
      margin: -2% auto;
      background: url("../../assets/img/bg4.png") no-repeat;
      color: #666;
      background-size: cover;
      .login-container {
        position: absolute;
        left: 330px;
        right: 350px;
        top: 320px;
        width: 295px;
        height: 344px;
        margin: 0 auto;
        padding: 40px 35px;
        border: 1px solid #ACBFF2;
        box-shadow: 0 0 10px #ACBFF2;
        border-radius: 10px;
        background: rgba(254, 254, 253, .3);
        margin-top: 1%;
        box-sizing: border-box;
        h2 {
          font-size: 20px;
          font-weight: bold;
          color: #345de7;
          text-align: center;
          margin-bottom: 25px;
        }
        .icon-yonghuming {
          position: absolute;
          top: 100px;
          left: 40px;
          color: #345de7;
          font-size: 18px;
          font-weight: bold;
        }
        .icon-mima {
          position: absolute;
          top: 154px;
          left: 40px;
          color: #345de7;
          font-size: 18px;
          font-weight: bold;
        }
        input {
          width: 100%;
          height: 40px;
          border-bottom: 1px solid #345de7;
          outline: none;
          border-top: 0;
          border-left: 0;
          border-right: 0;
          margin-bottom: 15px;
          background: transparent;
          color: #666;
          padding-left: 35px;
          box-sizing: border-box;
        }
        .btn-box {
          margin-top: 25px;
          .submit {
            width: 100%;
            height: 32px;
            background-color: #345de7;
            border-radius: 4px;
            outline: 0;
            border: 0;
            margin-bottom: 15px;
            color: #fff;
            letter-spacing: 10px;
            padding: 0;
          }
          .submit:hover {
            cursor: pointer;
            background: #121de7;
          }
          .reset {
            width: 100%;
            height: 32px;
            border-radius: 4px;
            outline: 0;
            background: #fff;
            border: solid 1px #345de7;
            color: #345DE7;
            letter-spacing: 10px;
            padding: 0;
          }
          .reset:hover {
            cursor: pointer;
          }
        }
      }
    }
  }
</style>



