<template>
  <div class="limitManage-wrap">
  	<div class="limitManage-content" v-show="show">

	  	<div class="clearfix limitManage-top">
	  		<p class="fl">
	  		  <button class="p_return sm removes" @click="batchRemove()"><i class="icon iconfont icon-shanchu"></i>批量删除</button>
	  		</p>
	  		<p class="fr">
	  		  <router-link :to="{path:'/system/limitManage/add'}" tag="button" class="p_return sm  newSction"><i class="icon iconfont icon-jiahao"></i>新建角色</router-link>
	  		</p>

	  	</div>
	  	<table class="limitManage-table">
  		<tr>
  			<th><input @click="selectAll()" :checked="check" type="checkbox" name="" id="" value="" /></th>
  			<th>角色</th>
  			<th>创建人</th>
  			<th>创建日期</th>
  			<th>权限</th>
  			<th>操作</th>
  		</tr>
  		<tr v-for="(val, index) in tableData">
  			<td><input @click="select(index)" type="checkbox" :checked="val.check" /></td>
  			<td>{{val.name}}</td>
  			<td>{{val.creator}}</td>
  			<td>{{val.createDate}}</td>
  			<td>{{val.moduleNames}}</td>
  			<td>
	  				<button class="p_return sm remove" type="button" @click="remove(val.id)">删除</button>
            <button @click="update(val.id)" class="p_return sm update " type="button" >编辑</button>
	  			</td>
  		</tr>

  	</table>
      <div v-if="noData == true">暂时没有数据</div>
  	<el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="curPageIndex"
        layout="total, prev, pager, next, jumper"
        :total="totalCount" :page-size="pageSize">
      </el-pagination>
    </div>

  </div>
</template>

<script type="text/javascript">


export default {
  name: 'component_name',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      check:false,
      i:0,
      show:true,
      role:"",
      name:"",
      date:"",
      Num:"",
      noData:false,
      //每页显示条数
      pageSize:3,
      //当前默认页数
      curPageIndex:1,
      totalCount: 1,
      tableData:[],
      detelelist:[]
    }
  },
  watch:{
    curPageIndex:{
      handler:function(val, oldVal){
        this.getData()
      },
      deep:true
    },
 
  },
	mounted(){
    this.getData();
	},
  methods:{
    getData(){
      this.$xhr.get('/api/sysRole/getSysRoleList',{
        params:{
          page:this.curPageIndex,
          limit:10
        }
      })
        .then((res) => {
          this.tableData=res.page.list;
          this.pageSize = res.page.pageSize
          this.totalCount = res.page.totalCount
//        console.log(res)
          if(this.selectParms.tableData==[]){
	      		this.noData = true 
	      	}
	      	else{
	      		this.noData = false
	      	}
        })
        .catch((req)=>{
          this.tableData=[];
            this.noData = true
        })

    },
  	//单删
  	remove(i){
      this.$confirm('是否确定要删除', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$xhr.delete(`/LzPmAssetController/delete?ids=${i}`)
          .then((res)=>{
            this.getData();
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
          })
          .catch((req)=>{
            console.log(res)
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
  	},
    //修改
    update(i)
    {
      this.$router.push({path:'/system/limitManage/add',query:{id:i}});
    },
  	//全、反选
  	selectAll(){
  		this.check = !this.check
  		this.tableData.forEach((val,index,arr)=>{
  			 val.check = this.check

  		})
  	},
  	//单选
  	select(i){
  		this.tableData[i].check = !this.tableData[i].check;

      let item = 0;
      this.tableData.forEach((val,index,arr)=>{
  			 if(val.check == true){
  			 	 item++
  			 }
  		})
        if(item == this.tableData.length){
      		this.check = true;
      	}else{
      		this.check = false
      	}
  	},
  	//选删
  	batchRemove(){
      this.detelelist = [];
      this.tableData.forEach((val, index, arr) => {
        if (val.check == true) {
          this.detelelist.push(this.tableData[index].id)
        }
      })
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$xhr.delete(`/api/sysRole/delete?ids=${this.detelelist.toString()}`)
          .then((res)=>{
            this.getData();
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
          })
          .catch((req)=>{
            console.log(res)
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
  	},
  	handleCurrentChange(val) {
      this.curPageIndex = val
  	},


  },
  computed:{

  }
}
</script>

<style lang="scss" scoped>
   .limitManage-wrap{
   	 .limitManage-top{
   	 	  height: 88px;
   	 	  box-sizing:border-box;
   	 	  padding-top: 45px;

   	 }
   	 .limitManage-table{
   	 	  width: 1158px;
   	 	  border: 1px solid #E8EAF6;
   	 	  tr{
   	 	  	height: 40px;
   	 	  	text-align: center;
   	 	  	th{
   	 	  		background: #F3F6FE;
   	 	  		text-align: center;
   	 	  		font-weight: bold;
   	 	  		border: 1px solid #E8EAF6;
   	 	  	}
   	 	  	td{
   	 	  		border: 1px solid #E8EAF6;
   	 	  	}
   	 	  	td:nth-child(5){
   	 	  		width: 37%;
   	 	  		div{
   	 	  			width: 430px;
   	 	  			padding: 0 10px;
   	 	  			display:block;white-space:nowrap; overflow:hidden; text-overflow:ellipsis;
   	 	  		}

   	 	  	}

   	 	  }
   	 }

   }
</style>



