<template>
  <div class="logRecord-wrap">
  	<form class="logRecord-top">
  		<p>
  			<label for="name">操作人：</label>
  			<el-input id="name" class="p_text" v-model="name" placeholder="请输入姓名"></el-input>
  		</p>
  		<p>
  			<label for="time">录入日期：</label>
        <el-date-picker
		      v-model="date"
		      type="date"
		      class="p_date"
		      placeholder="选择日期">     
		    </el-date-picker>
  		</p>
  		<div class="title-source">
  					<button class="p_return sm source" type="button" @click="source()"><i class="icon iconfont icon-sousuo"></i>查询</button>
  					<button class="p_return sm reset" type="reset"><i class="icon iconfont icon-zhongzhi"></i>重置</button>
  				</div>
  	</form>
  	<table class="logRecord-table">
  		<tr>
  			<th>编号</th>
  			<th>创建人</th>
  			<th>操作页面</th>
  			<th>操作类型</th>
  			<th>操作项目/合同</th>
  			<th>操作时间</th>
  		</tr>
  		<tr v-for="(val, index) in filterList">
  			<td>{{val.sectionNum}}</td>
  			<td>{{val.operator}}</td>
  			<td><div>{{val.operatorPage}}</div></td>
  			<td>{{val.operatorType}}</td>
  			<td>{{val.operand}}</td>
  			<td>{{val.operatordate}}</td>
  		</tr>
  		
  	</table>
  	<el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="curPageIndex"
        layout="total, prev, pager, next, jumper"
        :page-size="pageSize"
        :total="tableData.length" >
      </el-pagination>
  </div>
</template>

<script type="text/javascript">

export default {
  name: 'component_name',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      check:false,
      i:0,
      pageSize:3,
      curPageIndex:1,
      listData:[],
      name:'',
      date:"",
      time:'',
      key0:'',
      key1:'',
      key2:'',
       tableData:[
          {
          	sectionNum:"2010809123",
          	operator:"张欣1",
          	operatorPage:"资金管理>资金追加",
          	operatorType:"提交",
          	operand:"数据铁笼",
          	operatordate:"2017-09-10 15:43:12",
          },
          {
          	sectionNum:"2010809123",
          	operator:"张欣2",
          	operatorPage:"资金管理>资金追加",
          	operatorType:"提交",
          	operand:"数据铁笼",
          	operatordate:"2017-09-10 15:43:12",
          },
          {
          	sectionNum:"2010809123",
          	operator:"张欣3",
          	operatorPage:"资金管理>资金追加",
          	operatorType:"提交",
          	operand:"数据铁笼",
          	operatordate:"2017-09-10 15:43:12",
          },
          {
          	sectionNum:"2010809123",
          	operator:"张欣",
          	operatorPage:"资金管理>资金追加",
          	operatorType:"提交",
          	operand:"数据铁笼",
          	operatordate:"2017-09-11 15:43:12",
          },
          
         
       ]
    }
  },
  watch:{
  	tableData:{
  		handler:function(val, oldVal){
			this.handleCurrentChange(this.curPageIndex)
//      console.log(val)
  		},
  		deep:true
  	}
  },
	mounted(){
		this.handleCurrentChange(this.curPageIndex)
	},
  methods:{
  	handleCurrentChange(val){
  		this.listData = this.tableData.slice((val-1)*this.pageSize,val*this.pageSize)
//		console.log(this.listData)
  	},
    source(){
    	this.key0 = this.name
    	this.key1 = this.time
    }
  },
  computed:{
  	filterList:function(){
  		return this.listData.filter((val,index,arr)=>{
  			 if(this.key0 != ""){
  			 	   return val.operator.indexOf(this.key0) !=-1
  			 }
  			 if(this.key1 != ""){
  			 	   return val.operatordate.indexOf(this.key1) !=-1
  			 }
			 else{
			 	return arr
			 }
  		})
  	}
  }
}
</script>

<style lang="scss" scoped>
   .logRecord-wrap{
     .logRecord-top{
     	height: 80px;
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
      padding-top: 30px;
      .p_text{
      	width: 265px;
      }
      .source{
   					background: #FF6400;
   					color: #fff;
   					border:none;
   					font-size: 14px;
   					margin-right: 16px;
   					i{
   						font-size: 16px;
   						margin-right: 8px;
	            margin-left: -6px;
   					}
   				}
			.reset{
      	color:#345DE7;
      	i{
					font-size: 14px;
					margin-right: 8px;
            margin-left: -6px;
				}
      }
      .p_date{
      	width: 265px;
      	.el-input__suffix{
      		right:22px;
      		.el-input__icon{
      			line-height: 12px;
      		}
      	}
      }
     }
   	 .logRecord-table{
   	 	  width: 1158px;
   	 	  border: 1px solid #E8EAF6;
   	 	  tr{
   	 	  	height: 40px;
   	 	  	text-align: center;
   	 	  	th{
   	 	  		background: #F3F6FE;
   	 	  		text-align: center;
   	 	  		font-weight: bold;
   	 	  		border: 1px solid #E8EAF6;
   	 	  	}
   	 	  	td{
   	 	  		border: 1px solid #E8EAF6;
   	 	  	}
   	 	  	td:nth-child(3){
   	 	  		width: 27%;
   	 	  		div{
   	 	  			width: 300px;
   	 	  			padding: 0 10px;
   	 	  			display:block;
   	 	  			white-space:nowrap; 
   	 	  			overflow:hidden; 
   	 	  			text-overflow:ellipsis;
   	 	  		}
 
   	 	  	}
   	 	  }
   	 }
   }
</style>



