<template>
  <div class="clearfix milestone-wrap">
    <div class="milestone-left fl">
     <ul class="list-wrap">
     	<li v-for="(val,index) in listData" @click="tab(val)" :class="{active:val.id==item.id}"><i class="icon iconfont" :class="val.icon"></i>{{val.name}}</li>
     </ul>
    </div>
    <div class="milestone-right fr">
    	<el-form  :model="ruleForm" :rules="rules" ref="ruleForm" action="" method="post">
	    		<div class="right-top">
	    			<div class="right-content clearfix" >
	    				<label class="fl" for="name">{{item.name}}附件：</label>
			    		<ul class="fr ">
			    			<li v-for="(val,index) in nameData" class="clearfix">
			    				<el-input type="text" placeholder="请输入条件" id="name" class="p_text" v-model="val.attachmentName" ></el-input>
			    				<button type="button" class="remove fr" :disabled="check == true"  @click="remove(index)"><i class="iconfont icon-jianhao" ></i></button>
			    			</li>
			    		</ul>
	    			</div>	
	    		</div>
	    		<div class="right-bottom">
	    			<button type="button" class="p_submit" @click="applyConfig()"><i class="iconfont icon-submit"></i>应用</button>
	    			<button type="button" @click="add()" class="p_save"><i class="iconfont icon-baocun"></i>添加</button>
	    		</div>
    	</el-form>
    </div>
  
  </div>
</template>

<script type="text/javascript">

export default {
  name: 'component_name',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
    	item:{},
    	check:false,
    	ruleForm:{
    		name:'主实施计划',
    	},
    	rules:{
      	name: [
          { required: true, message: '请输入部门名称', trigger: 'blur' },
        ],

      },
       listData:[
        {id:21,icon:"icon-addtable", name:"新建项目",active:true},
        {id:20,icon:"icon-tjzhuanxiangzijin",name:"添加专项资金",active:false},
        {id:22,icon:"icon-zhaobiaoxingxi",name:"填写招标信息",active:false},
        {id:23,icon:"icon-iconfont-edit",name:"填写合同信息",active:false},
        {id:7,icon:"icon-zhunbeijieduan",name:"准备阶段",active:false},
        {id:8,icon:"icon-diaoyan",name:"调研阶段",active:false},
        {id:9,icon:"icon-design",name:"设计阶段",active:false},
        {id:10,icon:"icon-shishijieduan",name:"实施阶段",active:false},
        {id:11,icon:"icon-acceptance",name:"验收阶段",active:false},
        {id:13,icon:"icon-wenjian",name:"准备阶段（监理合同）",active:false},
        {id:14,icon:"icon-diaoyanjl",name:"调研阶段（监理合同）",active:false},
        {id:15,icon:"icon-shejijl",name:"设计阶段（监理合同）",active:false},
        {id:16,icon:"icon-shishijl",name:"实施阶段（监理合同）",active:false},
        {id:17,icon:"icon-yanshoujl",name:"验收阶段（监理合同）",active:false},
       ],
      nameData:[
        {
        	attachmentName:""
        }
      ]
    }
  },
  watch:{
  	nameData:function(val,oldVal){
		if(this.nameData.length>1){
			this.check = false
		}

  	}
  },
  mounted(){
     this.item = this.listData[0];
  },
  methods:{
  	tab(item){
			if(this.item.id != item.id){
				this.$xhr.get('/api/sysPmStage/getAttachmentListByID?id='+item.id.toString()).then((res)=>{
//					console.log(res)
					if(res.code == "1"){
						this.nameData = [];
						for(var obj of res.attachmentList){
							this.nameData.push({attachmentName:obj.attachmentName});
						}
						if(this.nameData.length==0){
							this.nameData.push({attachmentName:''});
						}
					}
				});
				this.item = item;
			}
  	},
    add(){
    	if(this.nameData[this.nameData.length-1].attachmentName != ""){
    		this.nameData.push({attachmentName:''});
    	}
    	else{
    		this.$message({
            type: 'success',
            message: '请输入条件!'
          });
    	}
    },
		applyConfig(){
			let list = [];
			for(var obj of this.nameData){
				if(obj!=""){
					list.push(obj);
				}
			}
			this.$xhr.post('/api/sysPmStage/addLzPmStage',{stageNo:this.item.id.toString(),pmStageAttachmentList:list}).then((res)=>{
				if(res.code == "1"){              
					this.$message({type: 'success',message: '操作成功!'});
				}
			});
		},
    remove(i){
        this.$confirm('是否确定要删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        
	
        		this.nameData.splice(i,1)
//      		console.log(this.nameData)
        
        	
    	if(this.nameData.length>0){
    		this.check = false
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
      }else{
      	this.check = true
      	this.nameData.push({attachmentName:''})
      	this.$message({
            type: 'info',
            message: '已全部删除'
          });
      }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
    }
  }
}
</script>

<style lang="scss" >
   .milestone-wrap{
   	height: 628px;
   	 .milestone-left{
   	 	margin-left: -20px;
   	 	 .list-wrap{
   	 	 	width: 212px;
   	 	 	height: 628px;
   	 	 	box-sizing: border-box;
   	 	 	background: #EEF2F7;
   	 	 	border: 4px solid #E2E6EF;
   	 	 	border-top: none;
   	 	 	&>li{
   	 	 		cursor: pointer;
   	 	 		font-size: 14px;
   	 	 		height: 42px;
   	 	 		line-height: 42px;
   	 	 		background: #EEF2F7;
   	 	 		position: relative;
   	 	 		width: 102%;
   	 	 		border-right: 4px solid #E2E6EF;
   	 	 		box-sizing: border-box;
   	 	 		i{
   	 	 			color:#345DE7;
   	 	 			display: inline-block;
   	 	 			margin-left: 21px;
   	 	 			margin-right: 8px;
   	 	 			font-size: 16px;
   	 	 		}
   	 	 	}
   	 	 	&>li:first-child{
   	 	 		border-top: 4px solid #E2E6EF;
   	 	 	}
   	 	 	&>li:before{
   	 	 		position: absolute;
   	 	 		bottom: 0;
   	 	 		left:0;
   	 	 		display: block;
   	 	 		content: "";
   	 	 		width: 100%;
   	 	 		height:1px;
   	 	 		background: linear-gradient(to right,#fff,#BFC6D0,#fff);
   	 	 	}
   	 	 	&>li:hover{
   	 	 		color:#F16C00;
   	 	 		i{
   	 	 			color:#F16C00;
   	 	 		}
   	 	 		&:before{
   	 	 			display: none;
   	 	 		}
   	 	 	}
   	 	 	&>.active{
   	 	 		color:#F16C00;
   	 	 		background: #fff;
   	 	 		border: none;
   	 	 		/*border-bottom: 4px solid #E2E6EF;*/
   	 	 		i{
   	 	 			color:#F16C00;
   	 	 		}
   	 	 		&:before{
   	 	 			display: none;
   	 	 		}
   	 	 	}
   	 	 	&>.active:first-child{
   	 	 		border-top: none;
   	 	 	}
        
   	 	 }
   	 }
   	 .milestone-right{
   	 	width: 968px;
   	 	height: 628px;
   	 	.right-top{
         .right-content{
         	  width: 560px;
         	  margin: 30px auto;
         	  label{
         	  	height: 45px;
         	    line-height: 45px;
         	  	width: 190px;
         	  }
         	  ul{
         	  	width: 350px;
         	  	li{
         	  		height: 45px;
         	  		line-height: 45px;
         	  		
         	  		.remove{
         	  			line-height: 45px;
         	  			border:none;
         	  			display: inline-block;
         	  			outline: none;
	         	  		.icon-jianhao{
	         	  			cursor: pointer;
	         	  			color:#345DE7;
	         	  			font-size: 30px;
	         	  			float: right;
	         	  			background: #fff;
	         	  		}
	         	  		.icon-jianhao:hover{
	         	  			color: rgba(52,93,231,0.7);
	         	  		}
         	  			
         	  		}
         	  	}
         	  }
         }
   	 		.p_text{
   	 			width: 300px;
   	 		}

   	 	}
   	 	.right-bottom{
   	 		margin-left: 420px;
   	 		/*text-align: center;*/
        .p_save{
        	margin-left: 27px;
        }
   	 	}
   	 }
   }
</style>



