<template>
<div>
  <div class="progressInfo">
      <div class="p_title">
        <i class="iconfont icon-chart58"></i><p>项目进度</p>
      </div>
      <div class="base-content"> 
        	<Div class="progress-left" >
        		 <h3>项目进度</h3>
        		<div class="progress-status-box">
        			<span>计划：</span>
        			<div class="progress-status">
        				<el-tooltip v-for="(item,index) in list.planList" :key="index"  class="item" effect="light"   placement="right">
	        			 	<div slot="content" class="slot-cont"><span>{{item.name}}</span><br/>开始时间：{{item.startTime}}<br/>结束时间：{{item.endTime}}</div>
						      <div class="progress-name" :class="{blue:item.status==1,green:item.status==2}"></div>
						    </el-tooltip>
        			</div>
        			
        			
        			
        			
        		</div>
        		<div class="progress-status-box">
        			<span>实际：</span>
        			<div class="progress-status">
        				<el-tooltip   v-for="(item,index) in list.realList"  :key="index" class="item" effect="light"   placement="right">
	        			 	<div slot="content" class="slot-cont"><span>{{item.name}}</span><br/>开始时间：{{item.startTime}}<br/>结束时间：{{item.endTime}}</div>
						      <div class="progress-name" :class="{red:item.status==1,green:item.status==2}"></div>
						    </el-tooltip>
        			</div>
        		</div>
        	<transition name="mybox">
				<div class="box"    v-show="boxshow" >
        		 <table> 
        			<tr>        				
        				<th>进度</th>
        				<th>计划时间</th>
        				<th>实际完成时间</th>        		 
        				<th>扣分情况</th>
        			</tr>
        			<tr v-for="(v,i) in list.progressMan" :key="i">        			
        				<td>{{v.name}}</td>
        				<td>{{v.startTime}}</td>
        				<td v-if='v.endTime'>{{v.endTime}}</td>
        				<td v-else>——</td>
        				<td v-if='v.score' :class="{red:v.score < 0}">{{v.score}}</td>
        				<td v-else>——</td>
        			</tr>
        			 
        		</table>
        	</div>
      		</transition>
        	</Div> 
        	<Div class="progress-right"   >
        		 <h3>付款进度</h3>
        		<div class="progress-status-box">
        			<span>计划：</span>
        			<div class="progress-status">
        				<div class="progress-name">
        				 <el-tooltip class="item" effect="light"   placement="right">
	        			 	<div slot="content" class="slot-cont">计划完成百分比{{parseInt(list.planPay.planMoney/list.planPay.planTotleMoney*100)}}%</div>
						   			   <div class="plan-progress" :style="{ width: parseInt(list.planPay.planMoney/list.planPay.planTotleMoney*100) + '%' }"></div>
						    </el-tooltip>						   	
						   </div>						   
						    <div class="plan-info">
						    	<div>计划付款：¥{{list.planPay.planMoney}}</div>
						    	<div>计划付款总额：¥{{list.planPay.planTotleMoney}}</div> 
						   	</div>
						   	
        			</div>
        		</div>
        		<div class="progress-status-box">
        			<span>实际：</span>
        			<div class="progress-status">
        				<div class="progress-name">
	        				 <el-tooltip class="item" effect="light"   placement="right">
		        			 	<div slot="content" class="slot-cont">实际完成百分比：{{parseInt(list.realPay/list.planPay.planTotleMoney*100)}}%</div>
							      	<div class="real-progress" :style="{ width: parseInt(list.realPay/list.planPay.planTotleMoney*100) + '%' }"></div>
							    </el-tooltip>
							    
						    </div>
						    <div style="color: #ff7900;font-weight: bold;">实际付款：¥{{list.realPay}}</div>
						    
        			</div>
        		</div>
         
    <transition name="mybox">
						<div class="box"  v-show="boxshow" >
        		 <table>
        		
        			<tr>
        				<th>进度</th>
        				<th>计划付款完成日期</th>
        				<th>计划支付</th>
        				<th>实际支付</th>
        				<th>扣分情况</th>
        			</tr>
						 <tr v-for="(v,i) in list.payMan">        			
        				<td>{{v.name}}</td>
        				<td>{{v.startTime}}</td>
        				<td>{{v.planPay}}</td>
        				<td v-if='v.realPay'>{{v.realPay}}</td>
        				<td v-else>——</td>
        				<td v-if='v.score' :class="{red:v.score < 0}">{{v.score}}</td>
        				<td v-else>——</td>
        			</tr>
 
 
        		</table>
        	</div>
        	</transition>
        	</Div> 
        	
        	
        	
        	
        	<div class="see-detail">
        		<span @click="togglebox" class="iconfont icon-iconfontdown2" :class="{changeIcon : boxshow}">查看进度详情</span>				 
 
        	</div>
      </div> 
  </div>
  
	
</div>  
</template>

<script type="text/javascript">
  export default {
    name: 'project-baseinfo',
    props: {
      projectId: {
      	type:Number,
        default: ''
      }
    },
    data() {
      return {
				show:false,
				hide:false,
				centerDialogVisible:false,
				boxshow:false,
				ruleForm:{
					user:'',
					payType:'0',
					payStep:'',
					money:'',
       		date1:'',
       		payNumber:'',
       		desc:''
       },
      	rules: {
          user: [
            { required: true, message: '请输入申请人', trigger: 'blur' },
           /* { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }*/
          ],
        	payType: [
            { required: true, message: '请选择付款类型', trigger: 'change' }
          ], 
          money: [
            { required: true, message: '请输入申请金额', trigger: 'blur' },
           /* { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }*/
          ],
        	payStep: [
            { required: true, message: '请选择付款阶段', trigger: 'change' }
          ],
         	date1: [
            { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
          ], 
          payNumber: [
            { required: true, message: '请输入支付流水号', trigger: 'blur' },
           /* { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }*/
          ],
          /* date2: [
            { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
          ],
          type: [
            { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
          ],
          resource: [
            { required: true, message: '请选择活动资源', trigger: 'change' }
          ],*/
          desc: [
            { required: true, message: '请填写备注', trigger: 'blur' }
          ]
       },
				list:{
					 planList:[
					 	 {name:'准备阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:1},
					 	 {name:'调研阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:2},
					 	 {name:'设计阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:0},
					 	 {name:'实施阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:0},
						 {name:'验收阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:0},
					 	 {name:'移交阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:0}
					 ],
					 realList:[
					 	 {name:'准备阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:1},
					 	 {name:'调研阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:2},
					 	 {name:'设计阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:0},
					 	 {name:'实施阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:0},
						 {name:'验收阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:0},
					 	 {name:'移交阶段',startTime:'2017-11-20',endTime:'2017-11-20',status:0}
					 ],
					 progressMan:[
						 {name:'准备阶段',startTime:'2017-11-20',endTime:'2017-11-20',score:'-1'},
					 	 {name:'调研阶段',startTime:'2017-11-21',endTime:'2017-11-20',score:'-2'},
					 	 {name:'设计阶段',startTime:'2017-11-22',endTime:'',score:'0'},
					 	 {name:'实施阶段',startTime:'2017-11-23',endTime:'',score:'0'},
						 {name:'验收阶段',startTime:'2017-11-24',endTime:'',score:'0'},
					 	 {name:'移交阶段',startTime:'2017-11-25',endTime:'',score:''}
					 ],
					 planPay:{
					 	planTotleMoney:240000,
					 	planMoney:58000
					 },
					 realPay:48000,
					payMan:[
						 {name:'准备阶段',startTime:'2017-11-20',planPay:'2000',realPay:'1000',score:'-1'},
					 	 {name:'调研阶段',startTime:'2017-11-21',planPay:'1000',realPay:'11000',score:'-2'},
					 	 {name:'设计阶段',startTime:'2017-11-22',planPay:'2000',realPay:'1000',score:'0'},
					 	 {name:'实施阶段',startTime:'2017-11-23',planPay:'2000',realPay:'1000',score:'0'},
						 {name:'验收阶段',startTime:'2017-11-24',planPay:'2000',realPay:'1000',score:'0'},
					 	 {name:'移交阶段',startTime:'2017-11-25',planPay:'2000',realPay:'1000',score:''}
					 ],
					aleryPay:[
						{id:'10086',insertTime:'2017-10-23',user:'张三',name:'付款阶段',payType:'阶段付款计划',payMoney:'8000',payTime:'2017-11-03 14:41:34',status:'1'},
						{id:'10010',insertTime:'2017-10-24',user:'李四',name:'付款阶段',payType:'阶段付款计划',payMoney:'8000',payTime:'2017-11-03 14:41:34',status:'0'},
						{id:'10000',insertTime:'2017-10-23',user:'张三',name:'付款阶段',payType:'阶段付款计划',payMoney:'8000',payTime:'2017-11-03 14:41:34',status:'1'}
					
					]
					 
					 
				}
      }
    },
    mounted() {
       // this.init();
    },
    methods: {
    	
    	change(){
    		this.show=!this.show
    		 if(!this.show){
    		 	this.hide=true
    		 }else{
    		 	this.hide=false
    		 }
    	},
    	togglebox:function(){
         	this.boxshow = !this.boxshow;
      	}
    },
    components: {

    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
 
  .see-detail{
 		cursor:pointer;
  	span{  	
  		color: #1b9aff;
  		font-size:14px;	
  		&:before{
  			transition:all 1s;
  			display:inline-block;
  			font-size:14px;	
  		}
  		&.changeIcon{
  			&:before{
  				transform: rotate(180deg);
  			}  			
  		}	
  	}
  }

  .progressInfo{
    padding: 20px;
    background: #fff;
    box-sizing: border-box;
    font-size: 14px;
    
    .base-content{
      padding: 0 20px;
      display:flex;
      justify-content: center;
      flex-wrap: wrap;
      
      
    
      table{
      	border-top: 1px solid #ccc;
      	border-right:1px solid #ccc;
      	width: 100%;
      	th{
      		color: #000;
      		font-weight: bold;
      		background: #f3f6fe;
      		height: 40px;
      	}
      	td,th{
      		border-left: 1px solid #ccc;
      		border-bottom:1px solid #ccc;
      		text-align: center;
      	}
      	td{
      		height: 40px;
      		padding-left: 10px;
      		padding-right: 10px;
      		&.red{
      			color: #ff2300;
      		}
      	 
      	}
      }
      
    }
    .el-row{
      line-height: 30px;
    }
    .p_title{
      margin-bottom: 20px;
    }
    .lable{
    	
      display:inline-block;
      width: 100px;
      color: #777777;
    }
    .txt{
      display:inline-block;
      color: #303030;
    }
 
		h3{
    		font-size: 14px;
    		color: #666;
    		font-weight: bold;
    	 
    		margin-bottom: 8px !important;
    	}
  
  .progress-right  {
  	margin-left: 35px;
  	.progress-status{
 				flex-wrap: wrap;
		    justify-content: space-between;
		    width: 430px;
		  	margin-bottom: 20px;
  		.plan-info{
  			width: 420px;
  			display: flex;
  			justify-content: space-between;	
  			div:first-child{
  				color: #5b81ff;
  				font-weight: bold;
  			}
  		}
  		.progress-name{
  		
  			flex-grow: 1;
  			border-radius: 15px;
  			width: 430px !important;
  			overflow: hidden;
  			.real-progress{
  				background: #ff9100;
  				border-radius: 15px;
  				height: 16px;
  			}.plan-progress{
  				background: #5b81ff;
  				border-radius: 15px;
  				height: 16px;
  				transition: all 1s;
  			}
  		}
  	}
  }
   .progress-status-box{
   	display: flex;
   } 
    .progress-status{
    		display: flex;
    		margin-bottom: 40px;
	    	align-items: center;
	    	.progress-name{
	    		margin-top: 3px;
	    		&:first-child{
	          border-top-left-radius: 12px;
	          border-bottom-left-radius: 12px;
	     		}
	     		&:last-child{
	          border-top-right-radius: 12px;
	          border-bottom-right-radius: 12px;
	     		}
	    		&.red{
		    		background-color: #ff9100;
		    	}
		    	&.blue{
		    		background-color: #5b81ff;
		    	}
		    	&.green{
		    		background-color: #2dd057;
		    	}
		    	width: 68px;
					height: 16px;
					background-color: #f2f2f2;
					margin-right: 2px;

			    position: relative;
	    }
    }
  }
    .box{
            height:320px;
             overflow: hidden;   
             text-align: center;                    
        }
        .mybox-leave-active,.mybox-enter-active{
            transition:  all 1s ease; 
        }
        .mybox-leave-active,.mybox-enter{
            height:0px !important;
        }
        .mybox-leave,.mybox-enter-active{
            height: 320px;
        }
        
        
        
</style>



