<template>
  <div class="selectUser-wrap">
	<div class="search-wrap" v-loading="loading">	  
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-form-item label="用户名">
				<el-input v-model="formInline.name" placeholder="用户名" class="p_text"></el-input>
			</el-form-item>
			<el-form-item label="申请部门">
				<el-select v-model="formInline.applyDept" placeholder="申请部门" class="p_select">
					<el-option :label="item.name" :value="item.id" v-for="(item,index) in orgList" :key="item.id"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="mll fr">
				<button  class="p_return sm  newSction"  @click.stop.prevent="getUserList">查询</button>
			</el-form-item>
		</el-form>
		<el-row>
			<el-col :span="16">
				<table class="orgusertb" >
					<tr>
						<th>用户</th>
						<th>姓名</th>
						<th>操作</th>
					</tr>
					<tr  v-for="(item,index) in SysUserList">
						<td>{{item.name}}</td>
						<td>{{item.realName}}</td>
						<td>
							<a @click="adduser(item)">选择</a>
						</td>
					</tr>
				</table>		
			</el-col>
			<el-col :span="1">
				&nbsp;
			</el-col>			
			<el-col :span="7">
				<table class="orgusertb">
					<tr>
						<th colspan="2">已选择的用户</th>
					</tr>
					<tr v-for="(item,index) in userlist">
						<td>{{item.name}}</td>
						<td>
							<a @click="userlist.splice(index,1)">移除</a>
						</td>
					</tr>
				</table>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-pagination style="text-align: left;display: inline-block;" 
				@current-change="getUserList" :current-page.sync="curPageIndex"
							layout="total, prev, pager, next, jumper" :total="totalCount">
				</el-pagination>
				<div class="fr mtl">
					<button class="p_submit " @click="SaveSelectUser()">确 定</button>
					<button class="p_return mll" @click="CloseSelectUser()">关 闭</button>
				</div>

			</el-col>
		</el-row>
	</div>
  </div>
</template>

<script type="text/javascript">


export default {
  name: 'selectUser',
  props: {
    userlist: {
		type:Array,
		default:function(){
			return [];
		}
    },
	single:{
		type:Boolean,
		default:false
	}
  },
  data() {
    return {
		loading:false,
		formInline:{
			name:'',
			applyDept:'',
		},
		SysUserList:[],
		orgList:[],
		curPageIndex:1,
		totalCount:0,
    }
  },
  mounted(){
	console.log('获取用户列表');
	// this.userlist=[{name:'张三',userid:'adsf'},{name:'李四',userid:'asdfj'}];
	if(this.single === true){
		if(this.userlist && this.userlist.length>1){
			this.userlist.splice(1,this.userlist.length-1);
		}
	}
  	this.getUserList();
  },
  methods:{
	getUserList(){
		this.loading=true;
		var url = '/api/sysUser/list?page='+this.curPageIndex+"&name="+this.formInline.name+"&govId="+this.formInline.applyDept;
		this.$xhr.get(url).then((res)=>{
			this.loading=false;
			if(res.data){
				this.SysUserList = res.data.pageList;
				this.totalCount = res.data.totalCount;
			}
		});
	},
	query(){

	},
	getSelectUserList(){
		return this.userlist;
	},
	handleCurrentChange(){

	},
	SaveSelectUser(){
		this.$emit('SaveSelectUser',this.userlist);
		return false;
	},
	CloseSelectUser(){
		this.$emit('CloseSelectUser');
		return false;
	},
	adduser(user){
		console.log(user);
		if(this.single === true){
			this.userlist.splice(0,this.userlist.length);
			this.userlist.push({name:user.name,userid:user.userId});
		}
		else{
			var has = false;
			for(var item of this.userlist){
				if(item.userid == user.userId){
					has=true;
					break;
				}
			}
			if(has == false){
				this.userlist.push({name:user.name,userid:user.userId})
			}
		}
	}
  },
  watch:{
  
  },
  computed:{

  }
}
</script>

<style lang="scss" scoped>
   .selectUser-wrap{

   	 
   }
  .orgusertb{
    width:100%;
    border: 1px solid #E8EAF6;
    tr{
      height: 40px;
      text-align: center;
      .checkbox{
        background: #fff;
      }
      th{
        background: #F3F6FE;
        text-align: center;
        font-weight: bold;
        border: 1px solid #E8EAF6;
      }
      td{
        border: 1px solid #E8EAF6;
        a{
          color: #409EFF;
        }
      }

    }
  }   
</style>



