<template>
  <div ref="topLeft" class="bar"></div>
</template>

<script type="text/javascript">
  import { echartsMixin } from 'assets/js/mixin'
  export default {
    name: '',
    mixin: [echartsMixin],
    props: {
      title: {
        type: String,
        default: ''
      },
      data: {
        type: Object,
        default: () => {}
      },
    },
    data() {
      return {}
    },
    mounted() {
      this.initOption();
      this.initChart();
    },
    methods: {

      initOption() {
        // initOption 作用： 一次性捕获 echats 图表的挂载点，声明好 echats 图表的 基本配置
        this.chart = this.$echarts.init(this.$refs.topLeft);
        this.option = {
          grid: {
            right: '5%',
            left: '3%',
            bottom: '10%',
            top: '15%',
            containLabel: true
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            },
            padding: 10,
  //            formatter: function(params) {
  //              // console.log(params)
  //              var str = params[0].name + "<br>";
  //              params.forEach(function(v, i) {
  //                str += v.seriesName + ": " + v.value + "元" + "<br>";
  //              });
  //              return str
  //            },
          },
          legend: {
            data: ['专项资金支出', '部门资金支出', '全部支出'],
            right: '40',
            top: '5',
            textStyle: {
              fontSize: '14',
              color: '#333'
            },
            itemGap: 20,
            itemHeight: 8,
            itemWidth: 8
          },
          calculable: true,
          xAxis: {
            type: 'category',
            axisLine: {
              lineStyle: {
                color: '#dae1ef',
                width: 2
              }
            },
            axisTick: {
              show: false,
              interval: 0,
              alignWithLabel: true
            },
            axisLabel: {
              interval: 0,
              rotate: '0',
              textStyle: {
                color: '#333',
                fontSize: '14'
              }
            },
            data: []
          },
          yAxis: [{
            type: 'value',
            name: '金额',
            nameTextStyle: {
              color: '#333',
              fontSize: '14'
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: ['#2f46a1']
              }
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: true,
              textStyle: {
                color: '#333',
                fontSize: '14'
              }
            },
            axisLine: {
              lineStyle: {
                width: 2,
                color: '#dae1ef'
              }
            },
            splitArea: {
              show: true,
              areaStyle: {
                color: ['#fff', '#F7F7F7']
              }
            },
          }],
          series: [{
            name: '专项资金支出',
            type: 'bar',
            barMaxWidth: 17,
            itemStyle: {
              normal: {
                color: '#43aaff'
              }
            },
            data: [],
            zlevel: 9
          }, {
            name: '部门资金支出',
            type: 'bar',
            barMaxWidth: 17,
            itemStyle: {
              normal: {
                color: '#ffbf3e',
              }
            },
            data: [],
            zlevel: 9
          }, {
            name: '全部支出',
            type: 'line',
            symbolSize: 0,
            itemStyle: {
              normal: {
                color: '#ff6500',
              },
              emphasis: {
                borderColor: '#ff9c31'
              }
            },
            data: [],
            zlevel: 9

          }]
        };
      },
      initChart() {
        this.option.xAxis.data = this.data.data1;
        this.option.series[0].data = this.data.data2;
        this.option.series[1].data = this.data.data3;
        this.option.series[2].data = this.data.data4;

        this.chart.setOption(this.option, true);
      },

      // echats 图表自适应
      _resizeHanlder() {
        this.chart.resize()
      },
      // 销毁echats图表方法
      _destroyHandler() {
        this.chart.dispose()
      }
    },
    components: {
    },
    watch: {
      data: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";

  .bar {
    width: 100%;
    height: 400px;
  }
</style>



