<template>
  <div class="inExecution-wrap">
    <div class="p_title">
      <i class="iconfont icon-execute"></i><p>执行中项目数量统计</p>
    </div>
    <div class="centerLeft">
      <centerLeft :data="centerleftData" v-if="Object.keys(centerleftData).length"></centerLeft>
    </div>
    <div class="centerRight">
      <table>
        <thead>
        <tr>
          <th>日期</th>
          <th>执行中项目</th>
          <th>逾期项目</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item, index) in centerRightData">
          <td>{{item.date}}</td>
          <td>{{item.executing}}</td>
          <td>{{item.overTime}}</td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script type="text/javascript">
  import centerLeft from '../echarts/centerLeft.vue'

  export default {
    name: '',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        centerleftData: {
          data1: [],
          data2: [],
          data3: []
        },
        centerRightData: [
          {date: '2017-12-05', executing: '12', overTime: '12'},
          {date: '2017-12-05', executing: '12', overTime: '12'},
          {date: '2017-12-05', executing: '12', overTime: '12'},
          {date: '2017-12-05', executing: '12', overTime: '12'},
          {date: '2017-12-05', executing: '12', overTime: '12'},
          {date: '2017-12-05', executing: '12', overTime: '12'},
          {date: '2017-12-05', executing: '12', overTime: '12'},
          {date: '2017-12-05', executing: '12', overTime: '12'},
        ]
      }
    },
    mounted() {
      this.getCenterLeftData();
    },
    methods: {
      getCenterLeftData() {
        this.centerleftData.data1 = ['一月', "二月", "三月", "四月", "五月", "六月", "七月", "八月"];
        this.centerleftData.data2 = [200, 300, 100, 330, 350, 450, 660, 700];
        this.centerleftData.data3 = [100, 200, 100, 0, 200, 300, 100, 500];
      },
    },
    components: {
      centerLeft
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";
  .inExecution-wrap {
    padding: 20px;
    font-size: 0;
    .p_title {
      font-size: 14px;
      margin-bottom: 20px;
    }
    .centerLeft {
      display: inline-block;
      width: 65%;
      vertical-align: top;
      box-sizing: border-box;
    }
    .centerRight {
      display: inline-block;
      padding: 0 20px;
      width: 35%;
      font-size: 14px;
      box-sizing: border-box;
      table {
        width: 100%;
        thead {
          border: 1px solid #e7ecff;
        }
        th {
          height: 42px;
          background-color: #eaeefd;
          text-align: center;
          font-weight: bold;
        }
        td {
          height: 42px;
          text-align: center;
          border: 1px solid #e7ecff;
        }
      }
    }
  }
</style>



