<template>
  <div class="header-wrap">
    <div class="header-center">
     <router-link class="logo" tag="div" :to="{path:'/project'}">柳州市信息化建设管理中心项目管理平台</router-link>
     <ul>
        <router-link  tag="li" :to="{path:'/project'}">
          <span class="xiangmu"></span>
          项目管理
        </router-link>
        <router-link  tag="li" :to="{path:'/statistics'}">
          <span class="tongji"></span>
          统计报表
        </router-link>
        <router-link  tag="li" :to="{path:'/asset'}">
          <span class="zichan"></span>
          资产管理</router-link>
        <router-link tag="li" :to="{path:'/system'}">
          <span class="pingtai"></span>
          平台管理</router-link>
        <router-link  tag="li" :to="{path:'/login'}">
          <a href="#" class="btn-logout">
            <span class="iconfont icon-tuichu"></span>
            退出登录
            </a>
          </router-link>
      </ul>

     </div>
  </div>
</template>

<script type="text/javascript">


export default {
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./../../assets/css/variable.scss";
@import "./../../assets/css/mixin.scss";
.header-wrap{
  background: #1276dc;
  .header-center{
    .logo{
      float: left;
      font-size: 22px;
      color: #ffffff;
      line-height: 68px;
    }
    width: 1200px;
    margin: 0 auto;
    height: 68px;
    line-height: 68px;
    color: #fff;
    box-sizing: border-box;
    display: flex;
    justify-content:space-between;
    ul{
      display: flex;
      li{
        font-size: 16px;
        margin-right:50px;
        cursor: pointer;
        &:last-child{
          margin-right: 0;
        }
        span.xiangmu,span.tongji,span.zichan,span.pingtai{
          display: inline-block;
          width: 30px;
          height: 30px;
          margin-right: 5px;
          background: url(../../assets/img/icon-top001.png) no-repeat;
          float: left;
          margin-top: 19px;          
        }
        span.tongji{
          background-position: -42px 0;
        }
        span.zichan{
          background-position: -85px 0;
        }     
        span.pingtai{
          background-position: -124px 0;
        }                   
        &.active,&:hover{
          color: #ffdb34;
          span.xiangmu{
            background-position: 0 -40px;
          }          
          span.tongji{
            background-position: -42px -40px;
          }
          span.zichan{
            background-position: -85px -40px;
          }     
          span.pingtai{
            background-position: -124px -40px;
          }     
        }        
      }
    }
    .btn-logout{
      display:inline-block;
      color: #fff;
      padding: 0 13px ;
      font-size: 14px;
      height: 28px;
      line-height: 28px;
      background: #0d3687;
      border-radius: 13px;
      text-align: center;
      &:hover{
        // color: #0366d6;
      }
    }
  }
}
</style>



